import { PointValueType, EvalValueType } from "./baseDto";

class PointDataSourceDetailDto {
    pointEvalType: number;

    pointId: string;

    period: string;

    periodAggregate: string;

    aggregate: string;

    setDetail(
        evalType: number,
        pointId: string,
        period: string,
        periodAggregate: string,
        aggregate: string
    ): PointDataSourceDetailDto {
        this.pointEvalType = evalType;
        this.pointId = pointId;
        this.period = period;
        this.periodAggregate = periodAggregate;
        this.aggregate = aggregate;
        return this;
    }
}

class PointDataSourceDto {
    /***/
    evalType: number;

    dataRange: string;

    timeRange: string;

    reqType: number;

    pointFormat: string;

    pointType: string;

    deviceId: string;

    pointName: string;

    deviceType: string;

    dsList: Array<PointDataSourceDetailDto>;

    setDataSource(
        evalType: number,
        dataRange: string,
        timeRange: string,
        reqType: number,
        pointFormat: string,
        pointType: string,
        deviceId: string,
        pointName: string,
        deviceType: string
    ) {
        this.evalType = evalType;
        this.dataRange = dataRange;
        this.timeRange = timeRange;
        this.reqType = reqType;
        this.pointFormat = pointFormat;
        this.pointType = pointType;
        this.deviceId = deviceId;
        this.pointName = pointName;
        this.deviceType = deviceType;
        return this;
    }

    setDetail(point: Model.Point) {
        let evalType = null;
        switch (point.childValueTyle) {
            case "workValue":
                evalType = PointValueType.wortV;
            case "statValue":
                evalType = PointValueType.statV;
        }
        //new PointDataSourceDetailDto().setDetail(evalType, point.pointId, );
    }
}

class DataSourceDto {
    dsId: string;
    dsConfigType: number;
    dsConfig: string;
    calcScript: string;
}

export { DataSourceDto, PointDataSourceDetailDto, PointDataSourceDto };
