export default class BaseTable extends AbstractBaseTable {

  public instance: any;
  public shapeStyle: any;
  public guiOption: any;
  public guiAdapter: any;
  public defaultOption: any;

  constructor(part: Model.Part) {
    super(part);
    const initOption = part.shapeStyle || option;
    this.staticData = part.staticData || data;
    this.guiOption = [];
    this.guiAdapter = null;
    this.instance = null;
    this.shapeStyle = null;

    this.getTableInitOption(initOption, this.staticData);
  }

  public initTableOption() {
    const option = new OptionAdapter().init(this.defaultOption, 'baseTable');
    this.shapeStyle = { ...option };
  }

  public initTableGui() {
    this.guiAdapter = new GuiAdapter();
    this.guiOption = this.guiAdapter.init(this.shapeStyle, 'baseTable');
  }

  public updateShapeStyle(formObj: any) {
    const guiId = formObj.rootId;
    const propKey: string = formObj.mKey;
    if (guiId === 'series') {
      const seriesIndex = formObj.index;
      const seriesCopy = this.shapeStyle[propKey]
        .map((item: any, index: number) => {
          if (index === seriesIndex) {
            item = super.getUpdatedTableOption(item, this.updatedOption[propKey])
          }
          return item
        });
      this.shapeStyle[propKey].length = 0;
      this.shapeStyle[propKey] = seriesCopy;
    } else {
      this.shapeStyle[propKey] = super.getUpdatedTableOption(
        this.shapeStyle[propKey],
        this.updatedOption[propKey],
      )
    }
  }

  public updateTableOption(formObj: any) {
    const propKey: string = formObj.mKey;
    const currentOption = {
      [propKey]: this.shapeStyle[propKey]
    };
    const option = propKey === 'series' ? currentOption : this.updatedOption;
    this.instance.$children[0].setOption(option);
  }

  public updateTableGui(formObj: any) {
    const propKey: string = formObj.mKey;
    const currentOption = {
      [propKey]: this.shapeStyle[propKey]
    };
    this.guiOption = this.guiAdapter.updateGuiOption(
      this.guiOption,
      currentOption,
      formObj,
    );
  }

  public getTableInitOption(option: TableReport.Option, data: any[]) {
    const opts = _.cloneDeep(option);
    opts.data = [...data];
    this.defaultOption = { ...opts };
  }

}