import UUID from 'utils/uuid';
export default class StaticModel implements Model.StaticModel {
    [key: string]: any;

    /**
     * 数据源类型id
     */
    public sourceTypeId: string;

    /**
     * 数据源类型
     */
    public sourceType: string = 'static';

    /**
     * 静态数据
     */
    public sourceData: string;

    constructor(staticData: any) {
        const { sourceData, sourceTypeId } = staticData;
        if (!sourceTypeId) {
            this.sourceTypeId = this.createdId();
        } else {
            this.sourceTypeId = sourceTypeId;
        }
        
        this.sourceData = sourceData || '';
    }

    public updateProps(staticData: any) {
        if (!_.isObject(staticData)) {
            return;
        }
        for (const propKey of Reflect.ownKeys(staticData)) {
            if (Reflect.has(this, propKey)) {
                this[propKey] = staticData[propKey];
            }
        }
    }

    public getPartAllProps(): Model.StaticModel {
        return {
            sourceTypeId: this.sourceTypeId,
            sourceType: this.sourceType,
            sourceData: this.sourceData,
        }
    }

    /**
     * Fixme 以后把这个方法挪出去
     */
    private createdId(): string {
        return 'static_' + new UUID().init();
    }

}