


import Vue from 'vue';
import { mapMutations, mapState } from 'vuex';
import _ from 'lodash';
export default Vue.extend({
    computed: {
        ...mapState({
            basePartList: (state: any) => state.screen.pageManage.partContainer,
            partSelection: (state: any) => state.screen.editor.partSelection,
        }),
        rectCoord() {
            return this.partSelection.selectPart;
        },
        selectGroup() {
            return this.partSelection.selectGroup;
        },
    },
    watch: {
        rectCoord(val) {
            console.log('watch rectCoord');
            console.log(val);
        },
    },
    methods: {
        ...mapMutations([
            'add_part',
            'remove_part',

            'clearPartSeletion',
            'updatePartSeletion',

            'updateTransform',
            // 'setSelectedBox',

            'breakup_his',
            'makeup_his',
        ]),
        makeupGroup() {
            const rectCoord = _.cloneDeep(this.rectCoord);
            const selectGroup = this.selectGroup;
            if (!rectCoord || !selectGroup.length) {
                return;
            }

            const partGroup: any[] = [];
            // 重置每个part的x，y
            selectGroup.map( (item: any) => {
                // 如果存在group就先打散再组合
                const isPartGroup = item.partType;
                if ( isPartGroup === 'group' ) {
                    const partGroupInner: Model.Group[] = item.partGroup;
                    const coord = item.partStyle;
                    partGroupInner.map( ( childPart: Model.Part ) => {
                        const partSty = childPart.partStyle;
                        partSty.x = partSty.x + (coord.x - rectCoord.x);
                        partSty.y = partSty.y + (coord.y - rectCoord.y);
                        partGroup.push(childPart);
                    });
                } else {
                    const copyPart = item.getPartAllProps();
                    const partStyle = copyPart.partStyle;
                    partStyle.x = partStyle.x - rectCoord.x;
                    partStyle.y = partStyle.y - rectCoord.y;
                    partGroup.push(copyPart);
                }
            });
            // 移除多个元素，清楚partSelection
            this.remove_part(selectGroup);
            this.clearPartSeletion();

            // 多个元素重新构建为一个group
            const groupPart = {
                partType: 'group',
                partGroup,
                partStyle: rectCoord
            };
            this.add_part(groupPart);

            // fixme 暴力取得数组最后一个part为最近新添加的
            const last = this.basePartList.length - 1;
            const newPart = this.basePartList[last];
            const partSelObj = {
                selectGroup: [newPart],
            };
            // 组合之后，显示rubberBand，选中当前元素
            this.updatePartSeletion(partSelObj);
            this.$root.busRef.$emit('updateSelGroup', [newPart]);
            
            // 更新transform
            this.updateTransform({type: ''});

            // 历史记录
            this.makeup_his();
        },
        // 打散,支持多组合打散
        breakupGroup() {
            // 如果选中的selectGroup为空，不进行操作
            if (!this.selectGroup.length) {
                return;
            }

            // 缓存多选的part
            const selPartGroup: Model.Part[] = [];
            // 缓存新建part的参数
            const newPartArr: Model.Part[] = [];
            const updatePartList = () => {
                
                this.add_part(newPartArr);
                // 获取新增的所有part
                const newPartLen = newPartArr.length;
                const startIndex = this.basePartList.length - newPartLen;
                if (startIndex >= 0) {
                    const newPartListRef: Model.Part[] = this.basePartList.slice(startIndex);
                    selPartGroup.push(...newPartListRef);
                }
            };

            // 遍历查找groupPart，进行解构
            for (const part of this.selectGroup) {
                if (part.partType !== 'group') {
                    continue;
                }

                const rectCoord = part.partStyle;
                const partGroups = part.partGroup;
                const centerCoord = {
                    xcenter: (rectCoord.width / 2) ,
                    ycenter: (rectCoord.height / 2) ,
                    angle: rectCoord.angle,
                };
                partGroups.map((item) => {
                    const copyPart = item.getPartAllProps();
                    const partStyle = copyPart.partStyle;
                    const pos = {
                        x0: partStyle.x + (partStyle.width / 2),
                        y0: partStyle.y + (partStyle.height / 2),
                    };
                    Object.assign(pos, centerCoord);
                    const rotateCoord = this.calRoateRect(pos);
                    partStyle.x = rotateCoord.x + (rectCoord.x - partStyle.width / 2);
                    partStyle.y = rotateCoord.y + (rectCoord.y - partStyle.height / 2);
                    partStyle.angle = rectCoord.angle + partStyle.angle;
                    newPartArr.push(copyPart);
                });
                this.remove_part([part]);
            }
            updatePartList();
            const type: string = 'multiply';
            // 更新partSeletion
            const partSelObj = {
                selectGroup: selPartGroup,
                type,
            };
            this.updatePartSeletion(partSelObj);
            
            // 更新transform
            this.updateTransform({type});

            // 历史记录
            this.breakup_his();
        },

        calRoateRect( payload ): { x: number , y: number } {
            const { x0, y0, xcenter, ycenter, angle  } = payload;
            const piAngle = Math.PI / 180 * angle;
            const X = (x0 - xcenter) * Math.cos(piAngle) - (y0 - ycenter) * Math.sin(piAngle) + xcenter; 
            const Y = (x0 - xcenter) * Math.sin(piAngle) + (y0 - ycenter) * Math.cos(piAngle) + ycenter;
            return {	
                x: X, 
                y: Y,
            };
        },
        /**
         * 初始化监听事件接收器
         */
        editorEvtReceiver(isDestoyed?: boolean) {
            const eventObjCfg = [
                {
                    name: 'breakup',
                    handle: this.breakupGroup,
                },
                {
                    name: 'makeup',
                    handle: this.makeupGroup,
                }
            ];
            // 存在两个以上子组件引用，需要改善
            for (const eventObj of eventObjCfg) {
                if (isDestoyed) {
                    this.$root.busRef.$off(eventObj.name);
                } else {
                    this.$root.busRef.$on(eventObj.name, (eventType: string) => {
                        eventObj.handle();
                    });
                }
            }
        },
    },
    mounted() {
        this.editorEvtReceiver();
    },
    beforeDestroy() {
        this.editorEvtReceiver(true);
    },
});
