import AbstractOption from '../../../common/abstractOption'

export default class Img extends AbstractOption {

  public config: any;

  constructor(shapeStyle?: Shape.Image) {
    super();
    this.config = this.init(shapeStyle);
  }

  public init(shapeStyle?: Shape.Image): object {
    // equalScale: 是否等比缩放图片
    const {
      imageSrc = '',
      equalScale = false
    } = shapeStyle || {};

    return {
      imageSrc,
      equalScale
    };
  }

  public computed(): void { }

}