
import Vue from 'vue';
import Cell from './cell.vue';
import Group from './group.vue';
import { mapState } from 'vuex';

export default Vue.extend({
    data() {
        return {
            // id: this.part.partId,
        };
    },
    components: {
        Cell,
        Group
    },
    mounted(){
        // const div: any = document.querySelector('#group-list');
        // div.addEventListen('mousedown', () => {
        // });
    },
    computed: {
        ...mapState({
            pageManage: (state: any) => state.screen.pageManage,
        }),
        partList() {
            return this.pageManage.partContainer;
        },
    },
    render(h): any {
        const mapNode = (h): any => {
            const conArray = [];
            const hVnode = ( vNodeName, part ): any => {
                return h(vNodeName, {
                    props: {
                        part: part
                    },
                    key: part.partId,
                });
            }; 
            for (const item of this.partList) {
                if (item.partType === 'group') {
                    conArray.push( hVnode(Group, item) );
                } else {
                    conArray.push( hVnode(Cell, item) );
                }
            }
            return conArray;
        }
        return h( 'div', {
                attrs: {
                    class: 'group-list',
                    id: "group-list"
                }
            },
            mapNode(h)
        );
    },
})
