
import { mapGetters, mapMutations } from 'vuex';
export default {
    name: 'eventController',
    computed: {
        ...mapGetters({
            partEvent: 'partEvent',
        }),
    },
    methods: {
        ...mapMutations([
            'addCanvasAllListener',
            'removeCanvasAllListener',
            'addCanvasListener',
            'removeCanvasListener',
        ]),
        // eventObject = { id, eventType, callback, isNative}
        initCanvasEvt() {
            
        },
        addCanvasClickEvt(evtListener) {
            this.addCanvasListener(evtListener);
        },
        removeCanvasClickEvt(evtListener) {
            this.removeCanvasListener(evtListener); 
        },
    },
    render(h) {
        return h();
    },
}
