import HoverPart from './hoverPart/hoverPart';
import RubberBand from './rubberBand/rubberBand';
import LockPart from './lock/lock';
import Transform from './transform/transform';
import PartSelection from './partSelection/partSelection';
import ClipBoard from './clipBoard/clipBoard';
import Layer from './layer/layer';
import RefLine from './reLine/refLine';
import History from './history/history';
import DataSource from './dataSource/dataSource';

export default class Canvas {
    

    public partId: string = '';

    /**
     * 锁定元素
     */
    public lockPart: LockPart;

    /**
     * 移入移出元素
     */
    public hoverPart: HoverPart;

    /**
     * 框线选择的区域
     */
    public rubberBand: RubberBand;
    
    /**
     * 图形变换
     */
    public transform: Transform;

    /**
     * 选中元素区域
     */
    public partSelection: PartSelection;

    /**
     * 粘贴板
     */
    public clipBoard: ClipBoard;

    /**
     * 粘贴板
     */
    public layer: Layer;

    /**
     * 参考线
     */
    public refLine: RefLine;

    /**
     * 参考线
     */
    public history: History;

    /**
     * 参考线
     */
    public dataSource: DataSource;

    

    constructor() {
        this.init();
    }

    public init(): void {
        this.partId = '';
        this.lockPart = new LockPart();
        this.hoverPart = new HoverPart();
        this.rubberBand = new RubberBand();
        this.transform = new Transform();
        this.partSelection = new PartSelection();
        this.clipBoard = new ClipBoard();
        this.layer = new Layer();
        this.refLine = new RefLine();
        this.history = new History();
        this.dataSource = new DataSource();
    }

    public destroyed() {
        this.lockPart.destroyed();
        this.hoverPart.destroyed();
        this.rubberBand.destroyed();
        this.transform.destroyed();
        this.partSelection.destroyed();
        this.clipBoard.destroyed();
    }

    /**
     * 设置partId
     */
    public setPartId( partId ): void {
        if (partId) {
            this.partId = partId;
        }
    }

    /**
     * 重置partId
     */
    public resetPartId(): void {
        this.partId = '';
    }

    /**
     * 映射编辑器属性
     * 被历史功能隐式调用
     */
    public mapEditorAllProps() {
        // console.log('mapEditorAllProps');
        // console.log(this);
        return {
            partId: this.partId,
            lockPart:  this.lockPart.getPureModel(),
            partSelection: this.partSelection.getPureModel(),
            rubberBand: JSON.parse(JSON.stringify(this.rubberBand)),
            transform: this.transform.getPureModel()
        }
    }

    /**
     * 重设编辑器属性
     * 被历史功能隐式调用
     */
    public resetEditorAllProps( pureModel, resetPartCallBack ) {
        const {partId, lockPart, partSelection, rubberBand, transform} = pureModel;
        this.partId = partId;
        this.lockPart.setPureModel(lockPart, resetPartCallBack);
        this.partSelection.setPureModel(partSelection, resetPartCallBack);
        this.rubberBand.mapRubberBand(rubberBand);
        this.transform.setPureModel(transform);
    }
    
}