import SelectPartObject from './selectPartObject';
import { PartSelectionType } from '../interface';
const typeObject = {
    single: 'single',
    multiply: 'multiply',
};

/**
 * 选中元素的区域
 * 这里包括两个方面，其一是点击part元素，这时的区域就是单个元素
 * 其二是利用rubberband选中的元素，这时可能是单个元素也可能是多个元素
 */
export default class PartSelection implements PartSelectionType {

    /**
     * 当前操作的名称，可选范围有 single, mutil
     */
    public type: string = 'single';

    /**
     * 当前选中元素的简单信息，
     * 单个元素指元素本身的信息，
     * 多个元素，通过计算得出外围的边长信息 -- 无效
     */
    public selectPart: SelectPartObject = new SelectPartObject();

    /**
     * 这里应该是part的数组，不再指定类型了 part[]
     * 持久化选中的part
     */
    public selectGroup: any[] = [];

    /**
     * 选中后，是否释放part
     * 目的是是否更新part配置项，
     * 优化鼠标选中目标（part）之后，
     * 释放鼠标才定位part的配置项 - shapeStyle
     */
    public releasePart: boolean = true;

    // 清楚partSelection状态
    public clearPartSeletion(): void {
        this.clearSelectGroup();
        this.setType('');
        // selectPart目前不更新
    }

    // 更新partSelection状态
    public updatePartSeletion(partSelObj: any): void {
        const { selectGroup, selectPart, type } = partSelObj;
        if (selectPart) {
            this.mapSelectPart(selectPart);
        }
        if (Array.isArray(selectGroup)) {
            this.mapSelectGroup(selectGroup);
        }
        if (type) {
            this.setType(type);
        } else {
            if (selectGroup.length > 1) {
                this.setType(typeObject.multiply);
            } else {
                this.setType(typeObject.single);
            }
        }
    }

    /**
     * 设置releasePart值
     */
    public setReleasePart(isRelease: boolean): void {
        this.releasePart = isRelease;
    }

    /**
     * 设置type值
     */
    public setType(type: string): void {
        this.type = type ? type : 'single';
    }

    /**
     * 映射selectPart的值
     */
    public mapSelectPart(selectPart: any): void {
        this.selectPart.mapSelectPartObject(selectPart);
    }

    /**
     * 更新选中的part数组，代表当前被选中的持久part
     */
    public mapSelectGroup(part: any): void {
        if (!part) {
            return;
        }
        this.clearSelectGroup();
        if (Array.isArray(part)) {
            part.forEach((eachPart) => {
                this.selectGroup.push(eachPart);
            });
        } else {
            this.selectGroup.push(part);
        }
    }

    /**
     * 清空选中的所有part
     */
    public clearSelectGroup(): void {
        this.selectGroup = [];
    }

    /**
     * 获取纯对象
     */
    public getPureModel(): PartSelectionType {
        return {
            type: this.type,
            selectPart: _.cloneDeep(this.selectPart),
            selectGroup: this.getPurePartGroup(this.selectGroup),
        };
    }

    /**
     * 获取纯对象
     */
    public setPureModel(model: any, callback: Function): void {
        // 先清空goup内的组件
        this.clearSelectGroup();
        const { type, selectPart, selectGroup } = model;
        this.type = type;
        this.mapSelectPart(selectPart);
        const partList = callback(selectGroup);
        this.mapSelectGroup(partList);
    }

    /**
     * 获取存对象
     */
    public getPurePartGroup(partGroup: any[]): any[] {
        // console.log(partGroup);
        
        const purePartGroup: any[] = [];
        if (partGroup.length === 0) {
            return purePartGroup;
        }
        partGroup.map((part) => {
            purePartGroup.push(part.getPartAllProps());
        });
        return purePartGroup;
    }

    public destroyed(): void {
        this.clearPartSeletion();
        this.type = null;
        this.selectPart = null;
        this.selectGroup = null;
    }
}