
import Shape from '@/views/screen/libary/reports';
import { Element } from '../element';
import updatePropsDeep from 'utils/initProps';

/**
 * 基础报表基类
 */
export default abstract class AbstractBaseTable extends Element {

  public instance: any;
  public shapeStyle: any;
  public staticData: any;

  /**
   * 当前报表初始化的配置项对象
   */
  public defaultOption: any;

  /**
   * 当前报表更新的配置项对象
   */
  public updatedOption: any;

  /**
   * 当前基础报表的属性修改界面配置项
   */
  abstract guiOption: any;

  /**
   * 当前基础报表的属性修改界面适配器实例
   */
  abstract guiAdapter: any;

  constructor(part: Model.Part) {
    super(part);
  }

  /**
   * 获取更新属性后的报表配置对象
   * @param origin 原配置对象
   * @param option 需更新属性的配置对象
   * @returns {object} 更新属性后的对象
   */
  public getUpdatedTableOption(origin: any, option: any) {
    return updatePropsDeep(origin, option);
  }

  /**
   * 初始化当前基础报表的配置项属性及属性界面配置
   */
  public initTableModel() {
    this.initTableOption();
    this.initTableGui();
  }

  /**
   * 更新当前报表的配置项属性及属性界面配置
   * @param formObj 当前报表的表单组件修改的值
   */
  public updateTableModel(formObj: any): void {
    this.setUpdatedTableOption(formObj);
    this.updateShapeStyle(formObj);
    this.updateTableOption(formObj);
    this.updateTableGui(formObj);
  }

  /**
   * 设置当前报表更新的配置项对象属性
   */
  public setUpdatedTableOption(formObj: any): void {
    const propPath: string = formObj.path;
    const propValue: any = formObj.val;
    this.updatedOption = _.zipObjectDeep([propPath], [propValue]);
  }

  /**
   * 初始化当前基础报表的配置项对象
   * 此算法应实现内容：
   * 1)当前基础报表的配置属性的初始化
   * 2)当前基础报表的shapeStyle初始化
   */
  abstract initTableOption(): void;

  /**
   * 初始化当前基础报表的属性修改界面配置项
   * 此算法应实现内容：
   * 1)当前基础报表guiAdapter属性的初始化
   * 2)当前基础报表guiOption属性的初始化
   */
  abstract initTableGui(): void;

  /**
   * 更新当前报表的shapeStyle
   * 此算法应实现内容：
   * 属性修改界面修改值时更新报表的shapeStyle属性
   */
  abstract updateShapeStyle(formObj: any): void;

  /**
   * 更新当前报表的配置项对象
   * 此算法应实现内容：
   * 根据更新的shapeStyle属性更新当前报表状态
   */
  abstract updateTableOption(formObj: any): void;

  /**
   * 更新当前报表的属性修改界面配置项
   * 此算法应实现内容：
   * 根据更新的shapeStyle属性更新当前报表属性修改界面
   */
  abstract updateTableGui(formObj: any): void;

  /**
   * 初始化基础报表
   * @param {string} domStr 指定id的字符串
   */
  public init(domId: string): void {
    if (_.isNil(domId) && !(domId as string).includes('#')) {
      return;
    }
    this.instance = new Shape();
    this.instance.typeName = this.childType;

    // 初始化table模型
    this.initTableModel();
    // 映射属性修改界面属性值
    this.instance.option = this.shapeStyle;
    // 挂载table实例
    this.instance.$mount(domId);
  }

  /**
   * 设置基础报表的公共属性
   */
  public setPublicStyle(option: Model.PartStyle): void {
    if (!_.isObject(option)) {
      return;
    }

    // 有代理, 不再需更新的宽、高、透明度等公共属性
    // if (option.width) {
    //   const widthWrap = { width: option.width };
    //   this.instance.$children[0].setOption(widthWrap);
    // }
    // if (option.height) {
    //   const heightWrap = { height: option.height };
    //   this.instance.$children[0].setOption(heightWrap);
    // }
    // if (option.opacity) {
    //   const opacityWrap = { opacity: option.opacity };
    //   this.instance.$children[0].setOption(opacityWrap);
    // }

    // 更新shape的partStyle
    for (let [key, value] of Object.entries(option)) {
      if (!_.isNil(this.partStyle[key])) {
        this.partStyle[key] = value;
      }
    }
  }

  /**
   * 设置基础报表的私有属性
   */
  public setPartStyle(formObj: any): void {
    if (!_.isObject(formObj)) {
      return;
    }
    // 更新当前报表的配置项属性、shapeStyle及属性界面配置
    this.updateTableModel(formObj);
  }

  public setPartGuiSeries(setType: string, seriesOption: any) {
    if (setType === 'add') { this.addPartGuiSeries() }
    if (setType === 'delete') { this.delPartGuiSeries(seriesOption) }
  }

  /**
   * 新增报表系列
   */
  public addPartGuiSeries() {
    // 更新报表初始化配置项对象，保证新增或删除系列报表状态同步
    this.defaultOption = null;
    this.defaultOption = { ...this.shapeStyle };
    // 新增一个空系列
    const index = this.defaultOption.series.length;
    this.defaultOption.series.push({ index });
    // 更新报表模型
    this.initTableModel();
    // 更新报表状态
    this.instance.$children[0].setOption(this.shapeStyle);
  }

  /**
   * 删除报表系列
   * @param seriesOption 当前删除的报表系列属性修改界面配置对象
   */
  public delPartGuiSeries(seriesOption: any) {
    const len = this.defaultOption.series.length;
    if (len >= 2) {
      this.defaultOption = null;
      this.defaultOption = { ...this.shapeStyle };
      // 删除当前系列
      const delIndex = seriesOption.index;
      const currIndex = this.defaultOption.series.findIndex((item: any, index: number) => index === delIndex);
      this.defaultOption.series.splice(currIndex, 1);
      this.initTableModel();
      this.instance.$children[0].setOption(this.shapeStyle);

      // 系列不可全部删除，应至少保留一个系列
      if (len === 2) {
        const currentOption = this.guiOption.find((item: any) => item.id === 'series');
        currentOption.children.forEach((item: any) => {
          item.editable = false;
        });
      }
    }
  }

  public getPartStaticData(): any[] {
    return this.staticData;
  }

  public setPartStaticData(option: any): void {
    console.log('设置基础报表静态数据');
    if (option.length > 0) {
      const data = option.slice(0, 50);
      this.staticData = data;
      this.shapeStyle.data.length = 0;
      this.shapeStyle.data = [...data];
      this.instance.$children[0].setOption(this.shapeStyle);
    }
  }

  /**
   * 合并基础类的所有属性到part
   */
  public getPartAllProps(): Model.Part {
    const parentProps = super.getPartAllProps();
    return {
      ...parentProps,
      shapeStyle: this.shapeStyle,
      staticData: this.staticData
    }
  }

  public setPartActiveData(option: any): void {
    console.log('设置基础报表动态数据');
    if (option.length > 0) {
      const data = option.slice(0, 50);
      this.shapeStyle.data.length = 0;
      this.shapeStyle.data = [...data];
      this.instance.$children[0].setOption(this.shapeStyle);
    }
  }

  public destroyed(): void {
    if (this.instance && !_.isEmpty(this.instance)) {
      this.instance.$destroy();
    }
  }

}
