// 基础组件配置名
const nameSet = new Set([
  'inputNumber',
  'input',
  'select',
  'slider',
  'checkbox',
  'colorPicker',
  'imageUpload'
])

const files = require.context('./src', true, /\.vue$/)
const cmptMap: any = {}

files.keys().forEach(key => {
  const vueCmpt = files(key).default
  if (typeof vueCmpt !== 'function') {
    return
  }

  const cmptName = key.replace(/(.*\/)*([^.]+).*/ig, '$2')

  // 基础组件的文件名称必须和对应配置的名称匹配
  if (nameSet.has(cmptName)) {
    cmptMap[cmptName] = vueCmpt
  }
})

export default cmptMap
