import AbstractOption from '../../../common/abstractOption'

export default class TextStyle extends AbstractOption {

  public config: any;

  constructor(shapeStyle?: Shape.Text) {
    super();
    this.config = this.init(shapeStyle);
  }

  public init(shapeStyle?: Shape.Text): object {
    const {
      padding = 5,
      lineHeight = 20
    } = shapeStyle || {};

    return {
      padding,
      lineHeight
    };
  }

  public computed(): void { }

}