declare const _:any;
import { RectObjectType } from '../interface';

export default class RectObject implements RectObjectType{
    /**
     * part选中的x坐标
     */
    public x: number = 0;

    /**
     * part选中的y坐标
     */
    public y: number = 0;

    /**
     * part选中的宽度
     */
    public width: number = 0;

    /**
     * part选中的高度
     */
    public height: number = 0;

    /**
     * part选中的角度
     */
    public angle: number = 0;

    /**
     * x缩放的比例,暂时没用到
     */
    public scaleX: number = 1;

    /**
     * y缩放的比例,暂时没用到
     */
    public scaleY: number = 1;

    constructor() {
    }

    public mapRectObject( part: RectObject): void {
        const { x, y, width, height, angle, scaleX, scaleY } = part;
        if(!_.isNil(x)) {
            this.x = x;
        }
        if(!_.isNil(y)) {
            this.y = y;
        }
        if(!_.isNil(width)) {
            this.width = width;
        }
        if(!_.isNil(height)) {
            this.height = height;
        }
        if(!_.isNil(angle)) {
            this.angle = angle;
        }
        if(!_.isNil(scaleX)) {
            this.scaleX = scaleX;
        }
        if(!_.isNil(scaleY)) {
            this.scaleY = scaleY;
        }
    }

}