import { LockPartType } from '../interface';
export default class LockPart implements LockPartType {

    /**
     * 当前选中的元素是否上锁
     */
    public isLocked = false;

    /**
     * 上了锁的元素集合
     */
    public lockGroup: any[] = [];

    /**
     * 上锁功能是否可用，目前场景不可用是灰色不可点击的状态
     */
    public enable: boolean = false;

    constructor() {}

    /**
     * 设置type值
     */
    public setIsLocked(isLocked): void {
        this.isLocked = isLocked;
    }

    /**
     * 映射partStyle的值
     */
    public mapLockGroup(part): void {
        if (!part) {
            return;
        }
        if (Array.isArray(part)) {
            part.forEach((eachPart) => {
                this.lockGroup.push(eachPart);
            });
        } else {
            this.lockGroup.push(part);
        }
    }

    /**
     * 映射partStyle的值
     */
    public clearLockGroup(): void {
        this.lockGroup = [];
    }

    /**
     * 获取纯对象
     */
    public getPureModel(): LockPartType {
        return {
            isLocked: this.isLocked,
            lockGroup: this.getPurePartGroup(this.lockGroup),
            enable: this.enable
        };
    }

    /**
     * 获取纯对象
     */
    public setPureModel(model, callback): void {
        const { isLocked, lockGroup, enable } = model;
        this.isLocked = isLocked;
        this.enable = enable;
        if (lockGroup.length === 0) {
            this.clearLockGroup();
            return;
        }
        const partList = callback(lockGroup);
        this.mapLockGroup(partList);
    }

    /**
     * 获取存对象
     */
    public getPurePartGroup(partGroup: any[]): any[] {
        const purePartGroup: any[] = [];
        if (partGroup.length === 0) {
            return purePartGroup;
        }
        partGroup.map((part) => {
            purePartGroup.push(part.getPartAllProps());
        });
        return purePartGroup;
    }

    public destroyed(): void {
        this.isLocked = null;
        this.clearLockGroup();
        this.lockGroup = null;
        this.enable = null;
    }

}
