
import Vue from 'vue';
import { mapState } from 'vuex';
import enableConfig from './enableConfig';
interface TypeObj {
    type: string;
    cando: boolean;
}
export default Vue.extend({
    computed: {
        ...mapState({
            selector: (state: any) => state.screen.editor,
        }),
        clipBoard(): any[] {
            return this.selector.clipBoard.partGroupTemp;
        },
        partSelection(): any[] {
            return this.selector.partSelection.selectGroup;
        },
    },
    watch: {
        // 监听粘贴版能否粘贴
        clipBoard( newArray: any[] ): void {
            this.watchClipBoardPaste(newArray);
        },
        partSelection( newArray: any[] ) {
            this.watchGroup(newArray);
            this.watchLayer(newArray);
            this.watchLockPart(newArray);
            this.watchClipBoard(newArray);
        },
    },
    methods: {
        // 监听组合打散功能
        watchGroup( selectGroup: any[] ) {
            let canGroup = false;
            let canBreakup = false;
            const len = selectGroup.length;
            if (len > 1  ) {
                canGroup = true;
                // 多选中全为基本元素和母版和特殊组件的时候是不能打散的，目前都不应许打散
                canBreakup = false;
                // 如果存在组合是允许打散或组合的
                if (this.isGroupSelectGroup(selectGroup)) {
                    canBreakup = true;
                }
            } else if (len === 1) {
                // 组合 -基本，母版，组件都不能组合
                canGroup = false;
                // 拆分 -组合，不以基础组件h和母版生成的组件
                if ( this.isGroupSelectGroup(selectGroup) ) {
                    canBreakup = true;
                } else {
                    canBreakup = false;
                }
                
            } else {
                canGroup = false;
                canBreakup = false;
            }
            const typeObj = [
                { 
                    type: 'makeup',
                    cando: canGroup,
                }, {
                    type: 'breakup',
                    cando: canBreakup ,
                },
            ];
            this.broadcastEnable( typeObj );
        },
        // 监听粘贴板粘贴模块的使能
        watchClipBoardPaste( lockGroup: any[] ) {
            let canPaste = false;
            if (lockGroup.length > 0 ) {
                canPaste = true;
            }

            const typeObj = [
                { 
                    type: 'paste',
                    cando: canPaste,
                }, {
                    type: 'pasteMouse',
                    cando: canPaste ,
                },
            ];
            this.broadcastEnable( typeObj );
        },
        // 监听粘贴板其他模块的使能
        watchClipBoard( selectGroup: any[] ) {
            let canCopy = false;
            let canCut = false;
            let canDele = false;
            if (selectGroup.length > 0 ) {
                canCopy = true;
                canCut = true;
                canDele = true;
            }
            // 选中存在锁定，就不能剪切和删除
            if (this.isLockSelectGroup(selectGroup)) {
                canCut = false;
                canDele = false;
            }
            const typeObj = [
                {
                    type: 'copy',
                    cando: canCopy ,
                },
                {
                    type: 'cut',
                    cando: canCut ,
                },
                {
                    type: 'delete',
                    cando: canDele ,
                },
            ];
            this.broadcastEnable( typeObj );
        },
        // 数组中是否包含了group - 组合
        isGroupSelectGroup( selectGroup: any[] ): boolean {
            return selectGroup.some( (part) => {
                if (part.partType === 'group') {
                    return true;
                }
            });
        },
        // 数组中是否存在锁定
        isLockSelectGroup( selectGroup: Model.Part[] ): boolean {
            return selectGroup.some( (part) => {
                if (part.isLocked) {
                    return true;
                }
            });
        },
        // 监听锁定解锁功能是否能用
        watchLockPart( selectGroup: any[] ) {
            let canLock = false;
            let canUnlock = false;
            if (selectGroup.length === 1) {
                selectGroup.forEach( (part: any) => {
                    if (part.isLocked) {
                        canLock = false;
                        canUnlock = true;
                    } else {
                        canLock = true;
                    }
                });
            } else if (selectGroup.length > 1) {
                // 多选不给操作
                canLock = true;
                // 多选存在不锁定锁定
                if (this.isLockSelectGroup(selectGroup)) {
                    canLock = false;
                    canUnlock = true;
                }
            }
            const typeObj = [
                { 
                    type: 'locking',
                    cando: canLock,
                }, {
                    type: 'unlock',
                    cando: canUnlock ,
                },
            ];
            this.broadcastEnable( typeObj );
        },
        // 目前只要选中数组大于0，图层都是可以操作的
        watchLayer( selectGroup: any[] ) {
            let canLayer = false;
            if (selectGroup.length >= 1) {
                canLayer = true;
            }
            const typeObj = [
                { 
                    type: 'top',
                    cando: canLayer,
                }, {
                    type: 'bottom',
                    cando: canLayer ,
                },
                { 
                    type: 'upper',
                    cando: canLayer,
                }, {
                    type: 'lower',
                    cando: canLayer ,
                },
            ];
            this.broadcastEnable( typeObj );
        },
        // 根据传入的模型值，映射为匹配的数据格式
        mapEnableCfg( type: string, cando: boolean ): any {
            const enableItem = enableConfig.get(type);
            return Object.assign(enableItem, { enable: cando });
        },
        // 这里采用中间件的形式去触发事件
        broadcastEnable( typeObj: TypeObj[] | TypeObj ): void {
            const message: any = [];
            if ( Array.isArray(typeObj) ) {
                typeObj.forEach( ( typeEach ) => { 
                    const type = typeEach.type;
                    const cando = typeEach.cando;
                    message.push(this.mapEnableCfg(type, cando));
                });
            } else {
                // 默认是一个对象
                const type = typeObj.type;
                const cando = typeObj.cando;
                message.push(this.mapEnableCfg(type, cando));
            }
            
            this.$root.busRef.$emit('enableWatch', message);
        },
    },
    render(h) {
        return h();
    },
});
