import { BaseStorage } from "./baseRepository";
import { Url } from "./url.request";
import { MDto } from "../dto/baseDto";

export class PartStorage {
    private static url: Url = new Url();

    public static saveProjectDetail(project: any): Promise<MDto<any>> {
        return BaseStorage.save(
            PartStorage.url.projectModule.save_project_detail,
            project
        );
    }

    public static saveProject(project: any) {
        return BaseStorage.save(
            PartStorage.url.projectModule.save_project,
            project
        );
    }

    public static savePartClass(partClass: any) {
        return BaseStorage.save(
            PartStorage.url.projectModule.save_part_class,
            partClass
        );
    }

    public static getPartClass(userId: string): Promise<MDto<any>> {
        return BaseStorage.get(PartStorage.url.projectModule.get_part_class, {
            userId: userId
        });
    }

    public static getProjects(
        organType: number,
        organId: string
    ): Promise<MDto<any>> {
        return BaseStorage.get(PartStorage.url.projectModule.get_project, {
            organType: organType,
            organizationId: organId
        });
    }

    public static getPages(proId: number): Promise<MDto<any>> {
        return BaseStorage.get(
            PartStorage.url.projectModule.get_project_pages,
            { proId: proId }
        );
    }

    public static getPageDetail(pagesId: string): Promise<MDto<any>> {
        return BaseStorage.get(PartStorage.url.projectModule.get_Pages_detail, {
            pagesId: pagesId
        });
    }
}
