

























































import Vue from 'vue';
import { throttle } from 'utils/throttle';
import MenuAdapter from '../menuData/menuAdapter.js';
// import Menu from '@/views/screen/core/menu';
import { mapMutations } from 'vuex';
import IconShape from '../common/svgIcon/iconShape.vue';
import IconEcharts from '../common/svgIcon/iconEcharts.vue';
import CmptGroup from '../cmptLib/cmptGroup.vue';

export default Vue.extend({
    name: 'rightNav',
    data() {
        return {
            activeNames: ['base', 'line'],
            menuDataList: new MenuAdapter().initMenu(),
            leftNavDom: undefined,
            scrollDom: undefined,
        };
    },
    computed: {
        projectObj(): any {
            return this.$store.getters.projectObj;
        },
        panelScale(): number { 
            return this.projectObj.panelScale;
        },
        calScale(): number {
            return this.panelScale / 100;
        },
    },
    components: {
        IconShape,
        CmptGroup,
        IconEcharts,
    },
    props: {
        leftNavH: {
            type: Number,
            default: 0,
        },
    },
    watch: {
        leftNavH() {
            throttle(this.setLeftNavDom);
        }
    },
    methods: {
        ...mapMutations([
            'add_part_his',
        ]),
        handleChange() {

        },
        getLeftNavDom() {
            if (!this.leftNavDom) {
                this.leftNavDom = document.querySelector(
                    '.leftnav .ln-body .el-tabs--left'
                );
                this.scrollDom = document.querySelector(
                    '.leftnav .ln-body .el-tabs--border-card > .el-tabs__content'
                );
            }
        },
        setLeftNavDom() {
            this.leftNavDom.style.height = `${this.leftNavH}px`;
            this.scrollDom.style.height = `${this.leftNavH}px`;
        },
        dragstart(event, dragItem, dragPerent) {
            const parentInfo = {
                sourceType: dragPerent.sourceType,
                type: dragPerent.type,
            };
            const partInfo = {
                ...dragItem,
                ...parentInfo
            };

            event.dataTransfer.setData("Text", JSON.stringify(partInfo));
            event.dataTransfer.dropEffect = "copy";
        },
        // dragend() {
        //     console.log("dragend");
        // },
        handleDropListener(isRemove?: boolean): void {
            // 如果id设置为canvas则canvas以外的区域无法被放置
            const viewPortDom = document.querySelector(`#screen-viewport`);
            const dragEventConfig = [
                {
                    key: 'dragover',
                    bindEvt: this.dragover,
                },
                // {
                //     key: 'dragenter',
                //     bindEvt: this.dragenter,
                // },
                {
                    key: 'dragleave',
                    bindEvt: this.dragleave,
                },
                {
                    key: 'drop',
                    bindEvt: this.dropPart,
                },
            ];
            dragEventConfig.forEach( (bindItem: { key: string, bindEvt: (evt: any) => void }) => {
                if (isRemove) {
                    viewPortDom.removeEventListener(bindItem.key, bindItem.bindEvt);
                } else {
                    viewPortDom.addEventListener(bindItem.key, bindItem.bindEvt);
                }
            });
        },
        bindDropListener(): void {
            this.handleDropListener();
        },
        dragover(evt: any) {
            // 阻止向上冒泡
            evt.preventDefault();
        },
        dragenter(evt: any): void {
            evt.dataTransfer.setData('Text', evt.target.id);
        },
        dragleave(evt: any) {

        },
        dropPart(ev: any): void {
            ev.preventDefault();
            const dragInfo = JSON.parse(ev.dataTransfer.getData('Text'));

            const partInfo = {
                // 兼容fireFox用layer[x or y]
                ...dragInfo,
            };
            
            // 取得相对元素的位置
            const halfPartDefaultW = 50;
            const halfPartDefaultH = 50;
            const domOffSet = $('#canvas').offset();
            const x = (ev.x - domOffSet.left - halfPartDefaultW) / this.calScale;
            const y = (ev.y - domOffSet.top - halfPartDefaultH) / this.calScale;
            const partStyle = {
                y,
                x,
            };

            if (partInfo.partStyle) {
                partInfo.partStyle = {
                    ...partInfo.partStyle,
                    ...partStyle,
                };
            } else {
                partInfo.partStyle = partStyle;
            }

            // 这里应该传递三级的菜单，这里如果发现没有这个part不应该调用addPart
            // 因为add_part会进行历史管理...
            this.add_part_his(partInfo);
        },
        unbindDropListener() {
            const removeBind = true;
            this.handleDropListener(removeBind);
        },
        editorEvtReceiver(isDestoyed?: boolean) {
            const eventObjCfg = [
                {
                    name: 'dropBind',
                    handle: this.bindDropListener,
                },
                {
                    name: 'dropUnbind',
                    handle: this.unbindDropListener,
                },
            ];
            // 存在两个以上子组件引用，需要改善
            for (const eventObj of eventObjCfg) {
                if (isDestoyed) {
                    this.$root.busRef.$off(eventObj.name);
                } else {
                    this.$root.busRef.$on(eventObj.name, ( eventType: string ) => {
                        eventObj.handle();
                    });
                }
            }
        }
    },
    mounted() {
        this.getLeftNavDom();
        this.setLeftNavDom();
        this.bindDropListener();
        this.editorEvtReceiver();

    },
    beforeDestroy() {
        this.leftNavDom = null;
        this.scrollDom = null;
        this.editorEvtReceiver(true);
        // fixMe mounted拿不到dom
        // this.unbindDropListener();
    }
})
