import AbstractOption from '../../../common/abstractOption'
import updatePropsDeep from 'utils/initProps'

/**
 * 基础报表的行配置项
 */
export default class TableRow extends AbstractOption {

  public config: any;

  constructor(opts?: TableReport.Row) {
    super();
    this.config = this.init(opts);
  }

  public init(opts?: TableReport.Row) {
    const source: TableReport.Row = {
      evenBackground: '#eee',
      oddBackground: '#fff',
      line: {
        width: 1,
        color: '#ccc',
        style: 'solid'
      }
    };
    return updatePropsDeep(source, opts);
  }

  public computed(): void { }

}