import AbstractOption from '../../../common/abstractOption'
import updatePropsDeep from 'utils/initProps'

/**
 * 基础报表的表头配置项
 */
export default class Thead extends AbstractOption {

  public config: any;

  constructor(opts?: TableReport.Thead) {
    super();
    this.config = this.init(opts);
  }

  public init(opts?: TableReport.Thead) {
    const source: TableReport.Thead = {
      show: true,
      background: '#409EFF',
      color: '#fff',
      fontSize: 16,
      fontWeight: 'normal',
      textAlign: 'left'
    };
    return updatePropsDeep(source, opts);
  }

  public computed(): void { }

}