


import Vue from 'vue';
import { mapMutations, mapState } from 'vuex';

export default Vue.extend({
    name: 'Lock',
    computed: {
        ...mapState({
            basePartList: (state: any) => state.screen.pageManage.partContainer,
            selector: (state: any) => state.screen.editor,
        }),
        partId() {
            return this.selector.partId;
        },
        partSelection() {
            return this.selector.partSelection;
        },
        lockGroup() {
            return this.selector.lockPart.lockGroup;       
        },
        selectGroup() {
            return this.partSelection.selectGroup;
        }
    },
    methods: {
        ...mapMutations([
            'unlock_group',
            'lock_group',
            'setIsLocked',
            'mapLockGroup',
            'clearLockGroup',
            'setDisableDrag',
            'updateRefLine',
            'updateSelectGroup',

        ]),
        updataLockState(): void {
            // 如果是多选的情况，不可能存在该事件
            const selectPartId = this.partId;
            const isMultiply = this.partSelection.type;
            const lockGroup = [];
            if (isMultiply === 'multiply') {
                return;
            }
            // this.unlockMapSelector();
            this.setDisableDrag(false);
            this.clearLockGroup();
            this.setIsLocked(false);
            
            this.basePartList.map( (item) => {
                if (item.partId === selectPartId) {
                    if (item.isLocked) {
                        lockGroup.push(item);
                        this.setDisableDrag(true);
                        this.setIsLocked(true);
                        this.mapLockGroup(lockGroup);
                        // this.lockMapSelector(lockGroup);
                    }
                }
            });
        },
        /**
         * 该方法目前会影响，rubberBand的操作
         */
        lockPart() {
            const selectPartId = this.partId;
            const selectGroup = this.partSelection.selectGroup;
            const selectType = this.partSelection.type;
            let lockGroup: any[] = [];
            // 多选的情况
            if ( selectType === 'multiply' ) {
                const selectIndexArr: any[] = [];
                selectGroup.map((it) => {
                    selectIndexArr.push(it.partId);
                });
                if ( selectGroup.length > 1 ) {
                    this.basePartList.map( (item, index) => {
                        if ( selectIndexArr.includes(item.partId) ) { 
                            item.isLocked = true;
                            const mapPartList = {
                                partIndex: index,
                                clonePart: item,
                            };
                            // 更新part的refline
                            this.updateRefLine(item);
                        }
                    });
                }
                // 这里是拷贝了当前选中的partGgroup
                lockGroup = selectGroup;
            } else {
                // 单选的情况,如果不存在partId，则不会执行单个part的锁定
                if ( !selectPartId ) {
                    return;
                }
                const selectIndex = this.basePartList.findIndex( (item) => {
                    return item.partId === selectPartId;
                });
                if ( selectIndex === -1 ) {
                    return;
                }

                // 只是引用
                const lockPart = this.basePartList[selectIndex];
                lockPart.isLocked = true;
                const mappartList = {
                    partIndex: selectIndex,
                    clonePart: lockPart,
                };
                // 更新part的refline
                this.updateRefLine(lockPart);
                lockGroup = [lockPart];
                // this.basePartList[selectIndex].isLocked = true;
            }
            // 更新选中元素
            this.updateSelectGroup(lockGroup);

            this.lockMapSelector(lockGroup);
        },
        // 优化为多选选中锁定，也能解锁
        unlockPart() {
            const lockPartIdGroup: string[] = [];
            for (const selPart of this.selectGroup) {
                if (selPart.isLocked) {
                    lockPartIdGroup.push(selPart.partId);
                }
            }
            if (!lockPartIdGroup.length) {
                return;
            }
            
            const updatePartGroup: Model.Part[] = [];
            // 将选中的锁定项改为不锁定
            this.basePartList.forEach( (item, index) => {
                if ( lockPartIdGroup.includes(item.partId) ) {
                    item.isLocked = false;
                    const mapPartList = {
                        partIndex: index,
                        clonePart: item,
                    };
                    // 更新part的refline
                    this.updateRefLine(item);
                    updatePartGroup.push(item);
                }
            });

            // 更新选中元素
            this.updateSelectGroup(updatePartGroup);
            this.unlockMapSelector();
        },
        clearLockPart(): void {
            if (this.lockGroup.length !== 0) {
                this.unlockMapSelector();
            }
        },
        lockMapSelector(lockGroup): void {
            this.setDisableDrag(true);
            const lockObj = {
                isLocked: true,
                part: lockGroup,
            };
            this.lock_group(lockObj);
        },
        unlockMapSelector(): void {
            this.setDisableDrag(false);
            this.unlock_group(false);
        },
        /**
         * 初始化监听事件接收器
         */
        editorEvtReceiver(isDestoyed?: boolean) {
            const eventObjCfg = [
                {
                    name: 'locking',
                    handle: this.lockPart,
                },
                {
                    name: 'unlock',
                    handle: this.unlockPart,
                },
            ];
            // 存在两个以上子组件引用，需要改善
            for (const eventObj of eventObjCfg) {
                if (isDestoyed) {
                    this.$root.busRef.$off(eventObj.name);
                } else {
                    this.$root.busRef.$on(eventObj.name, (eventType: string) => {
                        eventObj.handle();
                    });
                }
            }
        },
    },
    mounted() {
        this.editorEvtReceiver();
    },
    beforeDestroy() {
        this.editorEvtReceiver(true);
    },
});
