import BaseLineModel from './abstractBaseline'
import { option, data } from '../config/defaultOption/areaLine'

/**
 * 区域折线图
 * 与基础折线区别于series的areaStyle.color传参
 */
export default class AreaLine extends BaseLineModel {

  constructor(partType: Model.Part) {
    super(partType);
    const defaults = partType.shapeStyle || option;
    this.staticData = partType.staticData || data;
    this.guiAdapter = null;
    this.initialOption = null;
    this.optionGroup = null;
    this.guiOption = [];

    super.getChartInitOption(defaults, this.staticData);
  }

  public isInitArea(): boolean {
    return true;
  }

}


