






import Vue from 'vue'
export default Vue.extend({
  name: 'input-el',
  props: {
    opts: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  data() {
    return {
      input: ''
    }
  },
  computed: {
    modelholader: {
      get(): void {
        this.input = this.opts.model || ''
      },
      set(val: string): void {
        this.input = val
      }
    }
  },
  methods: {
    handleChange() {
      this.$emit('on-data-change', this.opts, this.input)
    }
  }
})
