






















import Vue from 'vue'
import { mapState, mapMutations } from 'vuex'
import inputNum from './form/inputNum.vue'

export default Vue.extend({
  name: 'publicStyle',
  data() {
    return {
      sourceList: [
        {
          label: '图表尺寸',
          id: 'size',
          children: [
            {
              hint: '宽度',
              key: 'width',
              disable: false,
              max: Infinity,
              min: 0,
              step: 1,
              value: 0
            },
            {
              hint: '高度',
              key: 'height',
              disable: false,
              max: Infinity,
              min: 0,
              step: 1,
              value: 0
            }
          ]
        },
        {
          label: '图表位置',
          id: 'position',
          children: [
            {
              hint: '横坐标',
              key: 'x',
              disable: false,
              max: Infinity,
              min: -Infinity,
              step: 1,
              value: 0
            },
            {
              hint: '纵坐标',
              key: 'y',
              disable: false,
              max: Infinity,
              min: -Infinity,
              step: 1,
              value: 0
            }
          ]
        },
        {
          label: '其他',
          id: 'other',
          children: [
            {
              hint: '旋转角度',
              key: 'angle',
              disable: false,
              max: 360,
              min: 0,
              step: 1,
              value: 0
            },
            {
              hint: '透明度',
              key: 'opacity',
              disable: false,
              max: 1,
              min: 0,
              step: 0.05,
              value: 1
            }
          ]
        }
      ]
    }
  },
  components: {
    inputNum
  },
  computed: {
    ...mapState({
      partSelection: (state: any) => state.screen.editor.partSelection,
      selectType: (state: any) => state.screen.editor.partSelection.type
    }),
    selectGroup(): Model.Part[] {
      return this.partSelection.selectGroup
    },
    curSelectPart(): Model.Part {
      return this.selectGroup.length === 1 ? this.selectGroup[0] : null
    },
    partStyle(): any {
      return this.curSelectPart ? this.curSelectPart.partStyle : {}
    },
    propsList(): any {
      return this.initPropsList(this.curSelectPart, this.sourceList)
    }
  },
  methods: {
    ...mapMutations(['updateSelectPart']),
    initPropsList(curSelectPart: any, sourceList: any[]): any[] {
      if (!curSelectPart) {
        return []
      }
      // 暂时隐藏图表透明度修改
      const otherItem = sourceList.find((item: any) => item.id === 'other')
      const isDisable = curSelectPart.sourceType === 'echarts' ? true : false
      otherItem.children[1]['disable'] = isDisable
      return sourceList
    },
    inputNumChange(side: any, value: any) {
      this.handleSetStyle(side, value)
    },
    handleSetStyle(side: any, value: any) {
      const option = {
        [side.key]: value
      }

      // 修改part实例的公共属性
      if (this.curSelectPart) {
        this.curSelectPart.setPublicStyle(option)
      }

      // 修改partSelection的属性
      if (this.selectType === 'single') {
        this.updateSelectPart(option)
      }
    }
  }
})
