

















import Vue from 'vue';
export default Vue.extend({
    data() {
        return {
            src: 'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg',
            srcArray: [],
        };
    },
    props: {
        imageModalShow: {
            type: Boolean,
            default: false,
        },
    },
    computed: {
        modalShow: {
            get() {
                return this.imageModalShow;
            },
            set(value: boolean) {
                this.$emit( 'handleImageModal', value);
            },
        },
    },
    methods: {
        initAllImg() {
            const requireAll = ( context: any ) => { 
                return context.keys().map(context);
            };
            requireAll(require.context('@/assets/image', true, /\.jpg$/)).forEach( (item) => {
                this.srcArray.push(item);
            });
        },
        getImgSrc(srcImg: string): void {
            this.$emit( 'getImgSrc', srcImg);
        }
    },
    mounted() {
        this.initAllImg();
    },
});
