import global from '../config/global';
import thead from '../config/thead';
import row from '../config/row';
import column from '../config/column';
import series from '../config/series';
import data from '../config/data'
// 基础配置项适配器
import OptionAdapter from '../../../common/adapter/baseOptionAdapter';

/**
 * 基础报表配置项适配器
 */
export default class BaseTableAdapter extends OptionAdapter {

  constructor() {
    super();
  }

  public getDefaultOption(type: string): object {
    const map: any = {
      baseTable: {
        global,
        thead,
        row,
        column,
        series,
        data
      },
    };
    return map[type] || {};
  }

}