export default class RubberBandCood {
    /**
     * 框线选中的x坐标
     */
    public x: number = 0;

    /**
     * 框线选中的y坐标
     */
    public y: number = 0;

    /**
     * 框线选中的宽度
     */
    public width: number = 0;

    /**
     * 框线选中的高度
     */
    public height: number = 0;

    constructor() {
    }

    public mapRubberBandCood( cood: RubberBandCood): void {
        const { x = 0, y = 0, width = 0, height = 0 } = cood;
        this.x = x;
        this.y = y;
        this.width = width;
        this.height = height;
    }

}