












import Vue from 'vue'
const idPrefix = 'text'

export default Vue.extend({
  name: 'textShape',
  props: ['initOption'],
  data() {
    return {
      content: '双击编辑文本',
      id: this._.uniqueId(idPrefix),
      hcenter: true,
      vmiddle: true,
      // 文本可编辑
      contentEditable: false,

      styleMap: {
        // 全局
        width: 200,
        height: 100,
        left: 0,
        top: 0,
        zIndex: 1,
        opacity: 1,
        // 角度
        transform: 0,

        // 文字
        fontSize: 14,
        color: '#333',
        fontStyle: 'normal',
        fontWeight: 'normal',
        // left center right justify
        textAlign: 'center',
        // top middle bottom
        alignItems: 'middle',

        // 文本格式
        padding: 0,
        lineHeight: 20,

        // 阴影
        shadowColor: 'transparent',
        shadowH: 0,
        shadowV: 0,
        shadowBlur: 0
      },
      textStyMap: {
        padding: 0
      },
      horClass: {
        hleft: false,
        hcenter: true,
        hright: false,
        hjustify: false
      },
      verClass: {
        vtop: false,
        vmiddle: true,
        vbottom: false
      },
      recordAxis: {
        ver: '',
        hor: ''
      }
    }
  },
  computed: {
    rectStyle(): object {
      return {
        width: `${this.styleMap.width}px`,
        height: `${this.styleMap.height}px`,
        left: `${this.styleMap.left}px`,
        top: `${this.styleMap.top}px`,
        zIndex: `${this.styleMap.zIndex}px`,
        opacity: this.styleMap.opacity,
        transform: `rotate(${this.styleMap.transform}deg)`,

        fontSize: `${this.styleMap.fontSize}px`,
        color: `${this.styleMap.color}`,
        fontWeight: this.styleMap.fontWeight,
        fontStyle: this.styleMap.fontStyle,
        textAlign: this.styleMap.textAlign,

        padding: `${this.styleMap.padding}px`,
        lineHeight: `${this.styleMap.lineHeight}px`,

        // textShadow无spread
        textShadow: `
            ${this.styleMap.shadowH}px 
            ${this.styleMap.shadowV}px
            ${this.styleMap.shadowBlur}px
            ${this.styleMap.shadowColor}`
      }
    },
    textStyle(): object {
      // 解决拖拽过程中，选中文本的情况
      const canUserSelect = this.contentEditable ? 'text' : 'none'
      return {
        userSelect: canUserSelect,
        padding: `${this.styleMap.padding}px`
      }
    },
    editorId(): string {
      return 'editor' + this.id
    }
  },
  methods: {
    /**
     * 双击text，可以编辑
     */
    dbTextClick() {
      this.contentEditable = true
      const dom: any = document.querySelector(`#${this.editorId}`)
      this.selectText(dom)
      dom.focus()
      // 编辑开始
      // this.$emit('textEditedBegin');
    },

    /**
     * 处理双轴文本的位置
     * left center right justify
     * textAlign: 'center',
     * top middle bottom
     * alignItems: 'middle',
     */
    handleAxisClass(option: any) {
      const handleCache = () => {
        const textAlign = option.textAlign
        const alignItems = option.alignItems
        if (textAlign !== '' || textAlign !== this.recordAxis.hor) {
          handleClass('hor', textAlign)
          this.recordAxis.hor = textAlign
        }
        if (alignItems !== '' || alignItems !== this.recordAxis.ver) {
          handleClass('ver', alignItems)
          this.recordAxis.ver = alignItems
        }
      }
      const handleClass = (type, arg) => {
        let typeValue = ''
        let typeClass = {}
        if (!arg) {
          return
        }
        if (type === 'hor') {
          typeValue = 'h' + arg
          typeClass = this.horClass
        } else {
          typeValue = 'v' + arg
          typeClass = this.verClass
        }
        Object.keys(typeClass).forEach(key => {
          if (typeValue === key) {
            typeClass[key] = true
          } else {
            typeClass[key] = false
          }
        })
      }
      handleCache()
    },

    /**
     * 全选文本
     */
    selectText(element: HTMLElement) {
      const doc = document
      let range
      let selection
      if (doc.body.createTextRange) {
        range = doc.body.createTextRange()
        range.moveToElementText(element)
        range.select()
      } else if (window.getSelection) {
        selection = window.getSelection()
        range = doc.createRange()
        range.selectNodeContents(element)
        selection.removeAllRanges()
        selection.addRange(range)
      }
    },

    /**
     * 设置属性参数，父组件通过调用该方法，更新所有的属性
     *  option {
     *     ...
     *  }
     */
    setOption(option: any) {
      const styles: object = option.content || {}
      if (this._.isPlainObject(styles)) {
        for (let [key, value] of Object.entries(styles)) {
          if (!this._.isNil(this.styleMap[key])) {
            this.styleMap[key] = value
          }
        }

        this.handleAxisClass(this.styleMap)
      }
    },
    handleIntOption() {
      this.setOption(this.initOption)
    },
    inputBlur($event: any) {
      if (this.contentEditable) {
        const getEventHtml = $event.target.innerHTML
        this.content = ''
        this.content = getEventHtml
      }
      this.contentEditable = false
      // 编辑结束，提交编辑的内容
      // this.$emit('textEditedEnd', this.content);
    }
  },
  mounted() {
    this.handleIntOption()
  }
})
