/*
 * @Description: 属于当个part的数据源配置模型
 * 1. 缓存各个模型的数据 -- 静态和测点
 * 2. 混存script或是filter脚本字符串
 * 3. 缓存展示数据
 * 
 * 后续需要更新的地方
 * DsType：数据源类型
 *  0表示静态数据源
 *  1表示测点数据源
 *  2表示Api数据源 

 * valueType取值类型
 *   0 实时值 1 历史值 
 * childType
 *   0 工程值，1 统计值
 * 
 * @Author: chuguo.zheng
 * @Date: 2019-08-05 16:10:28
 */
import StaticModel from './staticModel';
import PointModel from './pointModel';
export default class PartDataSource implements Model.DataSource{
    [key: string]: any;

    /**
     * 数据源类型 [static, point]
     * 
     * 数据源类型被获取为纯数据时，每个实例会被抽离为一个list
     * 比如： sourceTypeList: [staticData, pointData, apiData...]
     * 
     * enum 
     * 
     */
    public sourceType: number;

    /**
     * 该数据源id
     * uuid
     * dataSource_id
     */
    public dataSourceId: string;

    /**
     * 静态数据持久化
     */
    public staticData: StaticModel;

    /**
     * 测点数据持久化
     */
    public pointData: PointModel;

    /**
     * 是否开启脚本过滤
     */
    public useScript: boolean;

    /**
     * 保存脚本编辑的数据，这里是否应该拓展为一个类
     */
    public scriptLog: string;

    /**
     * 展示的数据，目前是由返回数据转成字符串
     */
    public displayData: any;

    constructor(partData: any) {
        this.init(partData);
    }

    public init(partData: any = {staticData: {}, pointData: {}}) {
        const { 
            staticData,
            pointData,
            sourceType,
            useScript,
            scriptLog,
            displayData,
        } = partData;
        this.staticData = new StaticModel(staticData || {});
        this.pointData = new PointModel(pointData || {});
        this.sourceType = sourceType || '';
        this.useScript = useScript || false;
        this.scriptLog = scriptLog || '';
        this.displayData = displayData || '';
    }
    /**
      * @description 
      * 
      * @param {*} partData 
      * @param {string} [mode] 指定模型 -- ['staticData', 'pointData']
     */
    public updateProps(partData: Model.DataSource, mode?: string): void {
        if (!_.isObject(partData)) {
            return;
        }
        const modeGroup = ['staticData', 'pointData'];
        if (mode && modeGroup.includes(mode)) {
            (this as any)[mode].updateProps(partData);
            return;
        }
        let propKey: any;
        for (propKey of Reflect.ownKeys(partData)) {
            if (Reflect.has(this, propKey)) {
                if (modeGroup.includes(propKey)) {
                    (this as any)[propKey].updateProps(partData[propKey]);
                }
                (this as any)[propKey] = partData[propKey];
            }
        }
    }

    public getPartAllProps(): Model.DataSource {
        return {
            staticData: this.staticData.getPartAllProps(),
            pointData: this.pointData.getPartAllProps(),
            sourceType: this.sourceType,
            useScript: this.useScript,
            scriptLog: this.scriptLog,
        }
    }


    public destroyed() {

    }
}