

































import Vue from 'vue';
import Alarm from './alarm.vue';
export default Vue.extend({
    name: 'Tool',
    data() {
        return {
            // 右侧tab页面
            activeName2: 'page',
            // 右侧tool
            activeTool1: false,
            // poper test
            gridVisible: false,
            pagesVisible: false,

            drawer: false,
        }
    },
    components: {
        Alarm,
    },
    computed: {
        iconActive() {
            return {
                color: '#66b1ff',
            };
        },
    },
    methods: {
        toggleTool() {
            this.tool1Show = !this.tool1Show
            this.activeTool1 = !this.activeTool1
        },
        handleShowPoper(value: boolean): void {
            this.gridVisible = value
        },
        handleShowPages(value: boolean): void {
            this.pagesVisible = value
        }
    }
});
