import Cmpt from './cmpt';

export class CmptGroup implements Model.CmptGroup {

    public size: number;

    public groupName: string;

    public cmptGroup: Cmpt[];

    public canDel: boolean;

    public cmptGroupId: string;

    public isEditor: boolean;

    public isHover: boolean;

    constructor(cmptGroup: Model.CmptGroup) {
        this.cmptGroupId = cmptGroup.cmptGroupId;
        this.groupName = cmptGroup.groupName || '默认';
        this.cmptGroup = cmptGroup.cmptGroup || [];
        this.isEditor = cmptGroup.isEditor || false;
        this.isHover = cmptGroup.isHover || false;
    }
}
