export default class SelectPartObject {
    [index: string]: any;

    /**
     * part选中的x坐标
     */
    public x: number = 0;

    /**
     * part选中的y坐标
     */
    public y: number = 0;

    /**
     * part选中的宽度
     */
    public width: number = 0;

    /**
     * part选中的高度
     */
    public height: number = 0;

    /**
     * part选中的角度
     */
    public angle: number = 0;

    /**
     * x缩放的比例,暂时没用到
     */
    public scaleX: number = 1;

    /**
     * y缩放的比例,暂时没用到
     */
    public scaleY: number = 1;

    public mapSelectPartObject(part: SelectPartObject): void {
        const props = new Set(['x', 'y', 'width', 'height', 'angle', 'scaleX', 'scaleY']);
        for (let [key, value] of Object.entries(part)) {
            if (props.has(key) && !_.isNil(value)) {
                this[key] = value;
            }
        }
    }

}
