import AbstractOption from '../../../common/abstractOption'
import updatePropsDeep from 'utils/initProps'

/**
 * 基础报表的公共配置项
 */
export default class TableCommon extends AbstractOption {

  public config: any;

  constructor(opts?: TableReport.Global) {
    super();
    this.config = this.init(opts);
  }

  public init(opts?: TableReport.Global) {
    const source: TableReport.Global = {
      flexible: true,
      fontFamily: 'sans-serif',
      line: {
        width: 1,
        color: '#ccc',
        style: 'solid'
      },
      paddingHorizontal: 10,
      paddingVertical: 15,
    };
    return updatePropsDeep(source, opts);
  }

  public computed(): void { }

}