import BaseChart from '../abstractChart'
import OptionAdapter from '../adapterOption/adapter/lineAndBar'
import GuiAdapter from '../adapterGui/adapter/line'
import DataTransfer from '../../common/dataTransfer/echarts/index'
import { option, data } from '../config/defaultOption/dblLine'

/**
 * 双y轴折线图模型
 */
export default class DblLineModel extends BaseChart {

  public guiAdapter: any;
  public initialOption: any;
  public optionGroup: any
  public guiOption: any;
  public defaultOption: any;
  public staticData: any;

  /**
   * yAxisIndex为0的数据系列长度
   */
  public partOneSeriesLen: any;

  constructor(partType: Model.Part) {
    super(partType);
    const defaults = partType.shapeStyle || option;
    this.staticData = partType.staticData || data;
    this.guiAdapter = null;
    this.initialOption = null;
    this.optionGroup = null;
    this.guiOption = [];
    this.defaultOption = null;

    super.getChartInitOption(defaults, this.staticData);
  }

  public initChartOption() {
    const adapter = new OptionAdapter();
    const initParam = super.getChartParams();
    this.optionAdapter = adapter;
    this.initialOption = adapter.init(this.defaultOption, 'dblLine');
    this.optionGroup = adapter.getInstance();

    super.initChart(initParam);
    // 双y轴需computedYAxis
    super.setChartOption(this.optionAdapter.computedYAxis(this.initialOption));
  }

  public initChartGui() {
    const adapter = new GuiAdapter();
    this.guiOption = adapter.init(this.initialOption, 'dblLine');
    this.guiAdapter = adapter;
  }

  public updateShapeStyle(formObj: any) {
    const guiId = formObj.rootId;
    const propKey: string = formObj.mKey;

    if (guiId === 'series') {
      const seriesIndex = formObj.index || 0;
      const seriesCopy = this.shapeStyle[propKey]
        .map((item: any, index: number) => {
          if (index === seriesIndex) {
            item = super.getUpdatedChartOption(item, this.updatedOption[propKey])
          }
          return item
        });
      this.shapeStyle[propKey].length = 0;
      this.shapeStyle[propKey] = seriesCopy;
    } else {
      this.shapeStyle[propKey] = super.getUpdatedChartOption(
        this.shapeStyle[propKey],
        this.updatedOption[propKey]
      );
    }
  }

  public updateChartOption(formObj: any) {
    const propKey: string = formObj.mKey;
    const currentOption = {
      [propKey]: this.shapeStyle[propKey]
    };

    // 双y轴需computedYAxis
    if (['yAxis', 'zAxis'].includes(propKey)) {
      const updatedObj = this.optionAdapter.computedYAxis(this.shapeStyle, true)
      super.setChartOption(updatedObj)
    }

    // series、数据种类变换的x轴
    if (['series', 'xAxis'].includes(propKey)) {
      super.setChartOption(currentOption)
    }

    // 公共属性
    if (!['yAxis', 'zAxis', 'series', 'xAxis'].includes(propKey)) {
      super.setChartOption(this.updatedOption)
    }
  }

  public isNeedComputedYAxis() {
    return true;
  }

  public setChartSeriesOption(defaults: Chart.Option, seriesData: any[]) {
    if (!seriesData.length) {
      return [];
    }

    const series1: Chart.SeriesItem[] = this.setSeriesOptionByYAxisIndex(defaults, seriesData, 0);
    this.partOneSeriesLen = series1.length;
    const series2: Chart.SeriesItem[] = this.setSeriesOptionByYAxisIndex(defaults, seriesData, 1);
    return series1.concat(series2);
  }

  public setSeriesOptionByYAxisIndex(defaults: Chart.Option, seriesData: any[], yAxisIndex: 0 | 1) {
    const series: Chart.SeriesItem[] = defaults.series.filter((item: any) => item.yAxisIndex === yAxisIndex);
    const dataList = new DataTransfer().getBothAxisSeiresData(seriesData, yAxisIndex);
    const diff = dataList.length - series.length;
    let addIndex = 0;

    // 合法的系列数据数量大于默认系列数量时自动添加默认系列项
    while (diff > 0 && diff > addIndex) {
      series.push(
        {
          yAxisIndex,
          label: { color: '' },
          lineStyle: { color: '' },
          itemStyle: { color: '' }
        }
      );
      addIndex++;
    }

    // 合法的系列数据数量小于默认系列数量时自动清除多余的默认系列项
    if (dataList.length && diff < 0) {
      series.length = dataList.length;
    }

    series.forEach((item: any, index: number) => {
      const _index = yAxisIndex === 1 ? this.partOneSeriesLen + index : index;
      const labelColor = super.getChartSeriesColor(_index);
      const name = yAxisIndex === 0 ? `y轴系列${index + 1}` : `y2轴系列${index + 1}`;
      item.name = item.name ? item.name : name;
      item.label.color = item.label.color ? item.label.color : labelColor;
      item.lineStyle.color = item.lineStyle.color ? item.lineStyle.color : labelColor;
      item.itemStyle.color = item.itemStyle.color ? item.itemStyle.color : labelColor;
      item.data = dataList[index];
    });
    return series;
  }

  public setChartLegendName() {
    return false
  }

}


