import AbstractOption from '../../../common/abstractOption'
import updatePropsDeep from 'utils/initProps'

/**
 * 图表直角坐标系内绘图网格的配置项
 * 默认为当前图表区域,以调整图表的top right bottom left位置
 */
export default class Grid extends AbstractOption {

  public config: any;

  constructor(opts?: Chart.Grid) {
    super();
    this.config = this.init(opts)
  }

  public init(opts?: Chart.Grid) {
    const source: Chart.Grid = {
      top: 60,
      right: 60,
      bottom: 60,
      left: 60
    };

    return updatePropsDeep(source, opts);
  }

  public computed() { }

}