
import Vue from "vue";
import { mapState } from 'vuex';
import historyIDB from './historyIDB';
import subMutaCfg from './subMutationCfg';
const cursorConifg = {
    maxSize: 30,
    threshold: 5,
};
export default Vue.extend({
    data() {
        return {
            // 订阅mutation的变量，用于释放订阅
            subscribeFuc: undefined,
            // 储存需要监听的事件
            subEventConfig: _.cloneDeep(subMutaCfg),
            // 模拟生成当前的队列
            cursorList: [],
            cursor: 1,
            // 储存历史的上限
            cursorMaxSize: cursorConifg.maxSize,
            // 到达上面删除的条数
            cursorThreshold: cursorConifg.threshold,
            // 记录之前删除的次数
            cursorTimes: 0,
            // 是否有数据库操作异常
            noError: true,

            // 能否再执行重做和撤销
            canRedo: true,
            canUndo: true,

            // 是否开始监听页面变化
            watchPageId: false,
        };
    },
    computed: {
        ...mapState({
            screen: (state: any) => state.screen,
            pageManage: (state: any) => state.screen.pageManage,
        }),
        pureModelMapper(): any {
            return this.$store.state.screen.pureModelMapper;
        },
        allCursorThreshold(): number {
            return this.cursorTimes * this.cursorThreshold;
        },
        currentPage(): Model.Page {
            return this.pageManage.currentPage;
        },
    },
    watch: {
        // 监听页面的改变，清楚历史记录
        currentPage() {
            if (this.watchPageId) {
                this.clearHistory().then(
                    () => {
                        this.initCursor();
                        this.addUndoState();
                    },
                    () => {
                        this.forbidHistory();
                    },
                );
            }
        }
    },
    methods: {
        /**
         * 撤销事件
         */
        undo() {
            if ( !this.noError || !this.canUndo ) {
                return;
            }
            this.cursor--;
            this.computeCursor();
            historyIDB.getIndex( (this.cursor) ).then(
                (success) => {
                    // this.pageManage.resetPartAllProps(success.state);
                    if (!success) {
                        return;
                    }
                    this.pureModelMapper.setHisPureModel(success.state);
                },
                (error) =>{
                    this.forbidHistory();
                }
            );
        },
        /**
         * 重做事件
         */
        redo() {
            if ( !this.noError || !this.canRedo ) {
                return;
            }
            this.cursor++;
            this.computeCursor();
            historyIDB.getIndex( (this.cursor) ).then(
                ( success: any ) => {
                    if (!success) {
                        return;
                    }
                    this.pureModelMapper.setHisPureModel(success.state);
                    // this.pageManage.resetPartAllProps(success.state);
                },
                ( error: any ) => {
                    this.forbidHistory();
                },
            );
            
        },
        /**
         * 新增撤销的快照
         * 游标同时也改变
         */
        addUndoState(mutation?): void {
            // fixme 这里如果使用computed的pureModelMapper，getHisPureModel会拿不到里面的引用
            const pureModelMapper = this.$store.state.screen.pureModelMapper;
            const partList = pureModelMapper.getHisPureModel();
            
            const snapshot = JSON.parse(JSON.stringify({
                index: this.cursor,
                state: partList
            }));
            
            historyIDB.addUndo( snapshot ).then(
                ( success ) => {
                    // console.log(success);
                    this.cursorList.push(this.cursor);
                    this.computeCursor();
                },
                ( error ) => {
                    // console.log(error); 
                    this.forbidHistory();
                }
            );
        },
        /**
         * 订阅突变
         * 只有在subEventConfig配置了的突变才需要记录
         */
        subMutation() {
            this.subscribeFuc = this.$store.subscribe( (mutation) => {
                if ( 
                    this.subEventConfig.includes(mutation.type) 
                    &&  this.noError
                ) { 
                    const realCursor = this.cursor - this.allCursorThreshold;
                    const len = this.cursorList.length;
                    if ( realCursor !== len ) {
                        const deleSnapshot = this.cursorList.splice( realCursor, (len - 1));
                        this.computeCursor();
                        historyIDB.clearRedo(deleSnapshot).then(
                            (success) => {

                            },
                            (error) => {
                                this.forbidHistory();
                            }
                        );   
                    }
                    this.handleCursorMaxSize();
                    this.cursor++;
                    this.addUndoState(mutation);
                    
                }
            });
        },
        /**
         * 当缓存在indexDB的值大于最大的容器值
         * 删除前面指定数目的项，类似队列，先进先出
         */
        handleCursorMaxSize() {
            if ( this.cursorList.length === this.cursorMaxSize ) {
                const deleSnapshot = this.cursorList.splice( 0, this.cursorThreshold );
                historyIDB.clearRedo(deleSnapshot).then(
                    (success) => {
                        this.cursorTimes++;
                    },
                    (error) => {
                        this.forbidHistory();
                    },
                ); 
            }
        },
        /**
         * 更新撤销重做的状态
         * 更新撤销重做的使能状态
         */
        computeCursor() {
            const hisStateArr: any[] = [];
            // 游标必须从之前删除后的节点开始算
            if (this.cursor === 1 + this.allCursorThreshold) {
                this.canUndo = false;
                hisStateArr.push({type: 'undo', cando: false});
            } else {
                this.canUndo = true;
                hisStateArr.push({type: 'undo', cando: true});
            }
            if ( this.cursor !== this.cursorList.length + this.allCursorThreshold ) {
                this.canRedo = true;
                hisStateArr.push({type: 'redo', cando: true});
            } else {
                this.canRedo = false;
                hisStateArr.push({type: 'redo', cando: false});
            }
            this.historyEvtEmitter(hisStateArr);
        },
        historyEvtEmitter(hisStateArr: any[]): void {
            if (!hisStateArr.length) {
                return;
            }
            const historyCfg: Map<string, any> = new Map([
                ['undo', { 
                    parent: 'history',
                    child: 'undo',
                    enable: false,
                }],
                ['redo', { 
                    parent: 'history',
                    child: 'redo',
                    enable: false,
                }],
            ]);
            const computeHisState: any[] = [];
            hisStateArr.forEach( ({type, cando}: any) => {
                const hisItem: any = historyCfg.get(type);
                hisItem.enable = cando;
                computeHisState.push(hisItem);
            });
            // 通过使能监听控制是否能用
            this.$root.busRef.$emit('enableWatch', computeHisState);
        },
        /**
         * 清楚历史数据，页面切换的时候的操作
         */
        clearHistory() {
            return new Promise( (resolve, reject) => {
                historyIDB.clearIndexDb().then(
                    () => {
                        resolve();
                    },
                    () => {
                        reject();
                    },
                );
            });
        },
        /**
         * 数据库操作异常，禁止使用历史功能
         */
        forbidHistory() {
            this.noError = false;
            const hisStateArr: any[] = [];
            hisStateArr.push({type: 'undo', cando: false});
            hisStateArr.push({type: 'redo', cando: false});
            this.historyEvtEmitter(hisStateArr);
            this.initCursor();
        },
        /**
         * 初始化游标信息
         */
        initCursor() {
            this.cursorList = [];
            this.cursor = 1;
            this.cursorMaxSize = cursorConifg.maxSize;
            this.cursorThreshold = cursorConifg.threshold;
            this.cursorTimes = 0;
        },
        /**
         * 初始化监听事件接收器
         */
        editorEvtReceiver(isDestoyed?: boolean): void {
            const eventObjCfg = [
                {
                    name: 'redo',
                    handle: this.redo,
                },
                {
                    name: 'undo',
                    handle: this.undo,
                }
            ];
            for (const eventObj of eventObjCfg) {
                if (isDestoyed) {
                    this.$root.busRef.$off(eventObj.name);
                } else {
                    this.$root.busRef.$on(eventObj.name, (eventType: string) => {
                        eventObj.handle();
                    });
                }
            }
        },
    },
    created() {
        this.subMutation();
    },
    mounted() {
        this.editorEvtReceiver();
        // this.initManager();
        // const closeResult = await historyIDB.closeIDB();
        // console.log(closeResult);
        historyIDB.initIndexedDB().then(
            async (success) => {
                // 这里是hack,每次刷新页面进来清楚之前的数据
                this.initCursor();
                await this.clearHistory();
                this.addUndoState();
                this.watchPageId = true;
            },
            (error) => {
                this.forbidHistory();
                throw new Error('初始化浏览器数据库报错');
            },
        );
        
    },
    beforeDestroy() {
        // 数据库的销毁会存在异常，需要解决
        // const closeResult = await historyIDB.closeIDB();
        // console.log(closeResult);
        // await historyIDB.closeIDBDatabase();

        // 销毁store注册的监听订阅
        this.subscribeFuc();
        this.editorEvtReceiver(true);
        historyIDB.destroyed();
    },
    render(h) {
        return h();
    },
});
