export default {
    /**
     * 新增page状态
     */
    add_state(state: Screen.state) {
        state.pageManage.addState();
    },

    /**
     * 移除page状态
     */
    remove_state(state: Screen.state, curState: any) {
        state.pageManage.removeState(curState);
    },

    /**
     * 更新page状态
     */
    setCurrentState(state: Screen.state, node: any) {
        if (!node) {
            return;
        }
        state.pageManage.updateCurrentState(node);
    }
}
