
import partMapList from './config';
import Group from '../group/group';
export default class PartBuilder {

    constructor(uniObj: any) {
        this.resetUniObj(uniObj);
        return this.matchUniPart(uniObj);
    }

    /**
     * 
     * 根据三级的level，找到相对的类对象
     * @param uniObj
     * {
     *  sourceType: '',
     *  type: '',
     *  childType: '',
     *  partType: '',  -- part, group, template
     *  ... 
     * }  
     * 
     * @memberOf Part
     */
    private matchUniPart(uniObj: any): any {
        const partType = uniObj.partType;
        let part: any = {};
        if (partType === 'group') {
            part = this.matchGroup(uniObj);
        } else {
            part = this.matchPart(uniObj);
        }
        return part || null;
    }

    private matchPart(uniObj: any): void {
        const lv1 = (partMapList as any)[uniObj['sourceType']];
        const lv2 = lv1 ? lv1[uniObj['type']] : null;
        const lv3 = lv2 ? lv2[uniObj['childType']] : null;
        if (_.isFunction(lv3)) {
            const part: any = lv3;
            const uniPartProto = part ? new part(uniObj) : null;
            return uniPartProto;
        }
    }

    private matchGroup(uniObj: any): any {
        const uniGroupProto = new Group(uniObj);
        return uniGroupProto;
    }

    private resetUniObj(uniObj: any) {
        // 设置图元默认的宽高
        const sourceType = uniObj.sourceType;
        const partStyle = uniObj.partStyle;
        
        if (['echarts', 'reports'].includes(sourceType)) {
            partStyle.width = partStyle.width || 400;
            partStyle.height = partStyle.height || 400;
        } else if (sourceType === 'shape') {
            partStyle.width = partStyle.width || 200;
            partStyle.height = partStyle.height || 200;
        } else {
            // 其他图元
        }

        // 设置refLine
        uniObj.refLine = this.initRefLine(partStyle);
    }

    private initRefLine(partStyle: any) {
        const { x, y, height, width } = partStyle;
        const refLine = {
            top: y,
            bottom: y + height,
            left: x,
            right: x + width,
            height,
            width,
        };
        return refLine;
    }

}
