/**
 * 基础的element配置项模型
 * 如图表、基础图形、基础报表
 */
export default abstract class AbstractOption {

  /**
   * 初始化的element配置项对象
   */
  abstract config: any;

  /**
   * 初始化element配置项
   * @param opts element配置项初始化对象
   */
  abstract init(opts?: any): void;

  /**
   * 计算element配置项组合属性
   * @param val 组合属性值
   * @param key 组合属性名
   */
  abstract computed(val: any, key?: string): void;

}