import { TransformType } from '../interface';

/**
 * 改变图形元素的代理层transform
 */
export default class Transform implements TransformType{

    // 控制transform的显示隐藏
    public selectBox: boolean = false;

    public scaleLimit: number = 0.1;

    public snap: number = 0.1;

    public bounds: boolean = false;

    public disableScale: boolean = false;

    public disableRotate: boolean = false;

    public disableSelect: boolean = false;

    public disableDrag: boolean = false;

    public selectOn: string = 'click'; // mousedown, dbclick

    constructor() {}

    /**
     * 更新部分transform的属性
     */
    public updateTransform( transformObj: Editor.TransformType ) {
        // 如果存在type就不再执行所有属性的赋值
        if (transformObj.type) {
            this.handleSelectType(transformObj.type);
            return;
        }
        this.initProps(transformObj);
    }

    /**
     * 专门处理带有type（single | multiply）的数据
     */
    public handleSelectType( type: string ): void {
        if (type === 'multiply') {
            this.setSelectedBox(true);
            this.setDisableRotate(true);
            this.setDisableScale(true);
        } else if (type === 'single') {
            this.setSelectedBox(true);
            this.setDisableRotate(false);
            this.setDisableScale(false);
        }
    }

    // 设置当前当前是否选中的变换框...
    public setSelectedBox( isSelectBox: boolean ): void {
        this.selectBox = isSelectBox;
    }

    public setScaleLimit( scaleLimit: number ): void {
        this.scaleLimit = scaleLimit;
    }

    public setSnap( snap: number ): void {
        this.snap = snap;
    }

    public setDisableScale( isDisableScale: boolean ): void {
        this.disableScale = isDisableScale;
    }

    public setDisableRotate( isDisableRotate: boolean ): void {
        this.disableRotate = isDisableRotate;
    }

    public setDisableSelect( isDisableSelect: boolean): void {
        this.disableSelect = isDisableSelect;
    }

    public setDisableDrag( isDisableDrag: boolean ): void {
        this.disableDrag = isDisableDrag;
    }

    public setSelectOn( selectOn: string ): void {
        this.selectOn = selectOn;
    }

    public getPureModel(): TransformType {
        return {
            selectBox: this.selectBox,
            scaleLimit: this.scaleLimit,
            snap: this.snap,
            bounds: this.bounds,
            disableScale: this.disableScale,
            disableRotate: this.disableRotate,
            disableSelect: this.disableSelect,
            disableDrag: this.disableDrag,
            selectOn: this.selectOn
        }
    }

    /**
     * 获取纯对象
     */
    public setPureModel( model: TransformType ): void {
        this.initProps(model);
    }

    public destroyed(): void {
        this.selectBox = null;
        this.scaleLimit = null;
        this.snap = null;
        this.bounds = null;
        this.disableScale = null;
        this.disableRotate = null;
        this.disableSelect = null;
        this.selectOn = null;
        this.disableDrag = null;
    }

    /**
     * 初始化存在的属性
     * 
     * @private
     */
    private initProps( transformObj: TransformType ) {
        const allProps = new Set([
            'selectBox',
            'scaleLimit',
            'snap',
            'bounds',
            'disableScale',
            'disableRotate',
            'disableSelect',
            'disableDrag',
            'selectOn',
        ]);
        const self: any = this;
        for (const [key, value] of Object.entries(transformObj)) {
            if (allProps.has(key) && !_.isNil(value)) {
                self[key] = value; 
            }
        } 
    }
}
