const mutations = {
    // 新增组件
    add_cmpt(state: Screen.state, cmpt: any) {
        state.cmptManage.addCmpt(cmpt);
    },

    // 新增组件容器
    add_cmpt_group(state: Screen.state, cmptGroupName: string) {
        state.cmptManage.addCmptGroup(cmptGroupName);
    },

    // 移除组件容器
    remove_cmpt_group(state: Screen.state, cmpt: any) {
        state.cmptManage.removeCmptGroup(cmpt);
    },

    // 编辑组件容器
    edit_cmpt_group(state: Screen.state, cmpt: any) {
        state.cmptManage.editCmptGroup(cmpt);
    },

    // 移动组件
    move_cmpt(state: Screen.state, cmptArgs: any) {
        state.cmptManage.moveCmpt(cmptArgs);
    },

    // 删除组件
    remove_cmpt(state: Screen.state, cmptArgs: any) {
        state.cmptManage.removeCmpt(cmptArgs);
    },

    edit_cmpt(state: Screen.state, cmptArgs: any) {
        state.cmptManage.editCmpt(cmptArgs);
    }
};

const getter = {
    cmptManage: (state: Screen.state) => state.cmptManage
};

export default {
    mutations,
    getter
};
