export default abstract class Adapter {

  abstract getDefaultOption(): any[];

  public initGuiOption(shapeStyle: any) {
    const guiOption: any[] = this.getDefaultOption();

    for (let item of guiOption) {
      for (let value of Object.values(shapeStyle)) {
        this.assignPublicGuiValueDeep('content', value, item.children)
      }
    }
    return guiOption
  }

  public updateGuiOption(guiOption: any[], currentStyle: any, opts: any): any[] {
    const guiId = opts.rootId;
    const currentOption = guiOption.find((item: any) => item.id === guiId);
    this.assignPublicGuiValueDeep('content', currentStyle, currentOption.children);
    return guiOption
  }

  public assignPublicGuiValueDeep(key: string, value: any, option: any[]): void {
    for (let item of option) {
      const hasChild = item.children.length
      if (!hasChild) {
        const mKey = item['component']['mKey']
        if (mKey === key) {
          const path = item['component']['path']
          const modelPath = path.replace(`${mKey}.`, ``)
          item['component']['model'] = _.get(value, modelPath)
        }
      } else {
        this.assignPublicGuiValueDeep(key, value, item.children)
      }
    }
  }
}