import AbstractOption from '../../../common/abstractOption'

export default class Border extends AbstractOption {

  public config: any;

  constructor(shapeStyle?: Shape.Border) {
    super();
    this.config = this.init(shapeStyle);
  }

  public init(shapeStyle?: Shape.Border): object {
    const {
      borderWidth = 0,
      borderColor = '#ccc',
      borderStyle = 'solid',
    } = shapeStyle || {};

    return {
      borderWidth,
      borderColor,
      borderStyle
    };
  }

  public computed(): void { }

}