
















































import { mapGetters, mapMutations } from 'vuex';
import LayerItem from './layerItem.vue';
import LayerGroupItem from './layerGroupItem.vue';
import LayerCtm from './layerCtm.vue';
interface LayerNode {
    partId: string;
    partName: string;
    isSelected: boolean;
    partType: string;
    partGroup: LayerNode[];
}
export default {
    data() {
        return {
            searchLayer: '',
            layerCtm: {},
            curSinglePart: {},
            layerTree: [],
        };
    },
    components: {
        LayerItem,
        LayerGroupItem,
        LayerCtm,
    },
    computed: {
        ...mapGetters([
            'partContainer',
            'partSelection',
        ]),
        // layerTree(): Model.Part[] {
        //     return this.partContainer;
        // },
        selectGroup(): any[] {
            return this.partSelection.selectGroup;
        },
        selectType(): string {
            return this.partSelection.type;
        },
    },
    watch: {
        partContainer(val) {
            this.mapLayerTree();
        },
        selectGroup( newSelectGroup: Model.Part[] ) {
            const partIdArr: string[] = [];
            newSelectGroup.forEach( (part) => {
                const partId: string = part.partId;
                partIdArr.push(partId);
            });
            this.updateSelected(partIdArr);
        }
    },
    methods: {
        ...mapMutations([
            'setPartId',
            'setDisableRotate',
            'setDisableScale',
            'clearSelectGroup',
            'updateSelectPart',
            'updateSelectGroup',
            'setType',
        ]),
        mapLayerTree() {
            if (this.searchLayer) {
                this.searchLayerEvt();
                return;
            }
            this.layerTree = this.partContainer;
        },
        /**
         * 搜索图层
         */
        searchLayerEvt() {
            if (!this.searchLayer) {
                this.layerTree = this.partContainer;
                return;
            }
            const filterPartList = this.partContainer.filter((part) => {
                return part.partName.indexOf(this.searchLayer) !== -1 ? true : false; 
            });
            this.layerTree = filterPartList;
            
        },
        hasChangedName(partName: string): void {
            const prop = {
                partName,
            };
            // 更新model的part属性，同时更新映射后的数据
            this.curSinglePart.partName = partName;

            // 如果选在存在索搜状态，需要进行一次过滤操作
            if (this.searchLayer) {
                this.searchLayerEvt();
            }
        },
        changeLayerName() {
            this.curSinglePart.isEdit = true;
        },
        /**
         * 事件触发修改当前选中的part
         */
        setCurrentLayer( currentPart: Model.Part, keyDown?: string ) {
            if (keyDown) {
                this.handleMultiplyLayer(currentPart);
                return;
            }
            this.curSinglePart = currentPart;
            this.mapSelector(currentPart);
        },
        /**
         * 事件触发修改当前多个选中的part
         */
        handleMultiplyLayer( curPart: Model.Part ) {
            let inSelGroupIndex: number;
            const isInSelGroup = () => {
                return this.selectGroup.some( (selPart: Model.Part, index: number) => {
                    if (selPart.partId === curPart.partId) {
                        inSelGroupIndex = index;
                        return true;
                    }
                });
            };
            
            // 判断触发的part是否在selectGroup中
            if (!isInSelGroup()) {
                const newSelGroup = this.selectGroup;
                let isMulti = true;
                // 更新为引用part
                const newCurPart = this.partContainer.find( (part: Model.Part) => {
                    return part.partId === curPart.partId;
                });
                if (newCurPart) {
                    newSelGroup.push(newCurPart);
                }
                if (newSelGroup.length === 1) {
                    isMulti = false;
                }
                // 更新selectGroup
                this.$root.busRef.$emit('updateSelGroup', newSelGroup, isMulti);
            } else {
                if (_.isNumber(inSelGroupIndex)) {
                    const newSelGroup = this.selectGroup;
                    newSelGroup.splice(inSelGroupIndex, 1);
                    let isMulti = true;
                    if (newSelGroup.length === 1) {
                        isMulti = false;
                    }
                    // 更新selectGroup
                    this.$root.busRef.$emit('updateSelGroup', newSelGroup, isMulti);
                }
            }
        },
        mapSelector( currentPart: Model.Part ): void {
            const newSelPart: Model.Part  = this.layerTree.find( (part: Model.Part) => {
                return part.partId === currentPart.partId;
            });
            const newSelGroup: Model.Part[] = [];
            if (newSelPart) {
                newSelGroup.push(newSelPart);
            }
            const isMulti = false;
            
            // 更新selectGroup
            this.$root.busRef.$emit('updateSelGroup', newSelGroup, isMulti);
        },
        /**
         * 更新列表中选中的项
         */
        updateSelected( partIdArr: string[] ): void {
            const setSelected = (index: number, isSelected: boolean): void => {
                this.layerTree[index].isSelected = isSelected;
                this.$set(this.layerTree, index, this.layerTree[index]);
            };
            this.layerTree.forEach( (part: Model.Part, index: number) => {
                if (partIdArr.includes(part.partId)) {
                    setSelected(index, true);
                } else {
                    setSelected(index, false);
                }
            });
        }
    },
    created() {
        this.mapLayerTree();
    }
};
