interface LockGroup {
    isLocked: boolean;
    part: any;
}

const mutations = {
    setIsLocked(state: Screen.state, isLocked: string) {
        state.editor.lockPart.setIsLocked(isLocked);
    },
    mapLockGroup(state: Screen.state, part: any) {
        state.editor.lockPart.mapLockGroup(part);
    },
    lock_group(state: Screen.state, payLoad: LockGroup) {
        const { isLocked, part } = payLoad;
        state.editor.lockPart.setIsLocked(isLocked);
        state.editor.lockPart.mapLockGroup(part);
    },
    unlock_group(state: Screen.state, isLocked: boolean) {
        state.editor.lockPart.setIsLocked(isLocked);
        state.editor.lockPart.clearLockGroup();
    },
    clearLockGroup(state: Screen.state) {
        state.editor.lockPart.clearLockGroup();
    },
};

const getter = {
    lockPart: (state: Screen.state) => state.editor && state.editor.lockPart,
};

export default {
    mutations,
    getter,
};
