import AbstractOption from '../../../common/abstractOption'
import updatePropsDeep from 'utils/initProps'

/**
 * 图表图例组件legend配置项
 */
export default class Legend extends AbstractOption {

  public config: any;

  constructor(opts?: Chart.Legend) {
    super();
    this.config = this.init(opts);
  }

  public init(opts?: Chart.Legend) {
    const source: Chart.Legend = {
      $position: 'bottom center',
      show: true,
      type: 'scroll',
      orient: 'horizontal',
      align: 'auto',
      padding: 0,
      itemGap: 10,
      itemWidth: 12,
      itemHeight: 12,
      formatter: null,
      selectedMode: true,
      icon: 'circle',
      inactiveColor: '#ccc',
      textStyle: {
        fontSize: 12,
        fontWeight: 'normal',
        color: '#333',
        padding: 0,
        backgroundColor: 'transparent',
        borderColor: 'transparent',
        borderWidth: 0,
        borderRadius: 0,
      },
      backgroundColor: 'transparent',
      borderColor: '#ccc',
      borderWidth: 0,
      borderRadius: 0,
      data: null,
      selected: {}
    };

    const props: any = updatePropsDeep(source, opts);
    const position = this.computed(props.$position);

    return { ...props, ...position };
  }

  public computed(value: any): object {
    const map: any = {
      'top center': {
        top: 'auto',
        bottom: 'auto',
        left: 'center'
      },
      'top left': {
        top: 'auto',
        bottom: 'auto',
        left: 'left'
      },
      'top right': {
        top: 'auto',
        bottom: 'auto',
        left: 'right'
      },
      'bottom center': {
        top: 'auto',
        bottom: '10',
        left: 'center'
      },
      'bottom left': {
        top: 'auto',
        bottom: '10',
        left: 'left'
      },
      'bottom right': {
        top: 'auto',
        bottom: '10',
        left: 'right'
      }
    };
    return map[value];
  }

}