import textStyle from '../config/textStyle';
import grid from '../config/grid';
import title from '../config/title';
import tooltip from '../config/tooltip';
import legend from '../config/legend';
import xAxis from '../config/xAxis';
import yAxis from '../config/yAxis';
import zAxis from '../config/zAxis';
import seriesLine from '../config/seriesLine';
import seriesBar from '../config/seriesBar';
// 基础配置项适配器
import OptionAdapter from '../../../common/adapter/baseOptionAdapter';

/**
 * 基本线图、柱图配置项适配器
 */
export default class LineAndBarAdapter extends OptionAdapter {

  constructor() {
    super();
  }

  public getDefaultOption(type: string): object {
    const map: any = {
      baseLine: {
        textStyle,
        grid,
        title,
        tooltip,
        legend,
        xAxis,
        yAxis,
        series: seriesLine
      },
      dblLine: {
        textStyle,
        grid,
        title,
        tooltip,
        legend,
        xAxis,
        yAxis,
        zAxis,
        series: seriesLine
      },
      baseBar: {
        textStyle,
        grid,
        title,
        tooltip,
        legend,
        xAxis,
        yAxis,
        series: seriesBar
      },
      baseLineBar: {
        textStyle,
        grid,
        title,
        tooltip,
        legend,
        xAxis,
        yAxis,
        zAxis,
        series: seriesBar
      }
    };
    return map[type] || {};
  }

  /**
   * 双y轴属性计算
   * @param chartOpts 属性配置界面对应的图表配置项
   * @param update 更新属性状态
   * @returns 图表的setOption配置项
   */
  public computedYAxis(chartOpts: any, update?: boolean): any {
    const _chartOpts = _.cloneDeep(chartOpts)
    const yAxis: any = []

    // 无第二y轴属性(zAxis)，强制抛出异常
    if (!_chartOpts.zAxis) {
      throw new Error('未检测到第二y轴zAxis初始化属性，双y轴图表必须初始化此关键属性!');
    }

    // 更新属性
    if (_.isBoolean(update) && update) {
      yAxis.push(_chartOpts.yAxis, _chartOpts.zAxis)
      return { yAxis }
    }

    // 初始化属性
    for (let [key, value] of Object.entries(_chartOpts)) {
      key === 'yAxis' ? (yAxis[0] = value) : void (0)
      key === 'zAxis' ? (yAxis[1] = value) : void (0)
    }
    delete _chartOpts.yAxis
    delete _chartOpts.zAxis
    _chartOpts.yAxis = yAxis
    return _chartOpts
  }

}