import { PartObjectDto } from "./partDto";

export class PartProjectPagesDto {
    pagesId: string;
    level: number;
    parentId: string;
    pagesName: string;
    backgroundColor: string;
    backgroundImage: string;
    partObjectList: Array<PartObjectDto>;

    public toPartProjectPage(page: Model.Page): PartProjectPagesDto {
        const projectPage = new PartProjectPagesDto();
        projectPage.pagesId = page.pagesId;
        projectPage.pagesName = page.pagesName;
        projectPage.backgroundColor = page.backgroundColor;
        projectPage.backgroundImage = page.backgroundImage;
        projectPage.partObjectList = page.partContainer.map(part => {
            return PartObjectDto.toPartObjectDto(part);
        });
        return projectPage;
    }
}

export class PartProjectDto {
    proId: number;
    proName: string;
    grid: number;
    gridShow: boolean;
    panelScale: number;
    screenH: number;
    screenW: number;
    userId: string;
    organType: number;
    organizationId: string;
    saveType: number;
    pagesList: Array<PartProjectPagesDto>;

    public static toProjectDto(
        project: Model.Project,
        pages: Model.Page[]
    ): PartProjectDto {
        const projectDto = new PartProjectDto();
        projectDto.proId = project.proId;
        projectDto.proName = project.proName;
        projectDto.grid = project.grid;
        projectDto.gridShow = project.gridShow;
        projectDto.screenH = project.screenH;
        projectDto.screenW = project.screenW;
        if (pages.length == 1) {
            projectDto.saveType = 1;
        } else if (pages.length > 1) {
            projectDto.saveType = 2;
        }
        projectDto.pagesList = pages.map(page => {
            return new PartProjectPagesDto().toPartProjectPage(page);
        });
        return projectDto;
    }

    public setProject(project: Model.Project) {
        this.proId = project.proId;
        this.proName = project.proName;
        this.grid = project.grid;
        this.gridShow = project.gridShow;
        this.screenH = project.screenH;
        this.screenW = project.screenW;
        return this;
    }
}
