
import Vue from 'vue';
import { mapMutations, mapState } from 'vuex';
export default Vue.extend({
    name: 'KeyBoard',
    data() {
        return {
            // 该数组的事件能被发射
            canEmitter: [],
            baseEmitter: ['save', 'zoomIn', 'zoomOut'],   
        };
    },
    methods: {
        ...mapMutations([
            'bindKeyCodeObject',
            'bindAllKeyCodeObject',
        ]),
        keyPress( keyCodeName: string): void {
            const kcHandleCfg = [
                {
                    kcName: ['cut', 'copy', 'delete', 'paste', 'pasteMouse'],
                    kcHandle: this.clipBoard
                },
                {
                    kcName: ['locking', 'unlock'],
                    kcHandle: this.lock
                },
                {
                    kcName: ['breakup', 'makeup'],
                    kcHandle: this.group
                },
                {
                    kcName: ['redo', 'undo'],
                    kcHandle: this.history
                },
                {
                    kcName: ['upper', 'lower', 'top', 'bottom' ],
                    kcHandle: this.layer
                },
            ];
            kcHandleCfg.some( (keyCodeCfg: any)  => {
                if (keyCodeCfg.kcName.includes(keyCodeName)) {
                    keyCodeCfg.kcHandle(keyCodeName);
                    return true;
                }
            });
            
        },
        /**
         * 初始化keycode的广播事件
         */
        initKeyBoardEmitter() {
            this.bindAllKeyCodeObject(this.handleKeyCodeCallback);
        },
        /**
         * 处理keyCode绑定后的回调
         */
        handleKeyCodeCallback(event: MouseEvent, eventName: string) {
            // 如果事件的目标不是body不阻止默认事件
            if (document.body !== event.target) {
                return;
            }
            // 阻止浏览器的默认事件
            if (event.preventDefault) {
                event.preventDefault();
            } else {
                // internet explorer
                event.returnValue = false;
            }
            // console.log(eventName);
            if (!(this.canEmitter.includes(eventName) || this.baseEmitter.includes(eventName))) {
                return;
            }
            this.$root.busRef.$emit(eventName, eventName);
        },
        onMenuChange() {
            this.$root.busRef.$on('enableWatch', (message) => {
                if (_.isNil(message)) {
                    return;
                }
                if (!Array.isArray(message)) {
                    message = [message];
                }
                const copyMsg = _.cloneDeep(message);
                copyMsg.forEach((emit: any) => {
                    if (emit.enable) {
                        if (!this.canEmitter.includes(emit.child)) {
                            this.canEmitter.push(emit.child);
                        }
                    } else {
                        if (this.canEmitter.includes(emit.child)) {
                            _.remove(this.canEmitter, (it) => {
                                return it === emit.child;
                            });
                        }
                    }
                });
            });
        },
    },
    mounted() {
        this.initKeyBoardEmitter();
        this.onMenuChange();
    },
    beforeDestroy() {
        this.$root.busRef.$off('enableWatch')
    },
    render(h) {
        return h();
    },
});
