
import Vue from "vue";
import { mapMutations, mapState, mapGetters } from 'vuex';
import _ from 'lodash';
export default Vue.extend({
    name: 'ClipBoard',
    data() {
        return {
            // 记录鼠标在page上移动的位置坐标
            mousePos: {
                x: 0,
                y: 0,
            },
        };
    },
    computed: {
        ...mapState({
            basePartList: (state: any) => state.screen.pageManage.partContainer,
            partSelection: (state: any) => state.screen.editor.partSelection,
        }),
        ...mapGetters([
            'clipBoard',
        ]),
        projectObj(): any {
            return this.$store.getters.projectObj;
        },
        panelScale(): number { 
            return this.projectObj.panelScale;
        },
        selectGroup() {
            return this.partSelection.selectGroup;
        },
        selectPart() {
            return this.partSelection.selectPart;
        },
        selectType(): string {
            return this.partSelection.type;
        },
        calScale(): number {
            return this.panelScale / 100;
        },
    },
    methods: {
        ...mapMutations([
            'add_part',
            'remove_part',

            'updateClipBoard',
            'clearClipBoard',

            'clearPartSeletion',
            'updatePartSeletion',

            'updateTransform',
            'setSelectedBox',

            'cut_his',
            'delete_his',
            'paste_his',
        ]),
        // 拷贝
        copyPart(): void {
            if (this.selectGroup.length === 0) {
                return;
            }
            this.clonePart();
        },
        // 剪切
        cutPart(): void { 
            if (!this.selectGroup.length) {
                return;
            }
            this.clonePart();

            // 删除当前的元素
            this.remove_part(this.selectGroup);

            // 更新框线选择
            this.clearPartSeletion();
            this.setSelectedBox(false);

            // 历史记录
            this.cut_his();
        },
        // 粘贴
        pastePart(evtName: string): void {
            let pasteMousePos: any;
            if (evtName === 'pasteMouse') {
                // 取得相对元素的位置
                const domOffSet = $('#canvas').offset();
                const x = (this.mousePos.x - domOffSet.left) / this.calScale;
                const y = (this.mousePos.y - domOffSet.top) / this.calScale;
                pasteMousePos = {
                    y,
                    x,
                };
            }
            // 如果粘贴板为空，则不再执行此操作
            const clipBoardGroup = this.clipBoard.partGroupTemp;
            
            const rectCoord = _.cloneDeep(this.clipBoard.rectCoord);
            if (!clipBoardGroup.length) {
                return;
            }
            
            // 缓存多选的part
            const selPartGroup: Model.Part[] = [];
            // 缓存新建part的参数
            const newPartArr: Model.Part[] = [];
            // 更新model的part,更新editor的partSelection
            const updatePartList = () => {
                this.add_part(newPartArr);
                // 获取新增的所有part
                const newPartLen = newPartArr.length;
                const startIndex = this.basePartList.length - newPartLen;
                if (startIndex >= 0 ) {
                    const newPartListRef: Model.Part[] = this.basePartList.slice(startIndex);
                    selPartGroup.push(...newPartListRef);
                }
            };

            // 判断是否为粘贴到鼠标位置，如果mousePos不为空，代表粘贴到鼠标位置
            if (pasteMousePos) {
                const {
                    x,
                    y,
                } = pasteMousePos;
                let combX: number = 0;
                let combY: number = 0;
                
                // 多选和单选
                if (clipBoardGroup.length > 1) {
                    clipBoardGroup.map((part: any) => {
                        const copyPart = _.cloneDeep(part);
                        const partStyle = copyPart.partStyle;
                        const diff = {
                            x: 0,
                            y: 0,
                        };

                        // 重新为每个part计算坐标
                        diff.x = partStyle.x - rectCoord.x;
                        diff.y = partStyle.y - rectCoord.y;
                        combX = diff.x + x;
                        combY = diff.y + y;
                        partStyle.x = combX;
                        partStyle.y = combY;
                        newPartArr.push(copyPart);
                    });
                } else {
                    clipBoardGroup.map((part: any) => {
                        const copyPart = _.cloneDeep(part);
                        copyPart.partStyle.x = x;
                        copyPart.partStyle.y = y;
                        newPartArr.push(copyPart);
                    });
                }
                updatePartList();
                // 修改rubberBand的坐标为鼠标的坐标
                rectCoord.x = x;
                rectCoord.y = y;
            } else {
                // 粘贴到原来复制时的位置
                clipBoardGroup.map((part: any) => {
                    newPartArr.push(_.cloneDeep(part));
                });
                updatePartList();
            }

             // 更新框线选择
            let type: string = '';
            if (selPartGroup.length > 1) {
                type = 'multiply';
            } else {
                type = 'single';
            }
            const partSelObj = {
                selectGroup: selPartGroup,
                selectPart: rectCoord,
                type,
            };
            // 这里只需要传type即可映射transform的属性
            this.updateTransform({ type });
            this.updatePartSeletion(partSelObj);
            this.paste_his();
        },
        deletePart(): void {
            if (Array.isArray(this.selectGroup) && !this.selectGroup.length) {
                // this.$message.warning('未选中元素');
                return;
            }
            this.remove_part(this.selectGroup);
            // 更新框线选择
            this.setSelectedBox(false);
            this.clearPartSeletion();

            // 历史记录
            this.delete_his();
        },
        clonePart() {
            const clipBoardArr: any[] = [];
            
            this.selectGroup.map((eachPart: Model.Part) => {
                // get part pure data 
                const copyPart = eachPart.getPartAllProps();
                copyPart.partId = '';
                clipBoardArr.push(copyPart);
            });

            // 更新clipBoard
            const clipBoardArg = {
                partGroup: clipBoardArr,
                rectCoord: _.cloneDeep(this.selectPart)
            };
            this.updateClipBoard(clipBoardArg);
        },
        /**
         * 初始化监听事件接收器
         */
        editorEvtReceiver(isDestoyed?: boolean) {
            const eventObjCfg = [
                {
                    name: 'cut',
                    handle: this.cutPart,
                },
                {
                    name: 'copy',
                    handle: this.copyPart,
                },
                {
                    name: 'paste',
                    handle: this.pastePart,
                },
                {
                    name: 'pasteMouse',
                    handle: this.pastePart,
                },
                {
                    name: 'delete',
                    handle: this.deletePart,
                },
            ];
            // 存在两个以上子组件引用，需要改善
            for (const eventObj of eventObjCfg) {
                if (isDestoyed) {
                    this.$root.busRef.$off(eventObj.name);
                } else {
                    this.$root.busRef.$on(eventObj.name, (eventType: string) => {
                        eventObj.handle(eventType);
                    });
                }
            }
        },
        showMousePosition(event: MouseEvent) {
            this.mousePos.x = event.pageX;
            this.mousePos.y = event.pageY;
        },
        initMouseListener() {
            document.addEventListener ('mousemove', this.showMousePosition, false);
        },
        destoyedMouseListener() {
            document.removeEventListener ('mousemove', this.showMousePosition, false);
        },
    },
    mounted() {
        this.editorEvtReceiver();
        this.initMouseListener();
    },
    beforeDestroy() {
        this.destoyedMouseListener();
        this.editorEvtReceiver(true);
    },
    render(h) {
        return h();
    }
});
