	props: {
		cmptList: {
			type: Array,
			default: () => {
				return [];
			}
		}
	},
    methods: {
        dragstart(event, dragItem) {
            const partInfo = dragItem.partSource;
            event.dataTransfer.setData("Text", JSON.stringify(partInfo));
            event.dataTransfer.dropEffect = "copy";
        },
    },
