import { MDto } from "./dto/baseDto";
import { PartProjectDto } from "./dto/projectDto";
import { UserPartClassDto, PartClassDto } from "./dto/partDto";
import { PartStorage } from "./repository/partRepository";

interface PageDetail {
    groups: Model.Group[];
    parts: Model.Part[];
}

/**
 * 组件服务
 */
export class PartService {
    /**
     * 保存工程详情数据
     * @param pr
     */
    static saveProjectDetail(pr: Model.ProjectPureData): Promise<MDto<any>> {
        const projectDto = PartProjectDto.toProjectDto(
            pr.projectPureData,
            pr.pagePureData
        );
        return PartStorage.saveProjectDetail(projectDto);
    }

    /**
     * 保存工程
     * @param p
     */
    static saveProject(p: Model.Project): Promise<MDto<any>> {
        const projectDto = new PartProjectDto().setProject(p);
        return PartStorage.saveProject(projectDto);
    }

    /**
     * 保存用户组件
     * @param c
     */
    static saveCompt(c: Model.CmptGroup): Promise<MDto<any>> {
        const userPart = new UserPartClassDto().setPartClass(c);
        return PartStorage.savePartClass(userPart);
    }

    /**
     * 获取用户组件
     * @param userId
     */
    static getCompt(userId: string): Promise<MDto<Model.CmptGroup[]>> {
        return PartStorage.getPartClass(userId).then(r => {
            if (r.data && !_.isEmpty(r.data)) {
                const list = r.data as any[];
                const rst: Model.CmptGroup[] = list.map(m => {
                    const pList = m.partClassList as any[];
                    const cmpt = {
                        cmptGroupId: m.categoryId,
                        groupName: m.categoryName,
                        cmptGroup: pList.map(p => {
                            const g = {
                                imgSrc: p.imgSrc,
                                cmptId: p.partClassId,
                                cmptName: p.partClassName,
                                partSource: p.partSource
                            } as Model.Cmpt;
                            return g;
                        })
                    } as Model.CmptGroup;
                    return cmpt;
                });
                return new MDto(0, rst, "");
            } else {
                return new MDto(-1, null, "");
            }
        });
    }
    /**
     * 获取工程
     * @param organType
     * @param organId
     */
    static getProjects(
        organType: number,
        organId: string
    ): Promise<MDto<Model.Project[]>> {
        return PartStorage.getProjects(organType, organId).then(r => {
            if (r.data && !_.isEmpty(r.data)) {
                const arr = r.data as any[];
                if (arr && arr.length > 0) {
                    const list: Model.Project[] = arr.map(p => {
                        const project = {
                            proId: p.proId,
                            proName: p.proName,
                            screenH: p.screenH,
                            screenW: p.screenW,
                            grid: p.grid,
                            gridShow: p.gridShow
                        } as Model.Project;
                        return project;
                    });
                    return new MDto(0, list, "");
                }
            }
            return new MDto(-1, null, "");
        });
    }
    /**
     * 获取页面
     * @param proId
     */
    static getPages(proId: number): Promise<MDto<Model.Page[]>> {
        return PartStorage.getPages(proId).then(r => {
            if (r.data && !_.isEmpty(r.data)) {
                const arr = r.data as any[];
                if (arr && arr.length > 0) {
                    const list: Model.Page[] = arr.map(p => {
                        return {
                            pagesId: p.pagesId,
                            pagesName: p.pagesName,
                            backgroundColor: p.backgroundColor,
                            backgroundImage: p.backgroundImage
                        } as Model.Page;
                    });
                    return new MDto(0, list, "");
                }
            }
            return new MDto(-1, null, "");
        });
    }

    static createPart(p: any): Model.Part {
        const dsList: any[] = p.dsList as any[];
        const ds = {
            sourceType: p.appDsType,
            useScript: p.useScript,
            scriptLog: p.filterScript
        };
        dsList.forEach(d => {
            const obj = {
                sourceType: d.dsId,
                sourceTypeId: d.dsConfigType
            };
            switch (d.dsConfigType) {
                case 0:
                    Object.assign(ds, {
                        staticData: Object.assign(obj, {
                            sourceData: d.dsConfig
                        })
                    });
                case 1:
                    Object.assign(ds, {
                        staticData: Object.assign(obj, {
                            pointData: JSON.parse(d.dsConfig)
                        })
                    });
                case 2:
            }
        });
        return {
            partName: p.partName,
            partId: p.partId,
            partType: p.partType,
            sourceType: p.sourceType,
            type: p.type,
            childType: p.childType,
            layer: p.layer,
            isLocked: p.isLocked,
            partStyle: JSON.parse(p.partStyle),
            shapeStyle: JSON.parse(p.shapeStyle),
            partData: ds
        } as Model.Part;
    }

    /**
     * 获取页面详情
     * @param pagesId
     */
    static getPageDetail(pagesId: string): Promise<MDto<PageDetail>> {
        return PartStorage.getPageDetail(pagesId).then(r => {
            if (r.data && !_.isEmpty(r.data)) {
                const groups = r.data.groups as any[];
                const parts = r.data.parts as any[];
                const gList: Model.Group[] = groups.map(g => {
                    const parts = g.parts as any[];
                    return {
                        groupId: g.groupId,
                        partGroup: parts.map(p => {
                            return PartService.createPart(p);
                        })
                    } as Model.Group;
                });
                const pList = parts.map(p => {
                    return PartService.createPart(p);
                });
                const detail = {
                    groups: gList,
                    parts: pList
                };
                return new MDto(0, detail, "");
            }
            return new MDto(-1, null, "");
        });
    }
}
