






import Vue from 'vue'
export default Vue.extend({
    computed: {
        disClick() {
            return this.ctmItem.disable;
        }
    },
    props: {
        ctmItem: {
            type: Object,
            default: () =>{
                return {}
            }
        }
    },
    methods: {
        itemClickDown(event) {
            event.stopPropagation();
            let showCtm = false;
            if(this.disClick) {
                showCtm = true;
            }
            this.$emit('itemClickDown', this.ctmItem, showCtm, event);
        }
    }
})
