








import Vue from 'vue';
import { mapGetters } from 'vuex';
export default Vue.extend({
    props: {
        gridStyle: {
            type: Object,
            default: () => {
                return {
                    height: 1080,
                    width: 1920,
                };
            },
        },
        panelScale: {
            type: Number,
            default: 50,
        },
    },
    computed: {
        ...mapGetters([
            'projectObj',
            'currentPage',
        ]),
        // 是否显示网格
        gridShow() {
            return this.projectObj.gridShow;
        },
        // 显示页面的背景色
        backgroundColor(): string {
            let bgColor: string = 'none';
            if (this.currentPage) {
                bgColor = this.currentPage.backgroundColor;
            }
            return bgColor;
        },
        // 显示页面的背景色
        backgroundImage(): string {
            let bgImg: string = 'none';
            if (this.currentPage) {
                bgImg = this.currentPage.backgroundImage;
            }
            return bgImg;
        },
        backgroundObj(): any {
            return {
                backgroundImage: `url("${this.backgroundImage}")`,
                backgroundColor: this.backgroundColor,
                width: this.gridStyle.width,
                height: this.gridStyle.height
            };
        },
        backgroundImg() {
            let img = `url("data:image/svg+xml;base64,")`;
            if (!this.gridShow) {
                img = 'none';
            }
            return {
                backgroundImage: img,
            };
        },
    },
