import AbstractOption from '../../../common/abstractOption'

export default class Background extends AbstractOption {

  public config: any;

  constructor(shapeStyle?: Shape.Background) {
    super();
    this.config = this.init(shapeStyle);
  }

  public init(shapeStyle?: Shape.Background): object {
    const {
      backgroundColor = '#ccc'
    } = shapeStyle || {};

    return {
      backgroundColor
    };
  }

  public computed(): void { }
  
}