/**
 * part类的公共样式类
 */
export default class PartComStyle {

    [index: string]: any;
    /**
     * 图形元素的x坐标
     */
    public x: number;

    /**
     * 图形元素的y坐标
     */
    public y: number;

    /**
     * 图形元素的宽度
     */
    public width: number;

    /**
     * 图形元素的高度
     */
    public height: number;

    /**
     * 图形元素的z坐标
     */
    public zIndex: number;

    /**
     * 图形元素的y坐标
     * fixme应该被去除
     */
    public position: string = 'absolute';

    /**
     * 图形元素的角度
     */
    public angle: number;

    /**
     * 图形元素的背景颜色
     */
    public backgroundColor: string;

    /**
     * 图形元素的透明度
     * 1~100
     */
    public opacity: number;

    constructor(styleArg: Model.PartStyle) {
        this.setPartAllProps(styleArg);
    }

    public getPartAllProps(): Model.PartStyle {
        return {
            x: this.x,
            y: this.y,
            width: this.width,
            height: this.height,
            zIndex: this.zIndex,
            angle: this.angle,
            backgroundColor: this.backgroundColor,
            opacity: this.opacity,
        };
    }

    public setPartAllProps(styleArg: Model.PartStyle) {
        if (!_.isObject(styleArg)) {
            return;
        }
        const {
            x,
            y,
            width,
            height,
            zIndex,
            angle,
            backgroundColor,
            opacity,
        } = styleArg;

        this.x = x || 0;
        this.y = y || 0;
        this.width = width || 100;
        this.height = height || 100;
        this.zIndex = zIndex || 1;
        this.angle = angle || 0;
        this.backgroundColor = backgroundColor || '';
        this.opacity = opacity || 20;
    }
}
