import { DataSourceDto } from "./dataSourceDto";
import { AppDsType } from "./baseDto";

export class PartObjectDto {
    isGroup: number;
    /**图层*/
    layer: number;
    /**是否锁定*/
    isLocked: boolean;
    /**样式*/
    partStyle: string;
    /**扩展组件额外样式*/
    shapeStyle: string;
    optionAttr: string;
    /**组件类名*/
    className: string;
    pagesId: string;
    userId: string;
    partId: string;
    partName: string;
    partType: string;
    sourceType: string;
    type: string;
    childType: string;
    filterScript: string;
    useScript: boolean;
    appDsType: number;
    dsList: Array<DataSourceDto>;
    groupId: string;
    groupList: Array<PartObjectDto>;

    public static createPartObject(part: Model.Part): PartObjectDto {
        const partObjectDto = new PartObjectDto();
        partObjectDto.partId = part.partId;
        partObjectDto.layer = part.layer;
        partObjectDto.partStyle = JSON.stringify(part.partStyle);
        partObjectDto.shapeStyle = JSON.stringify(part.shapeStyle);
        partObjectDto.partId = part.partId;
        partObjectDto.partName = part.partName;
        partObjectDto.partType = part.partType;
        partObjectDto.sourceType = part.sourceType;
        partObjectDto.type = part.type;
        partObjectDto.childType = part.childType;
        return partObjectDto;
    }

    public static toPartObjectDto(part: Model.Part): PartObjectDto {
        const partObjectDto = this.createPartObject(part);
        switch (part.partType) {
            case "group":
                const group: Model.Group = part as Model.Group;
                partObjectDto.isGroup = 1;
                partObjectDto.groupId = group.groupId;
                partObjectDto.groupList = group.partGroup.map(m => {
                    return this.createPartObject(m);
                });
            case "element":
                const ele: Model.Element = part as Model.Element;
                partObjectDto.isGroup = 0;
                if (ele.partData && !_.isEmpty(ele.partData)) {
                    const partData = ele.partData;
                    partObjectDto.useScript = partData.useScript;
                    partObjectDto.filterScript = partData.scriptLog;
                    const dsArr = [partData.staticData, partData.pointData];
                    switch (partData.sourceType) {
                        case "staticData":
                            partObjectDto.appDsType = AppDsType.staticData;
                            partObjectDto.dsList = dsArr.map(f => {
                                const ds = new DataSourceDto();
                                ds.dsConfigType = AppDsType.staticData;
                                ds.dsId = f.sourceTypeId;
                                ds.dsConfig = JSON.stringify(f);
                                return ds;
                            });
                        case "pointData":
                            partObjectDto.appDsType = AppDsType.pointData;
                            partObjectDto.dsList = dsArr.map(f => {
                                const ds = new DataSourceDto();
                                ds.dsConfigType = AppDsType.pointData;
                                ds.dsId = f.sourceTypeId;
                                ds.dsConfig = JSON.stringify(f);
                                return ds;
                            });
                        case "apiData":
                            partObjectDto.appDsType = AppDsType.apiData;
                            partObjectDto.dsList = dsArr.map(f => {
                                const ds = new DataSourceDto();
                                ds.dsConfigType = AppDsType.apiData;
                                ds.dsId = f.sourceTypeId;
                                ds.dsConfig = JSON.stringify(f);
                                return ds;
                            });
                    }
                }
        }
        return partObjectDto;
    }
}

export class PartClassDto {
    partClassId: string;
    /**
     * 1)0独立存在
     * 2)1属于Group
     * 3)2属于母版
     */
    ofCommunity: number;
    imgSrc: string;
    partClassName: string;
    partSource: string;
}

export class UserPartClassDto {
    userId: string;
    categoryId: string;
    categoryName: string;
    /**单个还是多个*/
    saveType: number;
    list: Array<PartClassDto>;

    setPartClass(cmpt: Model.CmptGroup) {
        this.categoryId = cmpt.cmptGroupId;
        this.categoryName = cmpt.groupName;
        const list = cmpt.cmptGroup;
        if (list && list.length == 1) {
            this.saveType == 1;
        } else if (list && list.length > 1) {
            this.saveType == 2;
        }
        this.list = list.map(m => {
            const partclass = new PartClassDto();
            partclass.partClassId = m.cmptId;
            partclass.partClassName = m.cmptName;
            partclass.imgSrc = m.imgSrc;
            partclass.ofCommunity = 0;
            partclass.partSource = JSON.stringify(m.partSource);
            return partclass;
        });
        return this;
    }
}
