






























































import Vue from 'vue';
import { mapGetters, mapMutations } from 'vuex';
import CmptLib from './cmptLib.vue';
export default Vue.extend({
    name: 'CmptGroup',
    data() {
        return {
            activeName: 0,
            newCmptGroup: false,
            newCmptGroupName: '',
            fixCmptGroup: false,
            fixCmptGroupName: '',
            searchCmpt: '',
            // 记录右击中保存cmptGroupItem的引用
            cmptGroupItem: {},
        };
    },
    // directives: {
    //     focus: {
    //         // 指令的定义
    //         bind:  (el, binds, vnode) => {
    //             console.log(vnode);
    //             console.log("wogengxinle ....");
    //             vnode.focus();
    //         },
    //     },
    // },
    components: {
        CmptLib,
    },
    computed: {
        ...mapGetters([
            'cmptManage',
        ]),
        cmptGroupList(): any[] {
            return this.cmptManage.cmptGroupList;
        },
        cmptGroupListProxy(): any[] {
            return this.cmptGroupList;
        },
    },
    methods: {
        ...mapMutations([
            'add_cmpt_group',
            'remove_cmpt_group',
            'edit_cmpt_group',
        ]),
        addCmptGroup( name: string ) {
            if (name) {
                const cmptGroupArg = {
                    isEditor: true,
                    groupName: name,
                };
                this.fixCmptGroupName = name;
                this.add_cmpt_group(cmptGroupArg);

                const lastCmptGroup = this.cmptGroupListProxy[this.cmptGroupListProxy.length - 1];
                // 获取input聚焦
                this.$nextTick(() => {
                    (this.$refs[lastCmptGroup.cmptGroupId]as any)[0] .focus();
                });
                return;
            }
            if (!this.newCmptGroupName) {
                this.$message.error('请输入组件类型');
                return;
            }
            this.add_cmpt_group(this.newCmptGroupName);
            this.handerCmptGroup();
        },
        handerCmptGroup(): void {
            this.newCmptGroup = !this.newCmptGroup;
        },
        cmptGroupMouseOver( item ) {
            item.isHover = true;
        },
        cmptGroupMouseOut( item ) {
            item.isHover = false;
        },
        /**
         * 切换默认组件块到修改组件
         */
        handleCmptGroup(cmptGroup) {
            this.fixCmptGroupName = cmptGroup.groupName;
            const cmptGroupArg = {
                isEditor: true,
                cmptGroup,
            };
            this.edit_cmpt_group(cmptGroupArg);
            // 获取input聚焦
            this.$nextTick(() => {
                (this.$refs[cmptGroup.cmptGroupId]as any)[0].focus();
            });
        },
        /**
         * 切换修改组件块到默认
         */
        handleFixCmptGroup(cmptGroup) {
            const cmptGroupArg = {
                isEditor: false,
                cmptGroup,
            };
            this.edit_cmpt_group(cmptGroupArg);
        },
        /**
         * 修改组件块
         */
        editCmptGroup(cmptGroup) {
            if (!this.fixCmptGroupName) {
                this.$message.error('组件类型不能为空');
                return;
            }
            const cmptGroupArg = {
                groupName: this.fixCmptGroupName,
                cmptGroup,
            };
            this.edit_cmpt_group(cmptGroupArg);
            this.handleFixCmptGroup(cmptGroup);
        },
        /**
         * 删除组件块
         */
        delCmptGroup(cmptGroup) {
            this.remove_cmpt_group(cmptGroup);
        },
        handleColClick(item) {
            item.isHover = !item.isHover;
        },
        handleClickOutSide(item) {
            if (!_.isNil(item)) {
                // 输入框为空
                if (!this.fixCmptGroupName) {
                    this.fixCmptGroupName = item.groupName;
                }
                this.editCmptGroup(item);
            }
        },
        cmptGroupCtm(cmptGroupItem) {
            this.cmptGroupItem = cmptGroupItem;
        },
        editCmptGroupCtm() {
            if (this.cmptGroupItem) {
                this.handleCmptGroup(this.cmptGroupItem);
            }
        },
        removeCmptGroupCtm() {
            if (this.cmptGroupItem) {
                this.delCmptGroup(this.cmptGroupItem);
            }
        }
    },
});
