import UUID from '../../utils/uuid';
import { AbstractPages } from '../abstract/abstractPages';

/**
 * page对象模型类
 */
export default class Pages extends AbstractPages {

    public statesContainer: any[] = [];

    public partContainer: Model.Part[] = [];

    public pagesId: string;
    
    // 背景色
    public pagesName: string;

    // 背景色
    public backgroundColor: string;

    // 背景图片
    public backgroundImage: string;

    public isHover: boolean;

    private level: number;

    private parentId: string;

    public init(pages?: Model.Page): void {
        const { 
            pagesId = '',
            pagesName = '', 
            backgroundColor = '', 
            backgroundImage = '',
            partContainer = [],
            isHover = false,
        } = pages || {};
        this.pagesId = pagesId || this.createdPagesId();
        this.pagesName = pagesName || '页面';
        this.backgroundColor = backgroundColor || '';
        this.backgroundImage = backgroundImage || '';
        if (partContainer.length) {
            this.partContainer = partContainer;
        }
        this.isHover = isHover;
    }

    /**
     * 获取页面的属性
     * 
     * @returns {Model.PageNode} 
     */
    public getProperties(): Model.PageNode {
        return {
            pagesId: this.pagesId,
            pagesName: this.pagesName,
            level: this.level,
            parentId: this.parentId,
            backgroundColor: this.backgroundColor,
            backgroundImage: this.backgroundImage,
            children: [],
            isHover: this.isHover,
        };
    }

    public setBackgroudColor(color: string ): void {
        this.backgroundColor = color;
    }

    public setBackgroudImage(img: string): void {
        this.backgroundImage = img;
    }

    public setPageName(name): void {
        this.pagesName = name;
    }

    public setLevel(level): void {
        this.level = level;
    }

    public setParentId(parentId): void {
        this.parentId = parentId;
    }


    public destroyed(): void {
        return;
    }

    private createdPagesId(): string {
        return 'page_' + new UUID().init();
    }

}
