







import Vue from 'vue'

export default Vue.extend({
  name: 'inputNumber-el',
  props: {
    opts: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  data() {
    return {
      initParam: {
        formOption: {
          step: 1,
          max: 100,
          min: 0
        }
      }
    }
  },
  computed: {
    inputValue: {
      get(): number {
        return this.opts.model || 0
      },
      set(val: number): number {
        return val
      }
    },
    param(): any {
      return Object.assign(this.initParam, this.opts)
    },
    max(): number {
      return this.param.formOption.max
    },
    min(): number {
      return this.param.formOption.min
    },
    step(): number {
      return this.param.formOption.step
    }
  },
  methods: {
    handleChange(val: number) {
      this.$emit('on-data-change', this.param, val)
    }
  }
})
