declare const _: any;
import EventObject from '../base/eventObject';
const eventTemp = {
    isNative: false,
    eventName: '',
    eventType: '',
    enable: true,
    keyCode: '',
    active: true,
}
export default class PartEvent {
    
    public mouseDown: EventObject;

    public mouseMove: EventObject;

    public mouseUp: EventObject;

    public mouseContext: EventObject;

    public mouseover: EventObject;

    public mouseout: EventObject;

    constructor() {
        const mouseDownTemp = _.cloneDeep(eventTemp);
        mouseDownTemp.eventType = 'mouseDown';
        this.mouseDown = new EventObject(mouseDownTemp);
        mouseDownTemp.eventType = 'mouseMove';
        this.mouseMove = new EventObject(mouseDownTemp);
        mouseDownTemp.eventType = 'mouseUp';
        this.mouseUp = new EventObject(mouseDownTemp);
    }

    public addAllListener(): void {
        this.setMouseActive(true);
    }

    public addMouseLeftListener() {

    }

    public addMouseRightListener() {

    }

    public removeAllListener() {
        this.setMouseActive(false);
    }

    public removeMouseLeftListener() {

    }

    public removeMouseRightListener() {

    }

    // 设置part的事件，增加或移除监听
    private setMouseActive( isActive ) {
        this.mouseDown.active = isActive;
        this.mouseMove.active = isActive;
        this.mouseUp.active = isActive;
    }


}