





































import Vue from 'vue';
import Scroll from '../../component/scroll/scroll.vue';
import LeftNav from '../leftNav/leftNav.vue';
import TopNav from '../topNav/topNav.vue';
import RightNav from '../rightNav/rightNav.vue';
import Canvas from '../canvas/canvas.vue';
import EnableWatch from '../editorEanble/enableWatch.vue';
import PaneResize from '../panelResize/panelResize.vue';
import { mapMutations, mapActions } from 'vuex';

// import WorkThread from '../../../dataSource/reqMethod/workThread';
export default Vue.extend({
    name: 'deitScreen',
    data() {
        return {
            moveVertical: 0,
            moveHorizontal: 0,
            loading: false,
        };
    },
    components: {
        LeftNav,
        TopNav,
        RightNav,
        Scroll,
        Canvas,
        EnableWatch,
        PaneResize,
    },
    created() {
        // this.loading = true;
        this.initCore();
    },
    mounted() {
        this.handleResize();
        this.$nextTick( () => {
            // setTimeout( () => {
            //     this.loading = false;
            // }, 300);
        });

        // test
        // const worker = new WorkThread();
        // worker.test();
    },
    computed: {
        projectObj(): any {
            return this.$store.getters.projectObj;
        },
        divViewPort() {
            return this.projectObj.divViewPort;
        },
        screenH(): number { 
            return this.projectObj.screenH;
        },
        screenW(): number { 
            return this.projectObj.screenW;
        },
        docClientW(): number { 
            return this.projectObj.docClientW;
        },
        docClientH(): number { 
            return this.projectObj.docClientH;
        },
        layoutSize() {
            return this.projectObj.layoutSize;
        },
        panelScale: {
            get(): number {
                return this.projectObj.panelScale;
            },
            set( value: number ) {
                this.$store.commit('setPanelScale', value);
            },
        },
        viewPortHeight(): number {
            return this.screenH * this.panelScale * 0.01;
        },
        viewPortWidth(): number {
            return this.screenW * this.panelScale * 0.01;
        },
        leftNavH() {
            return this.docClientH - this.layoutSize.topBarHeight;
        },
    },
    methods: {
        ...mapMutations([
            'setDocClient',
            'setPanelScale',
        ]),
        ...mapActions([
            'initCoreModel',
        ]),
        /**
         * 编辑器入口方法，调用vuex的初始化模型方法
         */
        initCore() {
            const mode = 'edit';
            this.initCoreModel(mode);
        },
        /**
         * 监听整个页面的缩放调整
         */
        handleResize: _.throttle( function() {
                const dom: HTMLElement = document.querySelector('.screen-main');
                const payload = {
                    h: dom.clientHeight,
                    w: dom.clientWidth,
                };
                this.setDocClient(payload);

                if (this.$refs.panelScroll) {
                    this.broadcastResize();
                }
        }, 500),
        /**
         * 广播缩放事件
         */
        broadcastResize() {
            this.$refs.panelScroll.resizeScroll();
        },
        scrollMove(args: number, position: string) {
            if (!position) {
                this.moveVertical = -args;
                this.moveHorizontal = -args;
            }
            if (position === 'left') {
                this.moveHorizontal = -args;
            }
            if (position === 'top') {
                this.moveVertical = -args;
            }
        },
    },
    beforeDestroy() {
    },
    errorCaptured(err, vm, info) {
        // console.log('watch vue error');
        // console.log(err);
        // console.log(vm);
        // console.log(info);
    }
})
