












import Vue from 'vue'
export default Vue.extend({
  name: 'slider-el',
  props: {
    opts: {
      type: Object,
      default: function() {
        return {}
      }
    },
    model: [String, Number, Boolean]
  },
  data() {
    return {
      initParam: {
        value: 0,
        step: 0.5,
        max: 100,
        min: 0
      },
      value: this.model || 0
    }
  },
  computed: {
    param(): any {
      return Object.assign(this.initParam, this.opts)
    },
    max(): number {
      return this.param.max
    },
    min(): number {
      return this.param.min
    },
    step(): number {
      return this.param.step
    }
  },
  methods: {
    handleChange(val: number) {
      this.$emit('on-data-change', this.param, val)
    }
  }
})
