
import { mapMutations, mapState } from 'vuex';
export default {
    data() {
        return {

        }
    },
    computed: {
        ...mapState({
            selectGroup: (state) => state.screen.editor.partSelection.selectGroup,
        }),
    },
    methods: {
        ...mapMutations([
            'handleLayer',
        ]),
        handleLayerOpt(operator: string): void {
            if (operator) {
                this.handleLayer( operator );
            }
        },
        /**
         * 初始化监听事件接收器
         */
        editorEvtReceiver(isDestoyed?: boolean) {
            const eventObjCfg = [
                {
                    name: 'upper',
                    handle: this.handleLayerOpt,
                },
                {
                    name: 'top',
                    handle: this.handleLayerOpt,
                },
                {
                    name: 'lower',
                    handle: this.handleLayerOpt,
                },
                {
                    name: 'bottom',
                    handle: this.handleLayerOpt,
                },
            ];
            // 存在两个以上子组件引用，需要改善
            for (const eventObj of eventObjCfg) {
                if (isDestoyed) {
                    this.$root.busRef.$off(eventObj.name);
                } else {
                    this.$root.busRef.$on(eventObj.name, ( eventType: string ) => {
                        eventObj.handle(eventType);
                    });
                }
            }
        },
    },
    mounted() {
        this.editorEvtReceiver();
    },
    beforeDestroy() {
        this.editorEvtReceiver(true);
    },
    render(h) {
        return h();
    }
};
