




















































import { mapMutations } from 'vuex';
import Vue from 'vue';
export default Vue.extend({
    data() {
        return {
            currentCmpt: {},
            isEditor: false,
            cmptName: '',
        };
    },
    props: {
        cmptGroupItem: {
            type: Object,
            default: (): Model.CmptGroup | any => {
                return {};
            },
        },
        cmptGroupList: {
            type: Array,
            default: (): Model.CmptGroup[] => {
                return [];
            },
        },
    },
    computed: {
        cmptList(): Model.Cmpt {
            return this.cmptGroupItem.cmptGroup;
        },
        filterCmptList(): Model.CmptGroup[] {
           const fliterCmptGroup = this.cmptGroupItem.cmptGroup.filter( (cg: Model.CmptGroup) => {
                return cg.cmptGroupId !== this.cmptGroupItem.cmptGroupId;
            });
            return fliterCmptGroup;
        },
    },
    methods: {
        ...mapMutations([
            'move_cmpt',
            'remove_cmpt',
            'edit_cmpt',
        ]),
        dragstart(event, dragItem) {
            const partInfo = dragItem.partSource;
            event.dataTransfer.setData("Text", JSON.stringify(partInfo));
            event.dataTransfer.dropEffect = "copy";
        },
        moveCmptCtm(targetCmptGroup: Model.CmptGroup): void {
            const moveCmptArgs: any = {
                sourceCmptGroup: this.cmptGroupItem,
                cmpt: this.currentCmpt,
                targetCmptGroup: targetCmptGroup,
            };
            console.log(moveCmptArgs);
            
            this.move_cmpt(moveCmptArgs);
        },
        currntCmptCtm(currentCmpt: Model.Cmpt) {
            this.currentCmpt = currentCmpt;
        },
        editCmptCtm(cmpt: Model.Cmpt) {
            let curCmpt: Model.Cmpt;
            if (_.isNil(curCmpt)) {
                curCmpt = this.currentCmpt;
            } else {
                curCmpt = cmpt;
            }

            // 整理数据格式为规定的格式,即是cmpt的属性
            const cmptProps = _.cloneDeep(curCmpt);
            cmptProps.isEditor = true;
            const cmptWrapper = {
                cmpt: cmptProps,
            };

            // 设置当前输入框的名称
            this.cmptName = cmptProps.cmptName;
            
            this.edit_cmpt(cmptWrapper);

            // 获取input聚焦
            this.$nextTick(() => {
                const cmptId = cmptProps.cmptId;
                (this.$refs[cmptId]as any)[0].focus();
            });
        },
        removeCmptCtm() {
            if (_.isNil(this.currentCmpt)) {
                return;
            }
            const delCmptArgs: any = {
                cmpt: this.currentCmpt,
                targetCmptGroup: this.cmptGroupItem,
            };
            this.remove_cmpt(delCmptArgs);
        },
        // 右击菜单中禁止移动到自己的组件容器
        onDisabledCmptGroup(currentCmptGroup: Model.CmptGroup) {
            return currentCmptGroup.cmptGroupId === this.cmptGroupItem.cmptGroupId;
        },
        // 输入框失焦保存数据
        handleClickOutSide(cmpt: Model.Cmpt) {
            // 整理数据格式为规定的格式,即是cmpt的属性
            const cmptProps = _.cloneDeep(cmpt);
            cmptProps.isEditor = false;
            if (this.cmptName) {
                cmptProps.cmptName = this.cmptName;
            }
            const cmptWrapper = {
                cmpt: cmptProps,
            };
            
            this.edit_cmpt(cmptWrapper);
        }
    },
});
