import AbstractOption from '../../../common/abstractOption'
import updatePropsDeep from 'utils/initProps'

/**
 * 图表title组件配置项
 */
export default class Title extends AbstractOption {

  public config: any;

  constructor(opts?: Chart.Title) {
    super()
    this.config = this.init(opts)
  }

  public init(opts?: Chart.Title) {
    const source: Chart.Title = {
      show: true,
      text: '标题名',
      top: 'auto',
      left: 'left',
      textStyle: {
        color: '#333',
        fontWeight: 'normal',
        fontSize: 16
      },
      padding: 15,
      backgroundColor: 'transparent',
      borderWidth: 0,
      borderColor: '#333',
      borderRadius: 0
    };
    return updatePropsDeep(source, opts);
  }

  public computed() { }

}