// vue页面过滤器

// 过滤图形元素的类型
export const countShapeType = (type: string) => {
  const map: any = {
    'simpleText': '文字',
    'rect': '矩形',
    'circle': '圆形',
    'line': '线',
    'image': '图片',
    'baseLine': '基本折线图',
    'areaLine': '区域折线图',
    'dblLine': '双轴折线图',
    'basePie': '基础饼图',
    'comparePie': '标注对比饼图',
    'ratioPie': '指标占比饼图',
    'rosePie': '南丁格尔玫瑰图',
    'noneAxisBar': '基本柱图',
    'verticalStackBar': '垂直基本柱图',
    'horizontalStackBar': '水平基本柱图',
    'verticalGroupBar': '垂直分组柱图',
    'horizontalGroupBar': '水平分组柱图',
    'baseLineBar': '基本折线柱图',
    'baseTable': '键值表格'
  };
  return map[type] || type;
};