


























import Vue from 'vue';
export default Vue.extend({
    data() {
        return {
            isActive: false,
            inputPartName: '',
        };
    },
    props: {
        isChild: {
            type: Boolean,
            default: false,
        },
        part: {
            type: Object,
            default: () => {
                return {};
            }
        },
        selectGroup: {
            type: Array,
            default: (): any[] => {
                return [];
            }
        }
    },
    computed: {
        partGroup(): any[] {
            return this.part.partGroup;
        },
        isSelected(): boolean {
            return this.part.isSelected;
        },
        isEdit() {
            return this.part.isEdit;
        }
    },
    watch: {
        isSelected( newVal: boolean ) {
            this.isActive = newVal;
        },
        isEdit(val) {
            if (val) {
                this.inputPartName = this.part.partName;
                this.$nextTick( () => {
                    this.$refs[this.part.partId].focus();
                });
            }
        },
    },
    methods: {
        /**
         * 图层是否被点击，可能是鼠标左键或右键
         */
        layerClick( keyDown?: string ) {
            if ( this.isChild ) {
                return;
            }
            if (keyDown === 'keyDown') {
                this.$emit('setCurrentLayer', this.part, keyDown);
                return;
            }
            this.$emit('setCurrentLayer', this.part);
        },
        /**
         * 取消input的输入状态
         */
        handleClickOutSide() {
            this.part.isEdit = false;
            this.$emit('hasChangedName', this.inputPartName);
        },
        /**
         * 解锁操作特殊处理
         */
        handleUnLock() {
            if (!this.isPartInSelectGroup()) {
                this.layerClick();
            }
            const evtName = 'unlock';
            this.$root.busRef.$emit(evtName, evtName);
        },
        /**
         * 校验当前的part是否在选中的selectGroup中
         * @return { boolean } true表示包含，false表示不包含
         */
        isPartInSelectGroup(): boolean {
            const hasPart = this.selectGroup.find( (part: Model.Part) => {
                return part.partId === this.part.partId;
            });
            return hasPart ? true : false;
        }
    },
});
