



















import { mouseWheel } from '../../../utils/bindEvent';
import { DomType, Dom, PosDom } from './interface';
import Vue from 'vue';
export default Vue.extend({
    data() {
        return {
            _wheelData: -1,
            yScaleRatio: 3,
            xScaleRatio: 3,
            halfHorezontal: 0,
            halfVertical: 0,

            xWidth: 25,
            xLeft: '0',
            yHeight: 25,
            yTop: '0',
            ySliderH: 0,

            $divViewPort: '',
            resizeSliderScale: -1,
        };
    },
    
    props: {
        viewPortHeight: {
            type: Number,
            default: 960,
        },
        viewPortWidth: {
            type: Number,
            default: 720,
        },
        divViewPort: {
            type: String,
            default: '',
        },
        layoutSize: {
            type: Object,
            default: () => {
                return {};
            },
        },
    },
    computed: {
        topH(): number {
            return this.layoutSize.topBarHeight;
        },
        leftNavW(): number {
            return this.layoutSize.leftNavWidth;
        },
    },
    watch: {
        viewPortHeight() {
            this.resizeScroll();
        },
        viewPortWidth() {
             this.resizeScroll();
        },
    },

    mounted() {
        this.init();
    },
    methods: {
        /**
         * 初始化整个滚动框架
         */
        init() {
            const dom = this.handleDomEle();
            this.resizeScroll();
            this.initScroll(dom);
        },
        handleDomEle() {
            const yelement = document.getElementById('y_handler');
            const ymainBox = document.getElementsByClassName('y_track');
            const xelement = document.getElementById('x_handler');
            const xmainBox = document.getElementsByClassName('x_track');
            const dom: Dom = {
                xDom: {
                    element: xelement,
                    mainBox: xmainBox[0],
                },
                yDom: {
                    element: yelement,
                    mainBox: ymainBox[0],
                }
            };
            return dom;
        },
        /**
         * 重置滚动条的状态
         */
        resizeScroll() {
            this.yScaleRatio = 2;
            const dom = this.handleDomEle();
            const yelement = dom.yDom.element;
            const ymainBox = dom.yDom.mainBox;

            // 初始化滑动条的位置和宽高
            this.initViewPortDiv();
            this.calculScaling(dom);
            this.initSliderPos(dom);
        },
        initScroll({ xDom, yDom }: Dom): void {
            this.initXScroll(xDom);
            this.initYScroll(yDom);
        },
        initViewPortDiv(): void {
            this.$divViewPort = $(`#${this.divViewPort}`)[0];
        },
        /**
         * 根据画板的分辨率计算滚条条的长度或宽度，和滚动缩放比例
         */
        calculScaling({ xDom, yDom }: any): void {

            const { element: xElement, mainBox: xmainBox } = xDom;
            const { element: yElement, mainBox: ymainBox } = yDom;

            // 计算y轴滚动滑块的宽度
            const yContainerH = $(ymainBox).height();
            const xContainerW = $(xmainBox).width();
            // console.log("y"+yContainerH);
            // console.log("x"+xContainerW);
            
            let ySlider = yContainerH - (this.viewPortHeight) / this.yScaleRatio;
            let xSlider = xContainerW - (this.viewPortWidth) / this.xScaleRatio;
            
            let ySlierTemp = 0;
            while ( (ySlider - 20) <= 0 || ySlierTemp < 0) {

                ySlierTemp = yContainerH - (this.viewPortHeight) / this.yScaleRatio;
                // 一当滚动滑块小于20，就默认高度是25
                ySlider = 22 ;

                // 增加滚轮距离和页面滑动的比例
                this.yScaleRatio++;
            }

            let xSlierTemp = 0;
            while ( (xSlider - 20) <= 0 || xSlierTemp < 0) {
                xSlierTemp = xContainerW - (this.viewPortWidth) / this.xScaleRatio;
                // 一当滚动滑块小于20，就默认高度是25
                xSlider = 22 ;
                // 增加滚轮距离和页面滑动的比例
                this.xScaleRatio++;
            }

            $(xElement).width(xSlider+"px");
            this.halfHorezontal = xSlider/2;
            $(yElement).height(ySlider+"px");
            this.halfVertical = ySlider/2;


        },

        initSliderPos({ xDom, yDom }: Dom): void{
            
            const { element: xElement, mainBox: xmainBox } = xDom;
            const { element: yElement, mainBox: ymainBox } = yDom;
            const halfYmainboxH = $(ymainBox).height() / 2;
            const halfXmainboxW = $(xmainBox).width() / 2;
            
            // if(this.resizeSliderScale === -1){
            // }else{
            //     const resizeSliderTarget = this.resizeSliderScale/this.yScaleRatio;
            //     this._wheelData = resizeSliderTarget;
            //     this.yTop = resizeSliderTarget + "px";
            // }
            // 这里原本resize应该记录之前滑块的位置，现在将这个功能去掉，现在resize会将滑块定位在中间的位置
            const ySliderTarget = halfYmainboxH - this.halfVertical;
            this._wheelData = ySliderTarget;
            this.yTop = ySliderTarget + 'px';

            const xSliderTarget = halfXmainboxW - this.halfHorezontal;
            this.xLeft = xSliderTarget + 'px';

            this.$emit('scrollMove', 0);

        },
        initXScroll(xDom: any): void {
            const domType: DomType = {
                position: 'left',
                clientCoord: 'clientX',
                clientType: 'clientWidth',
                offsetPos: 'offsetLeft',
                offsetType: 'offsetWidth',
                scrollPos: 'scrollLeft',
                bodyDis: this.leftNavW  || 0,
            };
            const opts = { 
                domEle: xDom,
                domType,
            };
            this._tragScroll(opts);
            this._clickScroll(opts);
        },
        initYScroll( yDom: any ): void{
            const domType: DomType = {
                position: 'top',
                clientCoord: 'clientY',
                clientType: 'clientHeight',
                offsetPos: 'offsetTop',
                offsetType: 'offsetHeight',
                scrollPos: 'scrollTop',
                bodyDis: this.topH || 0,
            };
            const opts = { 
                domEle: yDom, 
                domType, 
                domContent: this.$divViewPort,
            };
            
            this._tragScroll(opts);
            this._clickScroll(opts);
            this._wheelChange(opts);
        },
        
        _tragScroll( { domEle, domType }: { domEle: PosDom, domType: DomType}  ): void {
            const { element, mainBox } = domEle;
            const { position, clientCoord, clientType, offsetPos, offsetType } = domType;
            // { scrollMoveHook,  mouseoverHook, mouseout} = hook
            const mainType = mainBox[clientType];
            let _this = this;
            element.onmousedown = function(event) {
                const _offsetPos = element[offsetPos];
                const e = event || window.event;
                const pos = e[clientCoord];

                document.onmousemove = scrollGo;
                document.onmouseup = () => {
                    document.onmousemove = null;
                };

                function scrollGo(event) {
                    const eScroll = event || window.event;
                    let _pos = eScroll[clientCoord];
                    let _p = _pos - pos + _offsetPos;
                    // console.log('_pos'+_pos+'pos'+pos+'_offsetPos'+_offsetPos);
                    if (_p > (mainType - element[offsetType])) {
                        _p = mainType - element[offsetType];
                    }
                    if (_p <= 0) {
                        _p = 0;
                    }
                    element.style[position] = _p + 'px';
                    // scrollMoveHook();
                    if (position === 'top') {
                        _this._wheelData = _p;
                    }
                    // 移动数据
                    _this.emitSrollMove( _p, mainBox, offsetType, position);
                    
                }
            };
            element.onmouseover = function() {
                const parentDom = this;
                const childDom = parentDom.children[0];
                childDom.style.background = '#666';
            };
            element.onmouseout = function() {
                const parentDom = this;
                const childDom = parentDom.children[0];
                childDom.style.background = '#dedee4';
            };
        },

        _clickScroll({ domEle, domType }: { domEle: PosDom, domType: DomType }) {
            const { element, mainBox } = domEle;
            const { position, clientCoord, clientType, offsetPos, offsetType, scrollPos, bodyDis } = domType;
            const _this = this;
            const p = element.parentNode;
            p.onclick = function(event) {
                const e = event || window.event;
                const t = e.target || e.srcElement;
                const sTop = document.documentElement[scrollPos] > 0
                    ? document.documentElement[scrollPos]
                    : document.body[scrollPos];
                const top = mainBox[offsetPos];

                // 需要减掉top和left的菜单栏 左侧为 - 244；
                let _top = e[clientCoord] + sTop - top 
                    - element[offsetType] / 2
                    - top - bodyDis;

                if (_top <= 0) {
                    _top = 0;
                }
                if (_top >= (mainBox[clientType] - element[offsetType])) {
                    _top = mainBox[clientType] - element[offsetType];
                }
                if (t != element) {
                    element.style[position] = _top + 'px';
                    
                    if (position === 'top') {
                        _this._wheelData = _top;
                    }
                    _this.emitSrollMove( _top, mainBox, offsetType, position);
                }
            }
        },

        // 鼠标滚轮滚动，滚动条滚动
        _wheelChange({ domEle, domType, domContent }: { domEle: any, domType: DomType, domContent: any }) {
            let _this = this;
            const { element, mainBox } = domEle;
            const { position, clientCoord, clientType, offsetPos, offsetType, scrollPos } = domType;
            const node = typeof mainBox === 'string' ? $(domContent) : domContent;
            const rate = 0;
            let flag = 0;
            let wheelFlag = 0;
            if (node) {
                mouseWheel(node, (data: any) => {
                    wheelFlag = wheelFlag + data;
                    if (_this._wheelData >= 0) {
                        flag = _this._wheelData;
                        element.style[position] = flag + 'px';
                        wheelFlag = _this._wheelData * 12;
                        _this._wheelData = -1;
                    } else {
                        flag = wheelFlag / 12;
                    }
                    if (flag <= 0) {
                        flag = 0;
                        wheelFlag = 0;
                    }
                    if (flag >= (mainBox[offsetType] - element[offsetType])) {
                        flag = (mainBox[clientType] - element[offsetType]);
                        wheelFlag = (mainBox[clientType] - element[offsetType]) * 12;
                    }
                    element.style[position] = flag + 'px';
                    _this.emitSrollMove( flag, mainBox, offsetType, position);
                });
            }
        },
        emitSrollMove( elPosition: number, mainBox: HTMLDivElement, offsetType: string, position: string ): void {
            let halfTemp = 0;
            if (position === 'left') {
                halfTemp = this.halfHorezontal;
            }
            if (position === 'top') {
                halfTemp = this.halfVertical;
            }
            const tempVal = (elPosition + halfTemp -  (mainBox[offsetType]) / 2 );
            let scrollDistance = 0;
            if (position === 'left') {
                scrollDistance = this.xScaleRatio * tempVal;
            }
            if (position === 'top') {
                scrollDistance = this.yScaleRatio * tempVal;
            }

            this.$emit('scrollMove', scrollDistance, position);
            // 记录滑块与滑块容器的比例，用于resize（暂时不考虑了）
            // this.resizeSliderScale = this.yScaleRatio*elPosition;
        }
    },
    
})
