






import Vue from 'vue'
const idPrefix = 'rect'

export default Vue.extend({
  name: 'rectShape',
  props: ['initOption'],
  data() {
    return {
      id: this._.uniqueId(idPrefix),
      styleMap: {
        // 全局
        width: 200,
        height: 100,
        left: 0,
        top: 0,
        zIndex: 1,
        opacity: 1,
        // 角度
        transform: 0,

        // 填充色
        backgroundColor: '#E8E8E8',

        // 边线
        borderWidth: 1,
        borderColor: '#333',
        borderStyle: 'solid',
        borderRadius: 0,

        // 阴影
        shadowColor: '',
        shadowH: 0,
        shadowV: 0,
        shadowBlur: 0,
        shadowSpread: 0
      }
    }
  },
  computed: {
    shapeStyle(): any {
      return {
        width: `${this.styleMap.width}px`,
        height: `${this.styleMap.height}px`,
        left: `${this.styleMap.left}px`,
        top: `${this.styleMap.top}px`,
        zIndex: `${this.styleMap.zIndex}px`,
        opacity: this.styleMap.opacity,
        transform: `rotate(${this.styleMap.transform}deg)`,
        borderWidth: `${this.styleMap.borderWidth}px`,
        borderColor: this.styleMap.borderColor,
        borderStyle: this.styleMap.borderStyle,
        borderRadius: `${this.styleMap.borderRadius}px`,
        backgroundColor: this.styleMap.backgroundColor,
        boxShadow: `
        ${this.styleMap.shadowH}px 
        ${this.styleMap.shadowV}px
        ${this.styleMap.shadowBlur}px 
        ${this.styleMap.shadowSpread}px
        ${this.styleMap.shadowColor}`
      }
    }
  },
  methods: {
    setOption(option: any): void {
      const styles: object = option.content || {}
      if (this._.isPlainObject(styles)) {
        for (let [key, value] of Object.entries(styles)) {
          if (!this._.isNil(this.styleMap[key])) {
            this.styleMap[key] = value
          }
        }
      }
    },
    handleIntOption() {
      this.setOption(this.initOption)
    }
  },
  mounted() {
    this.handleIntOption()
  }
})
