export default class DataSource{
    
    /**
     * 数据源展示数据
     */
    public displayData: string;

    /**
     * 测点请求的缓存数据
     */
    public pointReqData: string;

    /**
     * 抽屉是否状态
     */
    public drawerStatus: boolean;

    constructor() {
        this.displayData = '';
        this.pointReqData = '';
        this.drawerStatus = false;
    }

    public setDisplayData( displayData: string ): void {
        this.displayData = displayData;
    }

    public setPointReqData( pointReqData: string ): void {
        this.pointReqData = pointReqData;
    }

    public setDrawerStatus( drawerStatus: boolean ): void {
        this.drawerStatus = drawerStatus;
    }

}