import PartEvent from './part/partEvent';
import CanvasEvent from './canvas/canvasEvent';
import KeyCodeEvent from './keyBoard/keyCodeEvent';
import ContextMenuEvent from './contextMenu/contextMenuEvent';

export default class Event {

    public partEvent: PartEvent;

    public partContextEvent: any;

    public canvasEvent: any;

    public contextMenuEvent: ContextMenuEvent;

    public scrollEvent: any;

    public keyCodeEvent: KeyCodeEvent;

    public dropEvent: any;

    constructor() {
        this.partEvent = new PartEvent();
        this.canvasEvent = new CanvasEvent();
        this.keyCodeEvent = new KeyCodeEvent();
        this.contextMenuEvent = new ContextMenuEvent();
    }

    public destroyed() {
        this.partEvent = null;
        this.canvasEvent = null;
        this.keyCodeEvent = null;
        this.contextMenuEvent = null;
    }
}