
import Vue from 'vue';
import { mapMutations, mapState, mapGetters } from 'vuex';
const selectorType = {
    single: 'single',
    multiply: 'multiply',
};

export default Vue.extend({
    name: 'partSelection',
    data() {
        return {
            rectDiff: new Map(),
            virtualPart: {},
        };
    },
    computed: {
        ...mapState({
            basePartList: (state: any) => state.screen.pageManage.partContainer,
        }),
        ...mapGetters([
            'partSelection',
        ]),
        selectGroup() {
            return this.partSelection.selectGroup;
        },
    },
    methods: {
        ...mapMutations([
            'updateRefLine',

            // partSelection 
            'setType',
            'updateSelectPart',
            'transform_his',
            'updateSelectGroup',
            'clearSelectGroup',
            // transform
            'setDisableRotate',
            'setDisableScale',
            // rubberBand
            'mapRubberBand',

            // editor
            'setPartId',
            'resetPartId'
        ]),
        /**
         * 更新rubberBand选中的内容
         * 更新多选外围的矩形区域
         * 更新选中的part数组
         */
        updateRubberBandCoord(rectCoord): void {
            this.mapRubberBand(rectCoord);
        },
        mapSelector(selector) {
            const {
                selectPart,
                selectGroup,
                type
            } = selector;
            this.clearSelectGroup();
            this.updateSelectPart(selectPart);
            this.updateSelectGroup(selectGroup);
            this.setType(type);

            // 设置虚拟part为空
            this.virtualPart = {};
        },
        /**
         * 映射多选的模型信息
         */
        mapSelectorGroup(rectCoord, innerPartList: any[]): void {
            const selectPart = Object.assign(rectCoord, {
                angle: 0
            });
            const selector = {
                selectPart,
                selectGroup: innerPartList,
                type: selectorType['multiply']
            };
            this.mapSelector(selector);
            this.setDisableRotate(true);
            this.setDisableScale(true);
        },
        /**
         * 映射单选的模型信息
         */
        mapSelectorPart(part): void {
            const selector = {
                type: selectorType['single'],
                selectPart: {
                    ...part.partStyle
                },
                selectGroup: [part],
            };
            this.mapSelector(selector);
            this.setPartId(part.partId);
            this.setDisableRotate(false);
            this.setDisableScale(false);
        },
        /**
         * 取消选择（ 单选还是多选 ）
         */
        deselect(): void {
            this.setType(selectorType['single']);
            this.clearSelectGroup();
            this.resetPartId();
            // lockPart: {
            //     lockGroup: [],
            //     isLocked: false,
            // }
            // this.clearLockSelector();
        },
        /**
         * 更新selectorPart模型
         */
        mapTransformSelector(part): void {
            let selector = {};
            switch (part.type) {
                case 'resize':
                    selector = {
                        width: part.w,
                        height: part.h,
                        x: part.x,
                        y: part.y,
                    };
                    break;
                case 'drag':
                    selector = {
                        x: part.x,
                        y: part.y,
                    };
                    break;
                case 'rotate':
                    selector = {
                        angle: part.r,
                    };
                    break;
                default:
                    break;
            }
            this.updateSelectPart(selector);
            
            // 如果任何一个part没有移动过，不做历史记录
            if (_.isBoolean(part.hasMove) && !part.hasMove) {
                return;
            }
            this.transform_his();
        },
        // 触发part选中事件后，根据当前的type，执行相应的事件
        mapDifferPart(part): void {
            const selectType = this.partSelection.type;
            switch (selectType) {
                case selectorType['single']:
                    this.mapSelectorPart(part);
                    break;
                case selectorType['multiply']:
                    this.handleRectDriff(part);
                    break;
                default:
                    this.mapSelectorPart(part);
                    break;
            }
        },
        // 更新不同type的part
        updataDifferPart(part): void {
            const transformType = this.partSelection.type;
            switch (transformType) {
                case selectorType['single']:
                    this.updataSinplePart(part);
                    break;
                case selectorType['multiply']:
                    this.updataMultiply(part);
                    break;
            }
        },
        // 右击part选中目标或多选
        mapCtmPart(part) {
            // 判断是否已经存在的选择目标
            if (this.selectGroup.length > 0) {
                return;
            }
            this.mapDifferPart(part);
        },
        // 点击part执行相应的事件
        handleRectDriff(part): void {
            const rectCoord = this.partSelection.selectPart;
            const selectGroup = this.partSelection.selectGroup;
            const currentSelectPartId = part.partId;

            if (selectGroup.length < 2) {
                return;
            }
            const includeCurrentPart = selectGroup.some((item) => {
                return item.partId === currentSelectPartId;
            });
            // 多选情况下，选择当前的part不包括在selectGroup中,切换为选中当前的元素
            if (!includeCurrentPart) {
                this.mapSelectorPart(part);
                return;
            }
            selectGroup.map( (item) => {
                const partStyle = item.partStyle;
                const partId = item.partId;
                const diff = {
                    x: 0,
                    y: 0,
                };
                diff.x = partStyle.x - rectCoord.x;
                diff.y = partStyle.y - rectCoord.y;
                this.rectDiff.set(partId, diff);
            });
        },

        /**
         * 更新单个变形后part的值
         */
        updataSinplePart(part) {
            const partId = part.partId;
            const partIdIndex: number = this.basePartList.findIndex((item) => {
                return item.partId === partId;
            });

            if (partIdIndex === -1) {
                return;
            }

            const cloneSelectPart = this.basePartList[partIdIndex];
            const mapSelectDomPart = (selectPart: Model.Group): void => {
                const partStyle = selectPart.partStyle;
                const partType = selectPart.partType;
                switch (part.type) {
                    case 'resize':
                        // 处理组合的时候放大缩小，子级也会做比例位置和大小也会做出相应的改变
                        if (partType === 'group') {
                            const  scaleW = part.w / partStyle.width;
                            const  scaleH = part.h / partStyle.height;
                            const childGroup = selectPart.partGroup;
                            
                            childGroup.forEach( (childPart: Model.Part) => {
                                const childPartStyle = childPart.partStyle;
                                childPartStyle.x = scaleW * childPartStyle.x;
                                childPartStyle.y = scaleH * childPartStyle.y;
                                childPartStyle.width = scaleW * childPartStyle.width;
                                childPartStyle.height = scaleH * childPartStyle.height;
                            });
                        }
                        partStyle.width = part.w,
                        partStyle.height = part.h,
                        partStyle.x = part.x;
                        partStyle.y = part.y;
                        break;
                    case 'drag':
                        partStyle.x = part.x;
                        partStyle.y = part.y;
                        break;
                    case 'rotate':
                        partStyle.angle = part.r;
                        break;
                }
            };
            mapSelectDomPart(cloneSelectPart);
            const mapPartList = {
                partIndex: partIdIndex,
                clonePart: cloneSelectPart,
            }
            // 更新part的refline
            this.updateRefLine(cloneSelectPart);
        },
        /**
         * 更新多选变形后每个part的值
         */
        updataMultiply( rectCoord ) {
            if (rectCoord.type === 'rotate') {
                return;
            }
            const selector = this.partSelection.selectPart;
            if (_.isEmpty(this.virtualPart)) {
                this.virtualPart = _.cloneDeep(selector);
            }
            
            this.selectGroup.map( (item) => {
                switch (rectCoord.type) {
                    case 'resize':
                        this.scalePart(rectCoord, this.virtualPart, item);
                        break;
                    case 'drag':
                        const partId = item.partId;
                        const rectDiff = this.rectDiff.get(partId);
                        const partStyle = item.partStyle;
                        partStyle.x = rectDiff.x + rectCoord.x;
                        partStyle.y = rectDiff.y + rectCoord.y;
                        // const pos = {
                        //     x: rectDiff.x + rectCoord.x,
                        //     y: rectDiff.y + rectCoord.y,
                        // };
                        // item.setPublicStyle(pos);
                        // this.virtualPart = {};
                        break;
                }
                // 更新part的refline
                this.updateRefLine(item);
            });
            this.virtualPart.x = rectCoord.x;
            this.virtualPart.y = rectCoord.y;
            this.virtualPart.height = rectCoord.h;
            this.virtualPart.width = rectCoord.w;
        },
        scalePart( coord: any, coordSource: any,  part: Model.Group ) {
            // 计算出缩放的比例
            const scaleW = coord.w / coordSource.width;
            const scaleH = coord.h / coordSource.height;

            // 获取part的类型
            const partType = part.partType;
            const partStyle = part.partStyle;

            // partStyle.width = partStyle.width * scaleW;
            // partStyle.height = partStyle.height * scaleH;

            // // 这里的缩放存在问题
            // partStyle.x = coord.x + (scaleW * (partStyle.x - coordSource.x));
            // partStyle.y = coord.y + (scaleH * (partStyle.y - coordSource.y));
            const pos = {
                width: partStyle.width * scaleW,
                height: partStyle.height * scaleH,
                x: coord.x + (scaleW * (partStyle.x - coordSource.x)),
                y: coord.y + (scaleH * (partStyle.y - coordSource.y)),
            };
            part.setPublicStyle(pos);
            
            if (partType === 'group') {
                const childGroup = part.partGroup;
                childGroup.forEach( (childPart: Model.Part) => {
                    const childPartStyle = childPart.partStyle;
                    childPartStyle.x = scaleW * childPartStyle.x;
                    childPartStyle.y = scaleH * childPartStyle.y;
                    childPartStyle.width = scaleW * childPartStyle.width;
                    childPartStyle.height = scaleH * childPartStyle.height;
                });
            }
        },
    },
    render(h) {
        return h();
    },
});
