export default class Point {

    /**
     * 测点类型 - 字符串，累计，数字，计算量等
     * 根据测点类型，选择测点类型的测点
     * 位置下的测点统计，通过计算量来表达
    */
    public pointType?: string;

    /**
     * 设备类型
     * 设备根据设备类型去分类
    */
    public pointDeviceType?: string;

    /**
     * 测点设备
    */
    public pointDevice?: string;

    /**
     * 记录唯一点id
     * 1. 可以通过测点类型选择
     * 2. 可以通过具体设备选择
     */
    public pointId: string;

    /**
     * 记录测点名称
     */
    public pointName: string;

    /**
     * 取值类型[实时， 历史]的子级
     *               | 工程值
     *      | - 实时 - 
     *               | 统计值
     * 类型 -
     *               | 工程值
     *      | - 历史 - 
     *               | 统计值
     * 工程值（当前值）
     * 统计值（日统、月统、年统）
     */
    public childValueTyle: number;

    /**
     * 统计值需要选该选项
     * 该选项为每月max，每日Average等...
     */
    public periodAggregate?: string;

    /**
     * 该选项为每月，每日，平均值等...
     * 
     * 取值聚合，当前值和统计值不需要选该选项
     */
    public period?: string;

    /**
     * 该选项为最大值，最小值，平均值等...
     * 
     * 取值聚合，当前值和统计值不需要选该选项
     */
    public aggregate?: string;

    /**
     * 该测点是否被删除
     * 找不到该测点
     * 后端配置code码来做检验
    */
    public isPointNull: boolean;

    /**
     * 统计周期 - 可能为日统，月统，年统...
     */
    // public period: string;

    /**
     * 该值主要由测点类型决定
     * 统计方法 - 平均值，最大值，最小值，次数等
     */
    // public statsMethod: string;
    
    /**
     * 日期范围 - [ 2019/1/1 - 2019/1/30 ]
     */
    // public dateRange: [string, string];

    /**
     * 时间范围 - [ 2:00 - 16:00 ]
     */
    // public timeRange: [string, string];

    constructor(point: any) {
        const {
            pointType,
            pointDeviceType,
            pointDevice,
            pointId,
            pointName,

            childValueTyle,
            periodAggregate,
            period,
            aggregate,
            
            isPointNull
        } = point;
        this.pointType = pointType || '';
        this.pointDeviceType = pointDeviceType || '';
        this.pointDevice = pointDevice || '';
        this.pointId = pointId || '';
        this.pointName = pointName|| '',

        this.childValueTyle = childValueTyle || '';
        this.periodAggregate = periodAggregate || '';
        this.period = period || '';
        this.aggregate = aggregate || '';

        this.isPointNull = isPointNull || false;
    }

    public getPartAllProps(): Model.Point {
        return {
            pointType: this.pointType,
            // pointDeviceType: this.pointDeviceType,
            // pointDevice: this.pointDevice,
            pointId: this.pointId,
            pointName: this.pointName,

            childValueTyle: this.childValueTyle,
            periodAggregate: this.periodAggregate,
            period: this.period,
            aggregate: this.aggregate,
            
            isPointNull: this.isPointNull,
        }
    }

}