/**
 * 该文件主要是pageManage的管理
 * 其中包含了对状态stateManage和partManage的管理
 * 
 * @author zhengchuguo
 */
import Pages from './pages';
import PartManage from '../part/partManage';
import StateManage from '../states/stateManage';
import Part from '../part/part';
import PagesController from './pagesController';

export default class PagesManage {

    /**
     * 状态容器
     */
    public stateManage: StateManage;

    /**
     * 组件容器
     */
    public partManage: PartManage;

    /**
     * 组件容器
     */
    public pagesController: PagesController;

    /**
     * 当前页面
     */
    public currentPage: Pages;

    /**
     * 当前状态
     */
    public currentState: any;

    /**
     * 当前元素列表
     */
    public partContainer: any = [];

    /**
     * 当前状态列表
     */
    public pageStateList: any = [];

    /**
     * 页面容器
     */
    public pagesContainer: any[] = [];

    constructor() {
        this.stateManage = new StateManage();
        this.partManage  = new PartManage();
        this.pagesController = new PagesController();
        this.currentPage = null;
    }

    /**
     * 更新当前页面
     * @pageModel { Pages } 这是一个纯引用的对象，需要重新获取双向绑定的引用 
     */
    public updateCurrentPage( pageModel: Pages ) {
        // 当前页面
        if (this.currentPage && pageModel.pagesId === this.currentPage.pagesId) {
            return;
        }

        const pageIndex = this.pagesController.findPages(this.pagesContainer, pageModel);
        if (_.isNumber(pageIndex) && pageIndex !== -1) {
            const curPage = this.pagesContainer[pageIndex];
            this.currentPage = curPage;
            // 存在未被初始化的partCtn
            const uniPartList: Model.Part[] = [];
            curPage.partContainer.forEach( (part: Model.Part) => {
                if ( !part.instance ) {
                    uniPartList.push(part);
                }
            });
            
            this.partContainer = curPage.partContainer;

            // 复制页面会出现以下的情况
            if (uniPartList.length) {
                // 删除未被初始化的part
                this.removePart( uniPartList );
                
                // 新增part,将会被初始化
                this.addPart(uniPartList);
            }
        }
    }
    
    /**
     * 初始化页面 
     */
    public initPage() {
        this.pagesController.initPage(this.pagesContainer);
    }

    /**
     * 新增页面 
     */
    public addPage() {
        return this.pagesController.newPage(this.pagesContainer);
    }

    /**
     * 移除页面 
     */
    public removePage( pagesId: string[] ) {
        if ( pagesId && !Array.isArray(pagesId) ) {
            return;
        }
        this.pagesController.removePage(this.pagesContainer, pagesId);
    }

    /**
     * 编辑页面(编辑页面名称)
     */
    public editPage(page: Pages, pagesProp: any ) {
        this.pagesController.editPage(this.pagesContainer, page, pagesProp);
    }

    public copyPage(page: Model.Page) {
        this.pagesController.copyPage(this.pagesContainer, page);
    }

    /**
     * 获取所有页面的纯数据
     */
    public mapPagesAllProps() {
        return this.pagesController.getPagesAllProps(this.pagesContainer);
    }

    /**
     * 恢复所有页面的纯数据
     */
    public resetPagesAllProps( pagesCtmProps: Model.Page[] ) {
        const partCallBackFn =  this.partManage.addPart.bind(this.partManage);
        this.pagesController.setPagesAllProps(this.pagesContainer, pagesCtmProps, partCallBackFn);
        // 是否需要更新当前的页面
    }

    /**
     * 新增元素
     * 
     * @param {any}  typeObj 新增元素所需的参数
     */
    public addPart( typeObj: Model.PartArgs[] ): void {
        // 更新图层
        this.partManage.updateLayer(this.partContainer);
        
        const partList: Model.Part[] = this.partManage.addPart( typeObj );
        if (!_.isNil(partList)) {
            this.partContainer.push(...partList);
        }
    }

    /**
     * 查找part数组
     */
    public findPartGroup(partList): any[] {
        const list: any[] = [];
        if (!Array.isArray(partList) || partList.length === 0) {
            return list;
        }
        partList.map( (part) => {
            const findPart = this.partManage.findCurrentPart( part.partId, this.partContainer );
            if (findPart) {
                list.push(findPart);
            }
        });
        return list;
    }

    /**
     * 删除元素
     * 
     * @partList 指定要删除的元素，规定是个数组，既可以删除多个
     */
    public removePart( partList: any[] ): void | null {
        this.partManage.removePart( this.partContainer, partList );
    }

    /**
     * 清除所有的元素
     * 
     * @partList 指定要删除的元素，规定是个数组，既可以删除多个
     */
    public clearPart(): void {
        this.partManage.clearPart( this.partContainer );
    }

    /**
     * 复制元素
     * 
     * @part 指定要复制的元素
     */
    public copyPart( part ) {
        if ( !part ) {
            return;
        }
        this.partManage.copy(this.partContainer, part);
    }


    /**
     * 映射元素列表
     * 被历史功能隐式调用
     */
    public mapPartAllProps() {
        const partPropList: any[] = [];
        if (this.partContainer.length === 0) {
            return partPropList;
        }
        this.partContainer.map( (part) => {
            partPropList.push(part.getPartAllProps());
        });
        return partPropList;
    }

    /**
     * 重设元素列表中每个元素的通用属性
     * 被历史功能隐式调用
     */
    public resetPartAllProps( partPropList ) {
        if ( partPropList.length === 0 ) {
            this.clearPart();
            return;
        }
        
        partPropList.map( (part) => {
            const findPart = this.partManage.findCurrentPart(part.partId, this.partContainer);
            if ( findPart ) {
                // 可能改变属性
                findPart.setPartAllProps(part);
            } else {
                // 已经被删除，恢复删除的元素
                this.addPart([part]);
            }
        });
        const delPartArray: Part[] = [];
        this.partContainer.map( (part: Part) => {
            const findPart = this.partManage.findCurrentPart(part.partId, partPropList);
            if ( !findPart ) {
                // 新增的
                delPartArray.push(part);
            }
        });
        this.removePart(delPartArray);
    }

    public destroyed() {
        this.pagesController.clearPages(this.pagesContainer);
        this.pagesContainer = null;
        this.currentState = null;
        this.currentPage = null;
        this.pageStateList = null;
        this.stateManage = null;
        this.partManage = null;
        this.partContainer = [];
        console.log("destroyed page manage");
    }

}
