export default class KeyCodeObject {
    
    /**
     * keycode事件名称,这个应该唯一
     */
    public eventName: string;

    /**
     * keycode组合键盘或是单个键
     */
    public keyCode: string | string[];

    /**
     * 回调函数
     */
    public handle: () => void;

    /**
     * 回调函数上下文
     */
    public handleContext: any;

    /**
     * 事件激活状态
     */
    public active: boolean;

    /**
     * 事件dom目标，可为空
     */
    
    constructor( keyCodeObjs ) {
        // console.log(keyCodeObjs);
        const {
            eventName = '',
            type = '',
            keyCode = '', 
            handle = null , 
            handleContext = null, 
            active = true 
        } = keyCodeObjs;
        this.eventName = eventName || type;
        this.keyCode = keyCode;
        this.handle = handle;
        this.handleContext = handleContext;
        this.active = active;
    }
}