<template>
    <svg 
        xmlns="http://www.w3.org/2000/svg" 
        class="svg-icon icon" viewBox="0 0 34 24" 
        aria-hidden="true">
        <path :d="iconPath">
        </path>
    </svg>
</template>
<script>
import pathConfig from './config';
export default {
    data() {
        return {
            pathConfig: pathConfig,
        }
    },
    props: {
        name: {
            type: String,
            default: '',
        }
    },
    computed: {
        iconPath() {
            return pathConfig[this.name];
        }
    }
};
</script>
<style lang="less" scope>
</style>

