
















import Vue from 'vue'
import { mapMutations, mapState } from 'vuex'
import PublicStyle from './public/public.vue'
import PrivateStyle from './private/propsTree.vue'

export default Vue.extend({
  components: {
    'private-style': PrivateStyle,
    'public-style': PublicStyle
  },
  data() {
    return {
      shapeName: '',
      guiOption: [],
      shapeStyle: {}
    }
  },
  computed: {
    ...mapState({
      partSelection: (state: any) => state.screen.editor.partSelection
    }),

    /**
     * 被选中的part数组
     */
    selectGroup(): Model.Part[] {
      return this.partSelection.selectGroup
    },

    /**
     * 当前选中的part
     * 目前只处理单个part
     */
    curSelectPart(): Model.Part {
      const { length } = this.selectGroup
      return length === 1 && this.isReleasePart ? this.selectGroup[0] : {}
    },

    /**
     * 优化选中part的性能
     * 修改为释放part，才更新配置项 -- 样式 or 数据
     */
    isReleasePart(): boolean {
      return this.partSelection.releasePart
    }
  },
  watch: {
    curSelectPart(part: Model.Element) {
      if (!this.isReleasePart) {
        return
      }

      if (Object.keys(part).length) {
        this.shapeStyle = null
        this.shapeStyle = part.shapeStyle
        this.shapeName = part.childType
      } else {
        this.shapeStyle = {}
      }
    },

    shapeStyle: {
      handler: function(shapeStyle) {
        if (this._.isObject(shapeStyle)) {
          this.guiOption = null
          this.guiOption = this.curSelectPart.guiOption
        } else {
          this.guiOption = []
        }
      },
      deep: true
    }
  },
  methods: {
    ...mapMutations(['set_partStyle', 'set_partGuiSeries']),

    /**
     * Fixme - 这里存在当前选中元素切换所触发的事件在input失焦事件之前
     */
    handleDataChange(opts: any, val: any) {
      const params: any = Object.assign(opts, { val })
      this.set_partStyle(params)
    },
    handleSeriesAdd(item: any) {
      const params: Model.SeriesParam = {
        type: 'add',
        series: item
      }
      this.set_partGuiSeries(params)

      // 因vue watch监控机制, 新增系列shapeStyle变化未监控到，需强制刷新
      this.shapeStyle = this.curSelectPart.shapeStyle
    },
    handleSeriesDel(item: any) {
      const params: Model.SeriesParam = {
        type: 'delete',
        series: item
      }
      this.set_partGuiSeries(params)
      this.shapeStyle = this.curSelectPart.shapeStyle
    }
  },
  created() {
    this.$root.busRef.$on('on-gui-change', this.handleDataChange)
    this.$root.busRef.$on('on-series-add', this.handleSeriesAdd)
    this.$root.busRef.$on('on-series-del', this.handleSeriesDel)
  },
  beforeDestroy() {
    this.$root.busRef.$off('on-gui-change', this.handleDataChange)
    this.$root.busRef.$off('on-series-add', this.handleSeriesAdd)
    this.$root.busRef.$off('on-series-del', this.handleSeriesDel)
  }
})
