




import Vue from 'vue'
export default Vue.extend({
  name: 'checkbox',
  props: {
    opts: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  computed: {
    checked: {
      get(): number {
        return this.opts.model
      },
      set(val: number): number {
        return val
      }
    }
  },
  methods: {
    handleChange(val: boolean) {
      this.$emit('on-data-change', this.opts, val)
    }
  }
})
