/*
 * @Description: 管理图层
 * 1. 处理置顶置底： 
 *  找到选中数组中最小的layer，比较列表中未选中中最大的layer,
 *  如果大于，不做操作，如果小于，选中的数组升序【4，5，6】，如果此时为选中最大是5，那么4开始5+i... 
 *  置底思维相似。
 * 2. 处理上移下移
 *  例如有数组【1，2，3，4，7，8，10】，选中的数组为【2，7，8】
 *  此时如果上移的结果是【1，2，3，4，7，9，10】可以看出是2与3相互交换，10换为7，【7，8】换为【9，10】
 *  上移：原始的数组升序排列（存在进来的数组并不是依靠layer排序的），新增bunchPart用于缓存目前下标指定的part
 *  遍历原始数组，当遇到2，因为2存在选中的数组，又因为不是连续的，所以直接交换即可，
 *  当遇到7，因为后面还有8，推入bunchPart，处理bunchPart。
 *  下移类似的思路。
 * @Author: chuguo.zheng
 * @Date: 2019-05-15 17:46:05
 */

type MapType = Map<string, (selPartGroup: Model.Part[], sourceGroup: Model.Part[]) => void>;
export default class Layer {

    private layerMap: MapType = new Map([
        ['upper', this.setUpperZindex],
        ['top', this.setMaxZindex],
        ['lower', this.setLowerZindex],
        ['bottom', this.setMinZindex],
    ]);

    /**
     * 该方法主要是控制图层的上下置顶和置底操作
     * 置顶置底，找到最大或是最小的zIndex, 并+1或-1
     * 图层上移或下移， 找到比邻的part并置换他们的zIndex值
     * @param  { part } current 当前操作的part对象
     * @param  { Array<part> } source  原始数据数组
     * @param  { String } operator 4种事件
     */
    public handleLayer( selpartGroup: Model.Part[], source: Model.Part[], operator: string ): void {
        if ( _.isNil(selpartGroup) ||  !source.length) {
            return;
        }
        if (!Array.isArray(selpartGroup)) {
            return;
        }
        
        // 获取对应的方法
        const operateFuc: (selPart: Model.Part[], source: Model.Part[]) => void 
            = this.layerMap.get(operator).bind(this);
        
        operateFuc(selpartGroup, source);
    }

    public setMaxZindex( selPartGroup: Model.Part[], sourceGroup: Model.Part[] ): void {
        // 找出选中数组中最小的zIndex
        let selMinIndex: number;
        selPartGroup.forEach( (part: Model.Part) => {
            const partZindex = part.layer;
            if (_.isNil(partZindex)) {
                selMinIndex = partZindex;
            }
            if (partZindex < selMinIndex) {
                selMinIndex = partZindex;
            }
        });

        // 如果该part被选中，排除该part
        const unexpectedPart = (part: Model.Part) => {
            return selPartGroup.some( (selPart) => {
                if (selPart.partId === part.partId) {
                    return true;
                }
            });
        };

        // 遍历数组，找出最最大的layer
        let maxIndex: number;
        for (const part of sourceGroup) {
            const partZindex = part.layer;
            if (unexpectedPart(part)) {
                continue;
            }
            if (_.isNil(maxIndex)) {
                maxIndex = partZindex;
            }
            if (partZindex > maxIndex) {
                maxIndex = partZindex;
            }
        }

        // 选中数组的最小值大于（除了选中part）找出来的最大值，不操作
        if (selMinIndex > maxIndex) {
            return;
        }

        // 给selGroup排序,根据layer从小到大，升序
        const sortPartGroup: Model.Part[] = _.sortBy(selPartGroup, (item) => { 
            return item.layer;
        });
        
        // 改变选中的值
        sortPartGroup.forEach( (selPart: Model.Part, index: number ) => {
            selPart.layer = maxIndex + (index + 1);
        });
    }

    public setMinZindex( selPartGroup: Model.Part[], sourceGroup: Model.Part[] ): void {

        // 找出选中数组中最大的zIndex
        let selMaxIndex: number;
        selPartGroup.forEach( (part: Model.Part) => {
            const partZindex = part.layer;
            if (_.isNil(selMaxIndex)) {
                selMaxIndex = partZindex;
            }
            if (partZindex > selMaxIndex) {
                selMaxIndex = partZindex;
            }
        });

        // 如果该part被选中，排除该part
        const unexpectedPart: (part: Model.Part) => boolean = (part) => {
            return selPartGroup.some( (selPart) => {
                if (selPart.partId === part.partId) {
                    return true;
                }
            });
        };

        // 遍历数组，找出最最大的layer
        let minIndex: number;
        for (const part of sourceGroup) {
            const partZindex = part.layer;
            if (unexpectedPart(part)) {
                continue;
            }
            if (_.isNil(minIndex)) {
                minIndex = partZindex;
            }
            if (partZindex < minIndex) {
                minIndex = partZindex;
            }
        }

        // 选中数组的最大值小于（除了选中part）找出来的最小值，不操作
        if (selMaxIndex < minIndex) {
            return;
        }
        
        // 给selGroup排序，降序
        const sortPartGroup: Model.Part[] = _.sortBy(selPartGroup, (item) => { 
            return -item.layer;
        });

        // 改变选中的值
        sortPartGroup.forEach( (selPart: Model.Part, index: number ) => {
            selPart.layer = minIndex - (index + 1);
        });
    }

    public setUpperZindex( selPartGroup: Model.Part[], sourceGroup: Model.Part[] ): void {
        // 给sourceGroup排序,升序[{layer: 1}, {layer: 3}]
        const sortSourceGroup: Model.Part[] = _.sortBy(sourceGroup, (item) => { 
            return item.layer;
        });
        const way = 'upper';
        this.handelZindex(selPartGroup, sortSourceGroup, way);
    }

    public setLowerZindex( selPartGroup: Model.Part[], sourceGroup: Model.Part[] ): void {
        // 给sourceGroup排序,降序[{layer: 3}, {layer: 1}]
        const sortSourceGroup: Model.Part[] = _.sortBy(sourceGroup, (item) => { 
            return -item.layer;
        });
        const way = 'lower';
        this.handelZindex(selPartGroup, sortSourceGroup, way);
    }

    /**
     * 该方法用于移动图层，如果是上移图层，传入的sortSourceGroup为升序，反之为降序
     * @param selPartGroup 选中的part元素
     * @param sortSourceGroup 全部个part列表
     * @param way 执行方法的类型 -- 上移图层 | 下移图层
     */
    private handelZindex( selPartGroup: Model.Part[], sortSourceGroup: Model.Part[], way: string ): void {
        // part是否存在选中的part中
        const includesPart: (part: Model.Part, partList: Model.Part[]) => boolean = (part, partList) => {
            return partList.some( (selPart) => {
                if (selPart.partId === part.partId) {
                    return true;
                }
            });
        };

        // 缓存串part的值，例如 1，2，3，4，6，8，9
        // 此时如果选中了【4，6】那么这些part应当缓存起来
        let bunchPart: Model.Part[] = [];
        sortSourceGroup.forEach( (part: Model.Part) => {
            if (includesPart(part, selPartGroup)) {
                bunchPart.push(part);
            } else {
                // 此时只有一个值，执行单个值的替换
                if (bunchPart.length === 1) {
                    const selPart = bunchPart[0];
                    const temp: number = selPart.layer;
                    selPart.layer = part.layer;
                    part.layer = temp;

                // 此时有一串值，执行多个值的替换
                } else if (bunchPart.length > 1) {
                    // 取得当前值
                    const temp = part.layer;
                    // 将需要当前的part与串part的第一个替换
                    part.layer = bunchPart[0].layer;
                    const len = bunchPart.length;
                    // 更新串part的layer
                    for (let i = 0; i < len; i++) {
                        // 这里主要是区别当前的操作，操作需要改变的串part数值
                        if (way === 'lower') {
                            bunchPart[i].layer = temp + (len - i - 1);
                        } else if (way === 'upper') {
                            bunchPart[i].layer = temp - (len - i - 1);
                        }
                    }
                }
                bunchPart = [];
            }
        });
    }
}
