/**
 * store for part relative to pageManage.
 * 主要是用于操作part
 */
export default {
	/**
     * 初始化页面 
     */
    initPage(state: Screen.state) {

    },

    /**
     * 更新当前选中的元素
     */
    // update_currentPart(state: Screen.state, partId: string ){
    //     state.pageManage.updateCurrentPart( partId );
    // },

    /**
    * 更新当前选中的元素
    */
    clear_currentPart( state: Screen.state ){
        state.pageManage.clearCurrentPart();
    },

    /**
     * 新增元素
     */
    add_part(state: Screen.state, dragInfo: any) {
        state.pageManage.addPart(dragInfo);
    },

    /**
     * 新增元素
     */
    add_part_his(state: Screen.state, dragInfo: any) {
        state.pageManage.addPart(dragInfo);
    },

    /**
     * 新增元素
     */
    update_part(state: Screen.state, payload: any) {
        const part = payload.clonePart;
        const partIndex = payload.partIndex;
        state.pageManage.updatePart(part, partIndex);
    },
    
	/**
     * 移除元素
     */
    remove_part(state: Screen.state, partList: any) {
        state.pageManage.removePart(partList);
    },

    /**
     * 移除元素,历史记录版本
     */
    remove_part_his(state: Screen.state, partList: any) {
        state.pageManage.removePart(partList);
    },
    
	/**
     * 清空元素
     */
    clear_part(state: Screen.state) {
        state.pageManage.clearPart();
    },

    /**
     * 复制元素
     */
    copy_part(state: Screen.state) {
        state.pageManage.copyPage();
    }
};
