declare const jwerty: any;
// declare const Mousetrap: any;
import KeyCodeObject from './keyCodeObject';
import keyCodeList from 'screenCfg/event/keyCodeConfig';

export default class KeyCodeEvent {

    // fixedme 存放所有keycode的事件map，这里应该是个map
    public keyCodeListener: KeyCodeObject[] = [];

    /**
     * 这里为什么会给每个keyCode实例化一个类，
     * 是因为可能需要销毁每个keyCodeObject监听，防止内存泄漏
     */
    constructor() {
        const formatKcList = this.formatKeyCodeList(_.cloneDeep(keyCodeList));
        this.initKeyCodeObject( formatKcList );
    }

    /**
     * 获取指定名称的keycode对象
     */
    public getKeyCodeObject( keyCodeObject ): KeyCodeObject | null {
        const { eventName, handle, context } = keyCodeObject;
        const findKeyCode = this.keyCodeListener.find( (item: KeyCodeObject) => {
            return item.eventName === eventName;
        });
        return findKeyCode ? findKeyCode : null;
    }

    /**
     * 为指定名称的keycode对象绑定事件
     * 采用jwerty插件库，压缩1.5K
     */
    public bindKeyCodeObject( kcObjtArg: KeyCodeObject ): void {
        const curKCObj: KeyCodeObject | null = this.getKeyCodeObject(kcObjtArg);
        if (!curKCObj) {
            return;
        }
        // Mousetrap.bind(curKCObj.keyCode,  kcObjtArg.handle);
        jwerty.key( curKCObj.keyCode, kcObjtArg.handle || false);
    }

    /**
     * 为指定名称的keycode对象绑定事件
     */
    public bindAllKeyCodeObject( cb: (event: MouseEvent, eventName: string) => void ): void {
        if (!cb) {
            return;
        }
        for (const keyCode of this.keyCodeListener) {
            jwerty.key( keyCode.keyCode, (event: MouseEvent) => { cb(event, keyCode.eventName) });
        }
    }

    /**
     * 为指定名称的keycode对象解绑事件
     */
    public unbindKeyCodeObject( keyCodeList: KeyCodeObject[] ): void {
        keyCodeList.forEach( ( keyCodeObj: KeyCodeObject ) => {
            jwerty.key( keyCodeObj.keyCode, false);
        });
        
    }

    /**
     * 为所有的keycode对象解绑事件
     * how to write, not support api...
     * https://stackoverflow.com/questions/9012846/jwerty-key-unbinding
     */
    public unbindAllKeyCodeObject(): void {
        this.keyCodeListener.forEach( (keyCodeObj: KeyCodeObject) => {
            jwerty.key( keyCodeObj.keyCode, false);
        });
    }

    /**
     * 格式化不同格式的配置项为普通的数组
     * @private
     * @param {any[]} keyCodeList 
     * @returns {any[]} 
     */
    private formatKeyCodeList(keyCodeList: any[]): any[] {
        const keyCodeFormat: any[] = [];
        keyCodeList.map( (keyCodeItem: any) => {
            keyCodeFormat.push(...keyCodeItem.value);
        });
        return keyCodeFormat;
    }

    /**
     * 实例化每个配置为类
     * @private
     * @param {any[]} keyCodeList 
     * @returns {any[]} 
     */
    private initKeyCodeObject( formatKcodeList: any[]) {
        formatKcodeList.forEach( (kcObject: any) => {
            // 存在keyCode的才需要添加keyup事件，例如lib就不用
            if (kcObject.keyCode) {
                const keyCode: KeyCodeObject = new KeyCodeObject( kcObject );
                this.keyCodeListener.push( keyCode );
            }
        });
    }
}
