



import Vue from 'vue';
import { mapState } from 'vuex';
import _ from 'lodash';
export default Vue.extend({
    name: 'RubberBand',
    data() {
        return {
            // 鼠标移动与原始坐标形成的区域, 可视层
            selectRectPos: {
                height: 0,
                width: 0,
                left: 0,
                top: 0,
            },
            // 鼠标在dom中的真实位置, dom层
            selectDomPos: {
                domX: 0,
                domY: 0,
                // br右下 bl左下 tl左上 tr右上
                mode: 'br',
            },
            // 鼠标点击的原始坐标
            selectOriginPos: {
                x: 0,
                y: 0,
            },
        };
    },
    props: {
        rubberBandShow: {
            type: Boolean,
            default: false,
        }
    },
    computed: {
        ...mapState({
            basePartList: (state: any) => state.screen.pageManage.partContainer,
        }),
        projectObj(): Model.Project {
            return this.$store.getters.projectObj;
        },
        panelScale(): number { 
            return this.projectObj.panelScale;
        },
        selectRectStyle(): any {
            return {
                height: this.selectRectPos.height + 'px',
                width: this.selectRectPos.width + 'px',
                left: this.selectRectPos.left + 'px',
                top: this.selectRectPos.top + 'px',
            };
        },
        calScale(): number {
            return this.panelScale / 100;
        },
        rubberBandMode(): number {
            return this.projectObj.rubberBandMode;
        },
    },
    methods: {
        /**
         * 初始化rubberband的图形信息
         * 用于canvas mousedown事件
         */
        initRubberBandPos(event) {
            // 初始化鼠标可视层位置
            this.selectRectPos.left = (event.clientX || 0);
            this.selectRectPos.top = (event.clientY || 0);
            this.selectOriginPos.x = (event.clientX || 0);
            this.selectOriginPos.y = (event.clientY || 0);

            // 初始化鼠标dom层位置
            const domOffSet = $('#canvas').offset();
            const x = (event.x - domOffSet.left) / this.calScale;
            const y = (event.y - domOffSet.top) / this.calScale;
            this.selectDomPos.domX = (x || 0);
            this.selectDomPos.domY = (y || 0);
        },

        /**
         * 实施计算rubberBand的图形，并实时更新dom
         * 用于canvas mousemove事件
         */
        calculateRectPosition(event) {
            const originX: number = this.selectOriginPos.x;
            const originY: number = this.selectOriginPos.y;
            const moveX: number = event.clientX;
            const moveY: number = event.clientY;
            const calX: number = moveX - originX;
            const calY: number = moveY - originY;
            const abs = Math.abs;
    
            // 根据用户的动作，实时更新拖拽的rect的框
            if (calX < 0 && calY < 0) {
                // 左上角拉动
                this.selectRectPos.height = abs(calY);
                this.selectRectPos.width = abs(calX);
                this.selectRectPos.left = moveX;
                this.selectRectPos.top = moveY;
                
                // 记录用户鼠标动作方向的mode值
                this.selectDomPos.mode = 'tl';
            } else if (calX > 0 && calY < 0) {
                // 右上角拉动
                this.selectRectPos.height = abs(calY);
                this.selectRectPos.width = calX;
                this.selectRectPos.top = moveY;

                // 记录用户鼠标动作方向的mode值
                this.selectDomPos.mode = 'tr';
            } else if (calX > 0 && calY > 0) {
                // 右下角拉动
                this.selectRectPos.height = calY;
                this.selectRectPos.width = calX;

                // 记录用户鼠标动作方向的mode值
                this.selectDomPos.mode = 'br';
            } else if (calX < 0 && calY > 0) {
                // 左下角拉动
                this.selectRectPos.height = calY;
                this.selectRectPos.width = abs(calX);
                this.selectRectPos.left = moveX;

                // 记录用户鼠标动作方向的mode值
                this.selectDomPos.mode = 'bl';
            }
        },

        /**
         * 重置rubberband的图形信息
         * 用于canvas mousedup事件
         */
        resetRubberBandPos() {
            this.selectRectPos.height = 0;
            this.selectRectPos.width = 0;
            this.selectRectPos.left = 0;
            this.selectRectPos.top = 0;

            this.selectDomPos.domX = 0;
            this.selectDomPos.domY = 0;
            this.selectDomPos.mode = 'br';
        },

        /**
         * 检测包含在rect框内的元素
         * @param {PartParentDom}  父节点的dom
         * @param {Scale}  缩放大小1~200?
         * @returns 
         */
        includePartInRect() {
            const scale: number = this.panelScale / 100;
            // 小数点位数
            const roundIndex: number = 6;
            // 缓存在框线选择内的part
            const innerPart: any[] = [];
            // 缓存每个part虚拟（矩形）的点的最值
            const calPartArr: any[] = [];
            // 缓存每个被锁定的part
            const lockPartArr: any[] = [];
            // 缓存四个点的数据
            const rect: any = {
                left: 0,
                right: 0,
                top: 0,
                bottom: 0,
            };

            const vHeight = this.selectRectPos.height;
            const vWidth = this.selectRectPos.width;
            const domX = this.selectDomPos.domX;
            const domY = this.selectDomPos.domY;

            // 计算底层真实的rubberband计算
            switch (this.selectDomPos.mode) {
                case 'br':
                    // 右下，不处理
                    break;
                case 'bl':
                    // 左下
                    this.selectDomPos.domX = domX - vWidth / scale;
                    break;
                case 'tl':
                    // 左上
                    this.selectDomPos.domX = domX - vWidth / scale;
                    this.selectDomPos.domY = domY - vHeight / scale;
                    break;
                case 'tr':
                    // 右上
                    this.selectDomPos.domY = domY - vHeight / scale;
                    break;
            }
            // 获取到真实的rubberband位置信息
            rect.top = _.round(this.selectDomPos.domY, roundIndex);
            rect.bottom = _.round((this.selectDomPos.domY  + vHeight / scale), roundIndex);
            rect.left = _.round((this.selectDomPos.domX), roundIndex);
            rect.right = _.round((this.selectDomPos.domX + vWidth / scale), roundIndex);
            
            // const viewPortDom: any = document.querySelector(`.no-zoom-area`);
            // const partParentDom = this.$util.offset(viewPortDom);
            // const top: number = partParentDom.top;
            // const left: number = partParentDom.left;
            // thanks for https://stackoverflow.com/questions/21717001/
            // html5-canvas-get-coordinates-after-zoom-and-translate#
            // thanks for http://jsfiddle.net/m1erickson/HwNp3/

            // rect.top = _.round((this.selectRectPos.top - top) / scale, roundIndex);
            // rect.bottom = rect.top + _.round(this.selectRectPos.height / scale, roundIndex);
            // rect.left = _.round((this.selectRectPos.left - left) / scale, roundIndex);
            // rect.right = rect.left + _.round(this.selectRectPos.width / scale, roundIndex);

            // 定义了相交选中的算法
            const defIntersect = (calPart: any): boolean => {
                if (
                    calPart.xMax > rect.left &&
                    calPart.yMax > rect.top &&
                    calPart.xMin < rect.right &&
                    calPart.yMin < rect.bottom
                ) {
                    return true;
                }
                return false;
            };

            // 定义了包含选中的算法
            const defInclude = (calPart: any): boolean => {
                if (
                    calPart.xMin > rect.left &&
                    calPart.xMax < rect.right &&
                    calPart.yMin > rect.top &&
                    calPart.yMax < rect.bottom
                ) {
                    return true;
                }
                return false;
            };

            // 遍历当前页面所有的图形元素，是否满足rubberband的选择范围
            this.basePartList.map( (item) => {
                // item in the rect range, except locked
                const part = item.partStyle;
                const partLocked = item.isLocked;
                const calPart = this.reactMaxOrMin(part);

                // 1为相交选中 0为包含选中
                if (
                    this.rubberBandMode === 1 &&
                    defIntersect(calPart)
                ) {
                    if (partLocked) {
                        lockPartArr.push( item );
                    } else {
                        innerPart.push( item );
                        calPartArr.push( calPart );
                    }
                } else if (
                    this.rubberBandMode === 0 &&
                    defInclude(calPart)
                ) {
                    if (partLocked) {
                        lockPartArr.push( item );
                    } else {
                        innerPart.push( item );
                        calPartArr.push( calPart );
                    }
                }
            });

            const isMultiPart = innerPart.length > 1 ? true : false;
            const isSinglePart = innerPart.length === 1 ? true : false;
            return {
                isMultiPart,
                isSinglePart,
                calPartArr,
                lockPartArr,
                innerPart,
            };
        },
        /**
         * 构建rubberband选中后，所有part外围的矩形
         */
        buildPartGroup(calPartArr): any {
            let xMax: number;
            let xMin: number;
            let yMax: number;
            let yMin: number;

            calPartArr.map( (item, index ) => {
                // 初始化属性
                if (index === 0) {
                    xMin = item.xMin;
                    yMin = item.yMin;
                    xMax = item.xMax;
                    yMax = item.yMax;
                }
                if ( item.xMax > xMax ) {
                    xMax = item.xMax;
                }
                if ( item.xMin < xMin ) {
                    xMin = item.xMin;
                }
                if ( item.yMax > yMax ) {
                    yMax = item.yMax;
                }
                if ( item.yMin < yMin ) {
                    yMin = item.yMin;
                }
            });

            return {
                x: xMin || 0,
                y: yMin || 0,
                width: xMax - xMin || 0,
                height: yMax - yMin || 0,
            };
        },
        /**
         * 更新选中数组，改方法不需要经过rubberBand
         * 主要应用与图层多选模块
         * 通过中间件的事件方式触发(layer)
         */
        updateSelectGroup( selPartGroup: Model.Part[] ): void {
            const calPartArr: any[] = [];
            // 获取选中元素，计算每个元素虚拟rect的位置
            selPartGroup.map( ( part: Model.Part ) => {
                // part in the rect range, except locked
                const partStyle = part.partStyle;
                const partLocked = part.isLocked;
                const calPart = this.reactMaxOrMin(partStyle);
                if (!partLocked) {
                    calPartArr.push( calPart );
                }
            });
            // 计算外围的框线
            const rectCoord = this.buildPartGroup(calPartArr);
            // 更新框线显示和选中的partGroup
            return rectCoord;
        },

        /**
         * 计算每个part在旋转后各个点形成矩形后，最大及最小的x,y
         */
        reactMaxOrMin( partStyle: any ): { xMax: number, xMin: number, yMax: number, yMin: number } {
            const { x, y, width, height, angle: partAngle } = partStyle;
            const xCenter = (width) / 2 + x;
            const yCenter = (height) / 2 + y;
            const xArr: any[] = [];
            const yArr: any[] = [];
            const partAllCoord = [
                [x, y],
                [x + width, y],
                [x, y + height],
                [x + width, y + height],
            ];
            for (const item of partAllCoord) {
                const payload = { 
                    x0: item[0],
                    y0: item[1], 
                    xcenter: xCenter, 
                    ycenter: yCenter,
                    angle: partAngle,
                };
                const coord: any = this.calRoateRect(payload);
                xArr.push( coord.x );
                yArr.push( coord.y );
            }
            const xmax = _.max(xArr) || 0;
            const xmin = _.min(xArr) || 0;
            const ymax = _.max(yArr) || 0;
            const ymin = _.min(yArr) || 0;
            return {
                xMax: xmax,
                xMin: xmin,
                yMax: ymax,
                yMin: ymin,
            };
        },

        calRoateRect( payload ): { x: number , y: number } {
            const { x0, y0, xcenter, ycenter, angle  } = payload;
            const piAngle = Math.PI / 180 * angle;
            const X = (x0 - xcenter) * Math.cos(piAngle) - (y0 - ycenter) * Math.sin(piAngle) + xcenter; 
            const Y = (x0 - xcenter) * Math.sin(piAngle) + (y0 - ycenter) * Math.cos(piAngle) + ycenter;
            return {	
                x: X, 
                y: Y,
            };
        },
    },
});
