import AbstractOption from '../../../common/abstractOption'
import updatePropsDeep from 'utils/initProps'

/**
 * 基础报表的列配置项
 */
export default class TableColumn extends AbstractOption {

  public config: any;

  constructor(opts?: TableReport.Column) {
    super();
    this.config = this.init(opts);
  }

  public init(opts?: TableReport.Column) {
    const source: TableReport.Column = {
      line: {
        width: 1,
        color: '#ccc',
        style: 'solid'
      }
    };
    return updatePropsDeep(source, opts);
  }

  public computed(): void { }

}