import Cmpt from './cmpt';
import UUID from '../../utils/uuid';
import { CmptGroup } from './cmptGroup';
interface MoveCmptArgs {
    cmpt: Cmpt; 
    sourceCmptGroup: Model.CmptGroup; 
    targetCmptGroup: Model.CmptGroup;
}
interface DelCmptArgs {
    cmpt: Cmpt; 
    targetCmptGroup: Model.CmptGroup; 
}

export default class CmptManage {
    /**
     * 默认的组件列表
     */
    public defCmptList: Model.CmptGroup[];

    /**
     * 用户定义的组件列表
     */
    public cmptGroupList: Model.CmptGroup[];


    public currentCmpt: Cmpt;


    constructor() {
        this.cmptGroupList = [];
        this.defCmptList = [];
    }

    /**
     * 新增组件容器
     */
    public addCmptGroup( groupName: string | Model.CmptGroup ): void {
        if (!groupName) {
            return;
        }
        let cmptGroup: any = {};
        const cmptGroupId = this.createdCmptGroupId();
        if (_.isString(groupName)) {
            cmptGroup = {
                cmptGroupId,
                groupName,
            };
        }
        // groupName此时是个对象
        if (_.isObject(groupName)) {
            Object.assign(cmptGroup, {
                cmptGroupId,
            }, groupName);
        }
        this.cmptGroupList.push( new CmptGroup(cmptGroup) );
    }

    /**
     * 编辑组件容器
     */
    public editCmptGroup( cmptGroupArg: { groupName: string, isEditor: boolean, cmptGroup: CmptGroup } ): void {
        if (!cmptGroupArg) {
            return;
        }

        const { groupName, isEditor, cmptGroup } = cmptGroupArg;
        const cmptGroupId = cmptGroup.cmptGroupId;
        const index = this.findCmptGroup(cmptGroupId, true);
        if (index === -1) {
            return;
        }

        const copyCmptGroup = _.cloneDeep(this.cmptGroupList[index]);
        if (groupName) {
            copyCmptGroup.groupName = groupName;
        }
        if (!_.isNil(isEditor)) {
            copyCmptGroup.isEditor = isEditor;
        }
        
        this.cmptGroupList.splice( index, 1 , copyCmptGroup);
    }

    /**
     * 移除组件容器
     */
    public removeCmptGroup( cmptGroup: CmptGroup ): void {
        if (!cmptGroup) {
            return;
        }
        const cmptGroupId = cmptGroup.cmptGroupId;
        const index = this.findCmptGroup(cmptGroupId, true);
        if (index === -1) {
            return;
        }
        this.cmptGroupList.splice( index, 1 );
    } 

    /**
     * 新增组件
     */
    public addCmpt( cmpt: Cmpt ): void {
        if (!cmpt) {
            return;
        }

        const cmptGroupId = cmpt.cmptGroupId;
        let cmptGroupTemp: any;
        if (cmpt.cmptGroupId) {
            cmptGroupTemp = this.findCmptGroup(cmptGroupId);
        }
        if (!cmptGroupTemp) {
            return;
        }

        const cmptGroupObj: CmptGroup = cmptGroupTemp;
        const extraInfo: any = {
            cmptId: this.createdCmptId(),
            projectId: '',
        };
        cmpt = { ...extraInfo, ...cmpt };
        cmptGroupObj.cmptGroup.push( new Cmpt(cmpt) );
    }

    /**
     * 移动
     */
    public moveCmpt( cmptArgs: MoveCmptArgs ): void {
        
        
        const {
            cmpt, 
            sourceCmptGroup, 
            targetCmptGroup,
        } = cmptArgs;
        if (!(cmpt && sourceCmptGroup && targetCmptGroup)) {
            return;
        }
        
        const removeCmpt = _.remove(sourceCmptGroup.cmptGroup, (cmptItem: Model.Cmpt) => {
            if (cmptItem.cmptId === cmpt.cmptId) {
                return true;
            }
        });

        if (Array.isArray(removeCmpt)) {
            targetCmptGroup.cmptGroup.push(...removeCmpt);
        }

        // 更新容器中原始数据
        const sourceIndex = this.findCmptGroup(sourceCmptGroup.cmptGroupId, true);
        this.cmptGroupList.splice(sourceIndex, 1, sourceCmptGroup);

        const tagIndex = this.findCmptGroup(targetCmptGroup.cmptGroupId, true);
        this.cmptGroupList.splice(tagIndex, 1, targetCmptGroup);
    }


    /**
     * 移除组件
     * @cmpt 组件数组
     */
    public removeCmpt( cmptArgs: DelCmptArgs ): void {
        const {
            cmpt,
            targetCmptGroup,
        } = cmptArgs;
        
        // 将要删除的所有组件id缓存到数组
        const cmptIdArray: string[] = [];
        if (!Array.isArray(cmpt)) {
            cmptIdArray.push(cmpt.cmptId);
        } else {
            const idTempArr = cmpt.map( (cmptIt) => {
                return cmptIt.cmptId;
            });
            cmptIdArray.concat(idTempArr);
        }
        
        // 删除所有缓存组件id的所有组件
        _.remove(targetCmptGroup.cmptGroup, (cmptItem: Model.Cmpt) => {
            if (cmptIdArray.includes(cmptItem.cmptId)) {
                return true;
            }
        });

        // 更新容器中原始数据
        const sourceIndex = this.findCmptGroup(targetCmptGroup.cmptGroupId, true);
        this.cmptGroupList.splice(sourceIndex, 1, targetCmptGroup);

    }

    /**
     * 编辑组件，目前不会涉及
     */
    public editCmpt( cmptArgs: DelCmptArgs ): void {
        const {
            cmpt,
        } = cmptArgs;
        const { cmptName, isEditor, cmptId, cmptGroupId } = cmpt;
        
        // 找到组件容器
        const parentCmptGroup = this.cmptGroupList.find( (cmptGroup: Model.CmptGroup) => {
            return cmptGroup.cmptGroupId === cmptGroupId;
        });

        // 找到当前cmpt数组中的位置
        const index = this.findCmpt(cmptId, parentCmptGroup.cmptGroup, true);
        if (index === -1) {
            return;
        }

        // 更新当前cmpt的属性
        const copyCmpt = _.cloneDeep(parentCmptGroup.cmptGroup[index]);
        if (!_.isNil(cmptName)) {
            copyCmpt.cmptName = cmptName;
        }
        if (!_.isNil(isEditor)) {
            copyCmpt.isEditor = isEditor;
        }
        parentCmptGroup.cmptGroup.splice( index, 1 , copyCmpt);
    }

    /**
     * 复制组件
     */
    public copyCmpt( cmpt: Cmpt ): void {

    }

    /**
     * 获取组件的纯数据
     * 
     * @returns {Model.CmptModule} 
     */
    public getPureData(): Model.CmptModule {
        const cmptGroupList: Model.CmptGroup[] = [];
        if (this.cmptGroupList) {
            this.cmptGroupList.forEach( (cmptGroup: Model.CmptGroup) => {
                cmptGroupList.push(this.getCmptGroupAllPros(cmptGroup));
            });
        }
        return {
            cmptGroupList,
            defCmptList: [],
        };
    }

    /**
     * 设置组件的纯数据
     * 
     * @returns {Model.CmptModule} 
     */
    public setPureData( pureData: Model.CmptModule ) {
        const cmptGroupList: Model.CmptGroup[] = pureData.cmptGroupList.map( (cmptGroup) => {
            return this.setCmptGroupAllPros(cmptGroup);
        });
        this.cmptGroupList = cmptGroupList;
        this.defCmptList = pureData.defCmptList;
    }

    public destroyed() {
        this.cmptGroupList = [];
        this.defCmptList = [];
    }

    /**
     * 从组件块列表中查找组件块
     * 
     * @private
     * @param {cmptGroup} cmptGroup 组件块
     * @param {boolean} [isIndex=false] 是否返回index值，true返回index，反之返回对象
     */
    private findCmptGroup( cmptGroupId: string ): CmptGroup;
    private findCmptGroup( cmptGroupId: string , isIndex: boolean ): number;
    private findCmptGroup( cmptGroupId: string , isIndex: boolean = false ): Model.CmptGroup | number {
        if (this.cmptGroupList.length === 0) {
            return null;
        }
        
        let cmptGroupTemp: Model.CmptGroup;
        let cmptGroupKey: number;
        this.cmptGroupList.some( (item, key) => {
            if (item.cmptGroupId === cmptGroupId) {
                cmptGroupKey = key;
                cmptGroupTemp = item;
                return true;
            }
        });

        return isIndex ? cmptGroupKey : cmptGroupTemp;
    }

    /**
     * 从组件别表中查找组件
     * 
     * @param {Cmpt} cmpt 要查找的组件
     * @param {CmptGroup} cmptGroup 组件列表
     * @param {boolean} [isIndex=false] 
     */
    private findCmpt( cmptId: string, cmptGroup: Model.Cmpt[] ): Model.Cmpt;
    private findCmpt( cmptId: string, cmptGroup: Model.Cmpt[] , isIndex: boolean ): number;
    private findCmpt( cmptId: string, cmptGroup: Model.Cmpt[] , isIndex: boolean = false ): Model.Cmpt | number{
        if (cmptGroup.length === 0) {
            return null;
        }
        
        let cmptTemp: Model.Cmpt;
        let cmptKey: number;
        cmptGroup.some( (item, key) => {
            if (item.cmptId === cmptId) {
                cmptKey = key;
                cmptTemp = item;
                return true;
            }
        });

        return isIndex ? cmptKey : cmptTemp;
    }

    /**
     * 设置组件列表的纯数据（用于读取）
     * 
     * @param cmptGp 组件列表实例
     */
    private setCmptGroupAllPros(cmptGp: Model.CmptGroup): Model.CmptGroup {
        const cmptGroupIns = new CmptGroup(cmptGp);
        cmptGroupIns.cmptGroup.map( (cmpt) => {
            return this.setCmptAllPros(cmpt);
        });
        return cmptGroupIns;
    }

    /**
     * 获取组件列表的纯数据（用于保存）
     * 
     * @param cmptGp 组件列表实例
     */
    private getCmptGroupAllPros(cmptGp: Model.CmptGroup): Model.CmptGroup {
        const cmptGroup: Cmpt[] = [];
        if (cmptGp.cmptGroup.length) {
            cmptGp.cmptGroup.forEach( (cmpt: Model.Cmpt) => {
                cmptGroup.push(this.getCmptAllPros(cmpt));
            });
        }
        return {
            size: cmptGp.size,
            groupName: cmptGp.groupName,
            cmptGroup,
            cmptGroupId: cmptGp.cmptGroupId,
            canDel: cmptGp.canDel,     
            isEditor: cmptGp.isEditor,
            isHover: cmptGp.isHover,
        };
    }

    /**
     * 设置取组件实例的纯数据（用于读取）
     * 
     * @param cmpt 组件实例
     */
    private setCmptAllPros( cmpt: Model.Cmpt ):  Model.Cmpt {
        return new Cmpt(cmpt);
    }

    /**
     * 获取组件实例的纯数据（用于保存）
     * 
     * @param cmpt 组件实例
     */
    private getCmptAllPros(cmpt: Model.Cmpt): Model.Cmpt {
        return {
            imgSrc: cmpt.imgSrc,
            cmptId: cmpt.cmptId,
            cmptName: cmpt.cmptName,
            cmptGroupId: cmpt.cmptGroupId,
            partSource: cmpt.partSource,     
            projectId: cmpt.projectId,
        };
    }

    private createdCmptId(): string {
        return 'cmpt_' + new UUID().init();
    }

    private createdCmptGroupId(): string {
        return 'cmptGroup_' + new UUID().init();
    }

}
