/**
 * 主要是用于操作project
 */
const mutations = {
    /**
     * 设置项目名字
     */
    setProName(state: Screen.state, proName: string) {
        state.projectObj.setProName(proName);
    },
    
    /**
     * 设置项目保存时间
     */
    setSaveTime(state: Screen.state, proTime: string) {
        state.projectObj.setSaveTime(proTime);
    },

    /**
     * 设置网格大小
     */
    setGrid(state: Screen.state, grid: number) {
        state.projectObj.setGrid(grid);
    },

    /**
     * 设置网格是否显示
     */
    setGridShow(state: Screen.state, gridShow: boolean) {
        state.projectObj.setGridShow(gridShow);
    },

    /**
     * 设置rubberBand选中模式
     */
    setRubberBandMode(state: Screen.state, rubberBandMode: string) {
        state.projectObj.setRubberBandMode(rubberBandMode);
    },

    /**
     * 设置页面缩放类型
     */
    setScaleMode(state: Screen.state, scaleMode: string) {
        state.projectObj.setScaleMode(scaleMode);
    },

    /**
     * 更新屏幕缩放值
     */
    setPanelScale(state: Screen.state, panelScale: number) {
        state.projectObj.setPanelScale(panelScale);
    },

    setDocClient(state: Screen.state, payload: any) {
        state.projectObj.setDocClientH(payload.h);
        state.projectObj.setDocClientW(payload.w);
    },

    /**
     * 更新屏幕的高度
     */
    setScreenH(state: Screen.state, screenH: number) {
        state.projectObj.setScreenH(screenH);
    },

    /**
     * 更新屏幕的宽度
     */
    setScreenW(state: Screen.state, screenW: number) {
        state.projectObj.setScreenW(screenW);
    }
};

const getter = {
    projectObj: (state: Screen.state) => state.projectObj
};

export default {
    mutations,
    getter,
};
