
export default class PageState {

    protected static ableTag: boolean = false;

    public partContainer: any[];

    public stateId: string;

    public stateName: string;

    /**
     * 记录样式配置
     */
    public styleConfig: any;

    /**
     * 记录数据配置
     */
    public dataConfig: any;

    private defaultState: boolean = false;

    private globalState: boolean = false;

    constructor(stateId, name) {
        this.stateId = stateId;
        this.stateName = name;
    }

    public destroyed(): void {

    }

    public setStateName(stateName): void {
        this.stateName = stateName;
    }

    public getStateName(): string {
        return this.stateName;
    }

    public setGlobalState(): void {
        this.globalState = true;
    }

    public getGlobalState(): boolean {
        return this.globalState;
    }

    public setDefaultState(): void {
        this.defaultState = true;
    }

    public getDefaultState(): boolean {
        return this.defaultState;
    }

    public isGlobalState(): boolean {
        return this.globalState;
    }

    public isDefaultState(): boolean {
        return this.defaultState;
    }
}
