









import Vue from 'vue'

export default Vue.extend({
  name: 'select-el',
  props: {
    opts: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  data() {
    return {
      initParam: {
        formOption: []
      }
    }
  },
  computed: {
    selectValue: {
      get(): any {
        return this.opts.model || ''
      },
      set(val: any): any {
        return val
      }
    },
    param(): any {
      return Object.assign(this.initParam, this.opts)
    },
    options(): any {
      return this.param.formOption
    }
  },
  methods: {
    handleChange(val: any) {
      this.$emit('on-data-change', this.param, val)
    }
  }
})
