/**
 * part参考线样式类
 */
export default class RefLine {
    /**
     * 图形元素的x坐标
     */
    public top: number;

    /**
     * 图形元素的y坐标
     */
    public bottom: number;

    /**
     * 图形元素的x坐标
     */
    public left: number;

    /**
     * 图形元素的y坐标
     */
    public right: number;

    /**
     * 图形元素的宽度
     */
    public width: number;

    /**
     * 图形元素的高度
     */
    public height: number;

    constructor(refLineArg: Model.RefLineStyle) {
        this.setPartAllProps(refLineArg);
    }

    public getPartAllProps(): Model.RefLineStyle {
        return {
            top: this.top,
            bottom: this.bottom,
            left: this.left,
            right: this.right,
            width: this.width,
            height: this.height,
        };
    }

    public setPartAllProps(refLineArg: Model.RefLineStyle) {
        if (!_.isObject(refLineArg)) {
            return;
        }

        const { top, bottom, left, right, width, height } = refLineArg;
        this.top = top;
        this.bottom = bottom;
        this.left = left;
        this.right = right;
        this.width = width;
        this.height = height;
    }

}
