import background from '../config/background';
import border from '../config/border';
import borderRadius from '../config/borderRadius';
import font from '../config/font';
import img from '../config/img';
import shadow from '../config/shadow';
import textStyle from '../config/textStyle';
// 基础配置项适配器
import OptionAdapter from '../../../common/adapter/baseOptionAdapter';

/**
 * 基础图形配置项适配器
 */
export default class BaseShapeAdapter extends OptionAdapter {

  constructor() {
    super();
  }

  public getDefaultOption(type: string): object {
    const map: any = {
      'baseText': {
        textStyle,
        font,
        shadow
      },
      'baseRect': {
        background,
        borderRadius,
        border,
        shadow
      },
      'baseCircle': {
        background,
        border,
        shadow
      },
      'baseLine': {
        border,
        shadow
      },
      'baseImg': {
        img,
        background,
        borderRadius,
        border,
        shadow
      }
    };
    return map[type] || {};
  }

}