


















































import Vue from 'vue';
import baseCell from '../../panel/part/baseCell.vue';
import Transform from './transform/transform.vue';
import PartSelection from './partSelection/partSelection.vue';
import RubberBand from './rubberband/rubberBand.vue';
import lock from './lock/lock.vue';
import Hover from './hover/hover.vue';
import EventCtl from './event/event.vue';
import ContextMenu from '../contextMenu/contextMenu.vue';
import Grid from './grid/grid.vue';

import KeyBoard from './keyBoard/keyBoard.vue';
import ClipBoard from './clipBoard/clipBoard.vue';
import Group from './group/group.vue';
import Layer from './layer/layer.vue';
import History from './history/history.vue';

import * as EditInter from '../edit/interface';
import { mapMutations, mapState, mapGetters, mapActions } from 'vuex';

export default Vue.extend({
    data() {
        return {
            isCanvasMouseDown: false,
            isPartMouseDown: false,
            ctmClickTarget: 'canvas',
            sreenBgColor: '#333',
            rubberBandShow: false,
            isHoverPart: false,

            showProSaveTime: true,
            showPagesName: true,
            showScreenCube: true,

            selectPartId: '',

            // 存储子组件
            selectorRef: {},
            rubberBandRef: {},
            hoverRef: {},
            lockRef: {},
            eventCtlRef: {},
            contextMenuRef: {},
        };
    },
    components: {
        RubberBand,
        ContextMenu,
        Hover,
        lock,
        baseCell,
        EventCtl,
        PartSelection,
        Transform,
        KeyBoard,
        ClipBoard,
        Group,
        Layer,
        History,
        Grid,
    },
    props: {
        moveHorizontal: {
            type: Number,
            default: 0,
        },
        moveVertical: {
            type: Number,
            default: 0,
        },
    },
    mounted() {
        this.handleRef();
        this.handleBus();
        this.handlePanelEvent();
    },
    computed: {
        ...mapState({
            pageManage: (state: any) => state.screen.pageManage,
            basePartList: (state: any) => state.screen.pageManage.partContainer,
        }),
        ...mapGetters([
            'currentPage',
        ]),
        projectObj(): Model.Project {
            return this.$store.getters.projectObj;
        },
        proSaveTime(): string {
            const proSaveTime = this.projectObj.saveTime || '未保存';
            return proSaveTime;
        },
        panelScale(): number { 
            return this.projectObj.panelScale;
        },
        divViewPort(): string { 
            return this.projectObj.divViewPort;
        },
        screenH(): number { 
            return this.projectObj.screenH;
        },
        screenW(): number { 
            return this.projectObj.screenW;
        },
        scaleScreenW(): number {
            return this.screenW * this.panelScale * 0.01;
        },
        docClientW(): number { 
            return this.projectObj.docClientW;
        },
        docClientH(): number { 
            return this.projectObj.docClientH;
        },
        partContainer(): any[] {
            return this.pageManage.partContainer;
        },
        transformObj(): string {
            return `scale(${this.panelScale * 0.01})`;
        },
        movePosition(): string {
            return `matrix(1, 0, 0, 1, ${this.moveHorizontal}, ${this.moveVertical})`;
        },
        screenStyle(): EditInter.ScreenStyle {
            return {
                'width': this.screenW + 'px',
                'height': this.screenH + 'px',
                'min-width': this.screenW + 'px',
                'min-height': this.screenH + 'px',
                'transition': 'all 0.8s ease-in-out',
                'transform': this.movePosition,
            };
        },
        noZoomStyle(): EditInter.NoZoomStyle {
            return {
                top: (100 - this.panelScale) / 2 + '%',
                left: (100 - this.panelScale) / 2 + '%',
                width: this.panelScale + '%',
                height: this.panelScale + '%',
            };
        },
        noZoomScreenStyle(): EditInter.NoZoomScreenStyle {
            return {
                width: this.screenW * this.panelScale * 0.01 + 'px',
                height: this.screenH * this.panelScale * 0.01 + 'px'
            };
        },
        screenCube(): string {
            return `${this.screenW}x${this.screenH} ${this.panelScale}%`;
        },
        pagesName(): string {
            return this.currentPage ? this.currentPage.pagesName : '未选中页面';
        },
        zoomStyle(): EditInter.ZoomStyle {
            return {
                transform: this.transformObj,
            };
        },
        zoomCanvasStyle(): EditInter.ZoomCanvasStyle {
            return {
                'width': this.screenW + 'px',
                'height': this.screenH + 'px',
                'background-color': this.sreenBgColor,
            };
        },
        viewPort_origin() {
            return {
                width: this.screenW + 'px',
                height: this.screenH + 'px',
            };
        },
    },
    watch: {
        scaleScreenW: _.debounce(function(newScreenW) {
            if (newScreenW < 550) {
                this.showProSaveTime = false;
                this.showPagesName = false;
                this.showScreenCube = false;
            } else {
                this.showProSaveTime = true;
                this.showPagesName = true;
                this.showScreenCube = true;
            }
        }, 300),
    },
    methods: {
        ...mapMutations([
            'addPagePart',
            'removePagePart',
            'clearPagePart',
            'copyPagePart',
            'setSelectedBox',

            'setReleasePart',
            // 'update_currentPart',
            // 'clear_currentPart',
            'add_part_all_listener',
            'remove_part_all_listener',
            'add_canvas_all_listener',
            'remove_canvas_all_listener',

            'destroyedCoreModel',
        ]),
        ...mapActions([
            // 'destroyedCoreModel',
        ]),
        // 初始化状态组件
        handleRef(isDestroy?: boolean): void {
            if (isDestroy) {
                this.selectorRef = null;
                this.rubberBandRef = null;
                this.hoverRef = null;
                this.lockRef = null;
                this.eventCtlRef = null;
                this.contextMenuRef = null;
                return;
            }
            this.selectorRef = this.$refs.selectorRef;
            this.rubberBandRef = this.$refs.rubberBandRef;
            this.hoverRef = this.$refs.hoverRef;
            this.lockRef = this.$refs.lockRef;
            this.eventCtlRef = this.$refs.eventCtlRef;
            this.contextMenuRef = this.$refs.contextMenuRef;
        },
        handleBus(isDestroyed?: boolean): void {
            const fucArray = [{
                    partContentMenu: this.partContentMenu,
                },
                {
                    selectPartEvent: this.selectPartEvent,
                },
                {
                    partMouseOver: this.partMouseOver,
                },
                {
                    partMouseOut: this.partMouseOut,
                },
                {
                    canvasCtm: this.canvasCtm,
                },
                {
                    updateSelGroup: this.updateSelGroup,
                },
            ];
            fucArray.map((event) => {
                const [
                    [eventName, eventWay],
                ] = Object.entries(event);
                if (isDestroyed) {
                    this.$root.busRef.$off(eventName);
                } else {
                    this.$root.busRef.$on(eventName, (part, domEvent) => {
                        if (eventWay) {
                            eventWay(part, domEvent);
                        }
                    });
                }
            });
        },

        // 主要是画布事件指定和删除
        handleCanvasEvent(isRemove: boolean = false) {
            const canvasListeners = [{
                domId: this.divViewPort,
                eventType: 'mousedown',
                handle: this.canvasMousedown,
            }, {
                domId: this.divViewPort,
                eventType: 'mousemove',
                handle: this.canvasMousemove,
            }, {
                domId: this.divViewPort,
                eventType: 'mouseup',
                handle: this.canvasMouseup,
            }];
            let handleCanvasClick: (evtListener: any) => void;
            if (!isRemove) {
                handleCanvasClick = this.eventCtlRef.addCanvasClickEvt;
            } else {
                handleCanvasClick = this.eventCtlRef.removeCanvasClickEvt;
            }
            // fixme 暂时解决了鼠标移除画布时，不触发mouseup事件，由body代触发
            this.addBodyEvent(isRemove);
            canvasListeners.forEach((evtListener: any ) => {
                // hack，mounted根据id拿不到dom，此时$el与domId是一样的
                if (isRemove && this.$el) {
                    evtListener.domId = this.$el;
                }
                handleCanvasClick(evtListener);
            });
        },
        // 为body添加事件
        addBodyEvent(isRemove?: boolean): void {
            if (!isRemove) {
                document.body.addEventListener('mouseup', (event) => {
                    this.canvasMouseup(event);
                });
            } else {
                document.body.removeEventListener('mouseup', (event) => {
                    this.canvasMouseup(event);
                });
            }
        },
        // 为画布操作事件
        handlePanelEvent(isDestroy?: boolean): void {
            if (isDestroy) {
                this.handleCanvasEvent(isDestroy);
                return;
            }
            this.handleCanvasEvent();
            
        },
        // 画布点击事件回调
        canvasMousedown(event: MouseEvent): any {
            // 设置transform为false, 这里设置是为了兼容画布的右击事件
            this.setSelectedBox(false);
            if (event.button !== 0) {
                return;
            }
            // 清除选中时候的user-select
            this.clearDragSelection();
            
            // 移除画布drop事件
            this.$root.busRef.$emit('dropUnbind');

            // 清除当前选中的Part对象数组
            this.setReleasePart(false);

            this.isCanvasMouseDown = true;
            this.rubberBandShow = true;
            this.selectorRef.deselect();
            this.rubberBandRef.initRubberBandPos(event);
        },
        canvasMousemove(event: MouseEvent) {
            if (!this.isCanvasMouseDown) {
                return;
            }
            this.rubberBandRef.calculateRectPosition(event);
        },
        canvasMouseup(event: MouseEvent) {
            if (!this.isCanvasMouseDown) {
                return;
            }
            this.isCanvasMouseDown = false;
            this.rubberBandShow = false;

            // 清除当前选中的lockPart
            this.lockRef.clearLockPart();

            // 返回rubberband区域的信息
            const checkedGroup = this.rubberBandRef.includePartInRect();
            
            // 存在勾选多个part时候，才需要构建rect	
            if (checkedGroup.isMultiPart) {

                // 返回计算后的外围矩形
                const rectCoord = this.rubberBandRef.buildPartGroup(checkedGroup.calPartArr);

                this.selectorRef.mapSelectorGroup(rectCoord, checkedGroup.innerPart);
                this.selectorRef.updateRubberBandCoord(rectCoord);

                // 显示方框
                this.setSelectedBox(true);
            }
            if (checkedGroup.isSinglePart) {
                const payload = checkedGroup.innerPart[0];
                this.selectorRef.mapSelectorPart(payload);
                this.setSelectedBox(true);
            }
            // rubberBand显示lock的part值，暂时不实现
            // this.lockRef.lockMapSelector(checkedGroup.lockPartArr);

            this.rubberBandRef.resetRubberBandPos();
            // 恢复画布drop事件
            this.$root.busRef.$emit('dropBind');
        },
        partContentMenu( part, event: MouseEvent): void {
            // 调试中的代码记得注释...
            // this.selectorRef.mapCtmPart(part);
            event.preventDefault();
            this.ctmClickTarget = 'part';
            this.contextMenuRef.handleShowCtm(true, event);
            
            // 设置释放part
            this.setReleasePart(true);
            // this.update_currentPart(part.partId);
            this.selectorRef.mapDifferPart(part);
            this.hoverRef.resetHoverPart();
            // isPartMouseDown
            this.setSelectedBox(true);
        },
        // 单击选中Part元素
        selectPartEvent(part: Model.Part, event: MouseEvent) {
            event.stopPropagation();

            // 设置选中part
            this.setReleasePart(false);

            // 记录partId
            this.selectPartId = part.partId;

            // 清除选中时候的user-select
            this.clearDragSelection();

            this.contextMenuRef.handleShowCtm(false, event);
            // 移除画布drop事件

            this.$root.busRef.$emit('dropUnbind');
            // 移除画布事件

            this.handlePanelEvent(true);
            // 移除mousemove的事件

            this.selectorRef.mapDifferPart(part);
            this.hoverRef.resetHoverPart();
            this.lockRef.updataLockState();
            this.isPartMouseDown = true;
            this.setSelectedBox(true);

        },
        selectPartEventUp() {
            if (!this.isPartMouseDown) {
                return;
            }
            
            this.isPartMouseDown = false;
            this.handlePanelEvent();
            // 恢复画布drop事件
            this.$root.busRef.$emit('dropBind');
            // 新增mousemove的事件

        },
        // transform停止事件时（鼠标松开），更新rubberband的样式
        transformUpdata(payload): void {
            this.selectorRef.mapTransformSelector(payload);

            this.selectPartEventUp();

            setTimeout(() => {
                // 设置释放part
                this.setReleasePart(true);
                // currentPart需要被移除
                // this.update_currentPart(this.selectPartId);
            }, 0);
        },
        // 实时更新part的公共样式
        partUpdate(payload) {
            this.$nextTick(() => {
                this.selectorRef.updataDifferPart(payload);
            });
        },
        partMouseOver(part) {
            if (this.isPartMouseDown) {
                return;
            }
            this.$nextTick( () => {
                if (this.hoverRef) {
                    this.hoverRef.handleHoverPart(part);
                }
            });
        },
        partMouseOut() {
            if (this.isPartMouseDown) {
                return;
            }
            this.$nextTick( () => {
                if (this.hoverRef) {
                    this.hoverRef.resetHoverPart();
                }
            });
        },
        canvasCtm(event) {
            this.ctmClickTarget = 'canvas';
        },
        /**
         * layer图层更新选中part
         */
        updateSelGroup( selPartGroup: Model.Part[], isMulti: boolean ): void {
            // 多选
            if (isMulti || selPartGroup.length > 1) {
                const rectCoord = this.rubberBandRef.updateSelectGroup(selPartGroup);
                this.selectorRef.mapSelectorGroup(rectCoord, selPartGroup);
                this.selectorRef.updateRubberBandCoord(rectCoord);
            } else {
            // 单选
                this.selectorRef.mapSelectorPart(selPartGroup[0]);
                this.setSelectedBox(true);
            }
        },
        /**
         * 清楚拖拽时，选中文本
         */
        clearDragSelection(): void {
            !+'\v1' ? document.selection.empty() : window.getSelection().removeAllRanges();
        },
    },
    beforeDestroy() {
        // 销毁事件
        // this.handlePanelEvent(true);
        // this.handleBus(true);
        // this.handleRef(true);
        // this.destroyedCoreModel();
        // console.log(this.$store.state);
        
    },
});
