import group from './group';
import event from './event';
import transform from './transform';
import partSelection from './partSelection';
import rubberBand from './rubberBand';
import hoverPart from './hoverPart';
import lock from './lock';
import clipBoard from './clipBoard';
import layer from './layer';
import refLine from './refLine';
import dataSource from './dataSource';

const mutations = {
    ...group.mutations,
    ...event.mutations,
    ...transform.mutations,
    ...partSelection.mutations,
    ...rubberBand.mutations,
    ...hoverPart.mutations,
    ...lock.mutations,
    ...clipBoard.mutations,
    ...layer.mutations,
    ...refLine.mutations,
    ...dataSource.mutations,
    setPartId(state: Screen.state, partId: string) {
        state.editor.setPartId(partId);
    },
    resetPartId(state: Screen.state) {
        state.editor.resetPartId();
    }
};

const getter = {
    ...event.getter,
    ...transform.getter,
    ...partSelection.getter,
    ...rubberBand.getter,
    ...hoverPart.getter,
    ...lock.getter,
    ...clipBoard.getter,
}

const action = {
    // ...canvas.actions,
};

export default {
    mutations,
    action,
    getter
}