
import Vue from 'vue'
import RectShape from './rect/rect.vue'
import LineShape from './line/line.vue'
import CircleShape from './circle/circle.vue'
import ImageShape from './image/image.vue'
import TextShape from './text/text.vue'

const cptManager: Map<string, any> = new Map([
  ['rect', RectShape],
  ['line', LineShape],
  ['circle', CircleShape],
  ['image', ImageShape],
  ['simpleText', TextShape]
])

export default Vue.extend({
  name: 'BaseShape',
  props: {
    typeName: {
      type: String,
      default: ''
    },
    option: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data() {
    return {}
  },
  render(h): any {
    const capitTypeName = this.typeName
    let cptName: any = RectShape

    if (capitTypeName) {
      cptName = cptManager.get(capitTypeName)
    }

    return h(cptName, {
      props: {
        initOption: this.option
      }
    })
  }
})
