



















import CanvasCtm from './canvasCtm.vue';
import PartCtm from './partCtm.vue';
import CtmListener from './ctmListener.vue';
import { mapMutations, mapState } from 'vuex';
export default{
    name: 'ContextMenu',
    data(){
        return {
            canvasDomId: '',
            layout: {
                x: 0,
                y: 0,
            },
            // 用于事件，比如粘贴至鼠标处
            mousePos: {
                x: 0,
                y: 0,
            },
            contextMenuTarget: '',
            contextMenuVisible: false,
        };
    },
    props: {
        clickTarget: {
            type: String,
            default: 'canvas',
        },
    },
    components: {
        CanvasCtm,
        PartCtm,
        CtmListener,
    },
    computed: {
        ...mapState({
            contextMenu: (state: any) => state.screen.event.contextMenuEvent.contextMenu,
        }),
        ctmType: {
            get(): any {
                return this.contextMenu.ctmType;
            },
            set(val) {
                this.setCtmType(val);
            },
        },
        ctmShow: {
            get(): any {
                return this.contextMenu.showMenu;
            },
            set(val) {
                this.setShowMenu(val);
            }
        },
        ctmTarget: {
            get(): any {
                return this.contextMenu.ctmTarget;
            },
            set(val) {
                this.setCtmTarget(val);
            },
        },
        styleComp() {
            return {
                top: this.layout.y + 'px',
                left: this.layout.x + 'px',
            };
        },
    },
    watch: {
        clickTarget(val) {
            this.ctmTarget = val;
        },
    },
    mounted() {
        this.initCtmEvent();
        this.canvasDomId = document.querySelector(`#screen-viewport`);
    },
    methods: {
        ...mapMutations([
            'initContextMenu',
            'setCtmType',
            'setCtmTarget',
            'setShowMenu',
        ]),
        // 初始化vuex的模型
        initCtmEvent() {
            const evtObj = {
                domId: document.querySelector(`#screen-viewport`),
                eventType: 'contextmenu',
                ctmType: 'canvas',
                ctmTarget: 'canvas',
                ctmShow: false
            }
            this.initContextMenu(evtObj);
        },
        handleShowCtm( isShow: boolean, event: MouseEvent) {
            // fixedme这里主要是画布和part切换时，菜单显示的问题
            if (isShow) {
                this.mousePos = {
                    y: event.offsetY || event.layerY,
                    x: event.offsetX || event.layerX,
                };
                this.layout = {
                    x: event.clientX,
                    y: event.clientY,
                };

                // Fixme 待优化，右击菜单的显示判断是否将会被遮挡
                this.$nextTick( () => {
                    const menuDom: HTMLDivElement = document.querySelector('.right-menu');
                    const mouseDomH = this.layout.y + $(menuDom).height();
                    const clientHeight = document.querySelector('.screen-main').clientHeight;
                    if (mouseDomH > clientHeight) {
                        this.layout.y = this.layout.y - $(menuDom).height();
                    }
                });
                this.ctmShow = !isShow;
            }
            setTimeout(() => {
                this.ctmShow = isShow;
            }, 10);
            
        }
    }
};
