















import Vue from 'vue';
import Layer from './layer/layer.vue';
import Pages from './pages/pages.vue';
import Components from './component/baseMenu.vue';
export default Vue.extend({
    name: 'rightNav',
    data() {
        return {
            modeTabName: 'first',
        };
    },
    components: {
        Components,
        Layer,
        Pages,
    },
    props: {
        layoutSize: {
            type: Object,
            default: {},
        },
        leftNavH: {
            type: Number,
            default: 0,
        },
    },
    computed: {
        leftNavSty(): any {
            return {
                width: `${this.layoutSize.leftNavWidth}px` || '280px',
            };
        },
    },
});
