const mutations = {
    setSelectedBox(state: Screen.state, isSelected: boolean) {
        state.editor.transform.setSelectedBox(isSelected);
    },
    setScaleLimit(state: Screen.state, scaleLimit: number) {
        state.editor.transform.setScaleLimit(scaleLimit);
    },
    setSnap(state: Screen.state, snap: number) {
        state.editor.transform.setSnap(snap);
    },
    setDisableScale(state: Screen.state, isDisableScale: boolean) {
        state.editor.transform.setDisableScale(isDisableScale);
    },
    setDisableRotate(state: Screen.state, isDisableRotate: boolean) {
        state.editor.transform.setDisableRotate(isDisableRotate);
    },
    setDisableSelect(state: Screen.state, isDisableSelect: boolean) {
        state.editor.transform.setDisableSelect(isDisableSelect);
    },
    setDisableDrag(state: Screen.state, isDisableDrag: boolean) {
        state.editor.transform.setDisableDrag(isDisableDrag);
    },
    updateTransform(state: Screen.state, transformObj: Editor.TransformType) {
        state.editor.transform.updateTransform(transformObj);
    },
};
const getter = {
    transform: (state: Screen.state) => state.editor && state.editor.transform,
};
export default {
    mutations,
    getter,
};
