import AbstractOption from '../../../common/abstractOption'
import updatePropsDeep from 'utils/initProps'

/**
 * series-pie系列列表配置项
 */
export default class SeriesPie extends AbstractOption {

  public config: any;

  constructor(series: Array<Chart.SeriesItem>) {
    super();
    this.config = this.init(series);
  }

  public init(series: Array<Chart.SeriesItem>) {
    if (!_.isArray(series) || _.isEmpty(series)) {
      return [];
    }

    const res = [];
    const source: Chart.SeriesItem = {
      // 指标占比pie系列数据值
      $ratio: {
        target: 0,
        actual: 0,
        text: ''
      },
      // 外半径
      $outerRadius: 50,
      // 内半径
      $innerRadius: 30,
      // 标签小数点位数
      $point: 2,
      // 标签是否显示值真实值
      $formatter: false,
      // 标签是否显示标签名
      $showFormatterName: true,
      // 初始化角度组合值
      $angle: 90,

      type: 'pie',
      // startAngle: 90,
      roseType: false,
      label: {
        show: true,
        position: 'outside',
        // formatter: "{b}\n{d}%",
        color: '',
        fontSize: 12,
        fontWeight: 'normal'
      },
      labelLine: {
        show: true,
        length: 20,
        length2: 20
      },
      itemStyle: {
        color: '',
      },
      data: []
    };

    for (let item of series) {
      const props: any = updatePropsDeep(source, item);
      const {
        $innerRadius,
        $outerRadius,
        $point,
        $formatter,
        $showFormatterName,
        $angle,
        $ratio
      } = props;

      // 计算内外半径
      props.radius = [];
      props.radius.push(`${$innerRadius}%`, `${$outerRadius}%`);
      // 计算标签显示类型的formatter
      props.label.formatter = this.setFormatter({
        $point,
        $formatter,
        $showFormatterName,
        $ratio
      });
      // 计算初始化角度值
      props.startAngle = this.setAngle($angle);

      res.push(props);
    }
    return res;
  }

  /**
   * 设置饼图的标签的数值显示类型
   * @param seriesItem 系列配置项
   */
  public setFormatter(seriesItem: Chart.SeriesItem): Function {
    const { $point, $formatter, $showFormatterName, $ratio = {} } = seriesItem;
    const { target = 0, actual = 0 } = $ratio;
    const diff = actual - target;
    const diffRatio = diff / target === Infinity ? 0 : diff / target;

    return function ({ name, value, percent }: any) {
      let rate = 0;
      const floatVal = value.toFixed($point);

      // 指标占比pie的实际值大于目标值时，需手动计算百分比
      rate = diff > 0 ?
        ((diffRatio + 1) * 100).toFixed($point) :
        percent.toFixed($point);

      return $showFormatterName ?
        ($formatter ? `${name}\n${floatVal}` : `${name}\n${rate}%`) :
        ($formatter ? `${floatVal}` : `${rate}%`);
    }
  }

  /**
   * 设置饼图的初始化角度值
   * @param angle 初始化角度组合值
   */
  public setAngle(angle: string | number): number {
    let res: any;
    if (typeof angle === 'number') {
      res = angle % 360;
    } else {
      const val: any = _.trim(angle);
      res = val.length === 0 || Number.isNaN(Number(val)) ? 90 : val % 360;
    }
    return parseInt(res, 10)
  }

  public computed(): void { }

}