class BaseDto<T> {
    code: number;
    data: T;
    msg: string;
}
/**
 * 返回数据规格定义
 */
export class MDto<T> extends BaseDto<T> {
    constructor(code: number, data: T, msg: string) {
        super();
        this.code = code;
        this.data = data;
        this.msg = msg;
    }
}

export enum AppDsType {
    staticData = 0,
    pointData = 1,
    apiData = 2
}

export enum EvalValueType {
    realTimeV = 0,
    historyV = 1
}

export enum PointValueType {
    wortV = 0,
    statV = 1
}
