/*
 * @Description: 这个顶部工具栏的配置文件
 * @type { string } 每个配置项的类型，id
 * @disable { boolean } 该功能能否使用
 * @icon { string[] } 显示图标的数组
 * @name { string } 用于显示的文本
 * @component { string } 前端渲染的组件类型，这里有default, dropdown, tooltip
 * @label { string } tooltip展示的文本
 * @children { any[] } dropdown子节点
 * @Author: chuguo.zheng
 * @Date: 2019-05-31 16:14:12
 */
export interface ToolConfig {
    type: string;
    disable: boolean;
    name: string;
    trigger?: string;
    icon: string[];
    component: string;
    label: string;
    children: ToolConfig[];
}

