


















































import Vue from 'vue';
import keyCodeConfig from 'screenCfg/event/keyCodeConfig';
import html2canvas from 'html2canvas';
import { mapMutations, mapState } from 'vuex';
import CmptModal from '../../contextMenu/cmptModal.vue';
export default Vue.extend({
    data() {
        return {
            ctmConfig: [],
            // 新增组件的模态框
            isAddCmpt: false,
            cmptModalShow: false,
            bindTypeFunc: new Map(),
        };
    },
    components: {
        CmptModal,
    },
    computed: {
        ...mapState({
            selector: (state: any) => state.screen.editor,
        }),
        selectGroup(): any[] {
            return this.selector.partSelection.selectGroup;
        },
        curSelectPart(): Model.Part {
            return this.selectGroup.length === 1 ? this.selectGroup[0] : null;
        }
    },
    methods: {
        ...mapMutations([
            'add_cmpt',
            'setSelectedBox',
        ]),
        changeLayerName() {
            this.$emit('changeLayerName');
        },
        /**
         * 格式化keyCode的配置列表
         * 不需要 - 粘贴到鼠标
         * 不需要 - 历史
         */
        initFormatCtm() {
            const unexpectList = [
                {
                    perent: 'clipboard',
                    child: ['pasteMouse'],
                },
                {
                    perent: 'history',
                },
                {
                    perent: 'scale',
                },
                {
                    perent: 'lib',
                    child: ['addTemplate'],
                }
            ];
            this.initFormatCtmConfig(unexpectList);
        },
        /**
         * 格式化keyCode的配置列表
         */
        initFormatCtmConfig( unexpectList ) {
            const copyKeyCodeConfig = _.cloneDeep(keyCodeConfig);
            unexpectList.forEach( (unxpItem: any) => {
                const hasChild = unxpItem.child;
                // 这里可以直接修改数组
                copyKeyCodeConfig.some( (keyCodeItem: any, index: number) => {
                    if (!hasChild && unxpItem.perent === keyCodeItem.key) {
                        // 删除只有parent的大项
                        copyKeyCodeConfig.splice(index, 1);
                        return true;
                    } else if (hasChild && unxpItem.perent === keyCodeItem.key) {
                        // 删除parent下的child的项
                        const keyCodeValueList = keyCodeItem.value;
                        hasChild.forEach( ( keyCodeType: string[] ) => {
                            keyCodeValueList.some( (keyCode: any, point: number) => {
                                if (keyCode.type === keyCodeType) {
                                    keyCodeValueList.splice(point, 1);
                                }
                            });
                        });
                        return true;
                    } 
                });
            });
            this.ctmConfig = copyKeyCodeConfig;
        },
        onMenuChange() {
            this.$root.busRef.$on('enableWatch', (message) => {

                if (_.isNil(message)) {
                    return;
                }
                if (!Array.isArray(message)) {
                    message = [message];
                }
                const copyMsg = _.cloneDeep(message);
                copyMsg.forEach((emit: any) => {
                    this.canDisableMenu(emit);
                });
            });
        },
        /**
         * 功能模块的使能
         */
        canDisableMenu( enableObj: {
            parent: string,
            child: string,
            enable: boolean
        }) {
            // 需要做一些特殊的处理，因为这里不需要处理layer
            const exceptItem = ['layer', 'copy', 'pasteMouse'];
            if (exceptItem.includes(enableObj.child)) {
                return;
            }
            // 更新具体的菜单配置选项的使能，比如复制原本为false，现在更新为true
            const canDo = !enableObj.enable;
            const updateCtmCfj = (ctmCfgList: any, ctmIndex: number) => {
                // 如果值相同就没必要更新
                if (ctmCfgList[ctmIndex].disable === canDo) {
                    return;
                }
                const ctmItem: any = _.cloneDeep(ctmCfgList[ctmIndex]);
                ctmItem.disable = canDo;
                ctmCfgList.splice(ctmIndex, 1, ctmItem);
            };

            // 在配置列表中，找到对应的配置项，并执行更新使能的方法
            this.ctmConfig.some((item: any) => {
                if (enableObj.parent === item.key) {
                    const secondItemList = item.value;
                    const secondIndex = secondItemList.findIndex((chidItem: any) => {
                        return chidItem.type === enableObj.child;
                    });
                    updateCtmCfj(secondItemList, secondIndex);
                    return true;
                }
            });
        },
        /**
         * item 配置项的
         */
        itemClickBroadcast(item: any): void {
            if (item.disable) {
                return;
            }
            // if (!showCtm) {
            //     this.$emit('handleShowCtm', showCtm);
            //     this.setSelectedBox(false);
            // }
            this.editorEvtEmitter(item.type);
        },
        // 发射指定名称的事件...
        editorEvtEmitter(evtName: string): void {
            // 如果是添加组件和母版就不需要发射事件
            const extraNameGroup = ['addCmpt', 'addTemplate'];
            if (extraNameGroup.includes(evtName)) {
                this.handleAddCmpt();
                return;
            }
            this.$root.busRef.$emit(evtName, evtName);
        },
        handleAddCmpt() {
            if (!this.curSelectPart) {
                return;
            }
            this.cmptModalShow = true;
        },
        snapshotDiv(currentPart, snapshotCallback) {
            const partId = currentPart.partId;
            const dom: any = document.querySelector(`#mounted_${partId}`);
            // const dom: any = document.querySelector(`#canvas`);
            // rotate 属性导致显示异常
            // base64内存飙升，有时甚至内存溢出
            // cloneNode 在其添加父级元素，不能用clone去转化图片
            // 在某块区域为其添加父级，该父级链接在body文档下
            html2canvas( dom, { allowTaint: true, background: null } ).then( canvas => {
                const dataURL = canvas.toDataURL("image/png");
                snapshotCallback(dataURL);
            });
            // const dataURL = require('../../../../../assets/logo.png');
            // snapshotCallback(dataURL);
        },
        handleCmptModal(isShow: boolean): void {
            this.cmptModalShow = isShow;
        },
        addCmptGroup(cmptTemp) {

        },
        addCmpt(cmpt): void {
            
            const snapshotCallback = (imgSrc) => {
                const copyPart: Model.Group = this.curSelectPart.getPartAllProps();
                // 如果是group，去除group内part的id
                if (copyPart.partType === 'group') {
                    copyPart.partGroup.forEach( (part) => {
                        part.partId = '';
                    });
                }
                // 防止id相同
                copyPart.partId = '';
                const cmptArgs = {
                    // fixme，做一次特别的深拷贝，去除instance实例的深拷贝，对象会造成内存泄漏
                    partSource: copyPart,
                    imgSrc,
                    ...cmpt,
                };
                this.add_cmpt(cmptArgs);
                this.$message({
                    type: 'success',
                    message: '组件添加成功',
                });
            };
            this.snapshotDiv(this.curSelectPart, snapshotCallback);
            this.cmptModalShow = false;
        },
    },
    mounted() {
        this.initFormatCtm();
        this.onMenuChange();
    },
    beforeDestroy() {
        this.$root.busRef.$off('enableWatch');
    },
});
