const mutations = {
    add_part_all_listener(state: Screen.state) {
        console.log(state.event.partEvent);
        state.event.partEvent.addAllListener();
    },
    remove_part_all_listener(state: Screen.state) {
        state.event.partEvent.removeAllListener();
    },

    addCanvasAllListener(state: Screen.state) {
        console.log(state.event.canvasEvent);
        state.event.canvasEvent.addAllListener();
    },
    removeCanvasAllListener(state: Screen.state) {
        state.event.canvasEvent.removeAllListener();
    },

    addCanvasListener(state: Screen.state, evtObj: any) {
        state.event.canvasEvent.addClickListener(evtObj);
    },
    removeCanvasListener(state: Screen.state, evtObj: any) {
        state.event.canvasEvent.removeClickListener(evtObj);
    },

    bindKeyCodeObject(state: Screen.state, keyCodeObject: any) {
        state.event.keyCodeEvent.bindKeyCodeObject(keyCodeObject);
    },
    bindAllKeyCodeObject( state: Screen.state, cb: any ) {
        state.event.keyCodeEvent.bindAllKeyCodeObject(cb);
    },
    unbindKeyCodeObject(state: Screen.state, keyCodeObject: any) {
        state.event.keyCodeEvent.unbindKeyCodeObject(keyCodeObject);
    },
    unbindAllKeyCodeObject(state: Screen.state, keyCodeObject: any) {
        state.event.keyCodeEvent.unbindAllKeyCodeObject(keyCodeObject);
    },

    initContextMenu(state: Screen.state, evtObj: any) {
        state.event.contextMenuEvent.initContextMenu(evtObj);
    },
    setCtmType(state: Screen.state, ctmType: any) {
        state.event.contextMenuEvent.setCtmType(ctmType);
    },
    setCtmTarget(state: Screen.state, ctmTarget: any) {
        state.event.contextMenuEvent.setCtmTarget(ctmTarget);
    },
    setShowMenu(state: Screen.state, showMenu: any) {
        state.event.contextMenuEvent.setShowMenu(showMenu);
    }
}
const getter = {
    partEvent: (state: Screen.state) => state.event && state.event.partEvent,
}
export default {
    mutations,
    getter
}