
/**
 * 通过某属性维度值对对象数组进行分组,
 * 并返回对应属性维度值升序排列的前两项
 * @param origin 数据源
 * @param dimens 维度属性
 * @returns 分组后二维数组
 */
const groupDataByDimens = function (origin: any[], dimens: string): Array<any[]> {
  if (!origin.length) {
    return [];
  }

  const data1: any[] = [];
  const data2: any[] = [];
  const zList: number[] = origin
    .filter((item: any) => !Number.isNaN(Number(item[dimens])))
    .map((item: any) => Math.floor(item[dimens]));
  const indexList: number[] = Array.from(new Set(zList)).sort((a, b) => a - b);
  const index1 = indexList[0] || null;
  const index2 = indexList[1] || null;

  for (let item of origin) {
    const key = Math.floor(Number(item[dimens]));
    if (key === index1) { data1.push(item) }
    if (key === index2) { data2.push(item) }
  }
  return [data1, data2];
};

/**
 * 将含x、y、s属性的一维对象数组通过s维度值,转换为包含x、y属性值的二维数组,
 * 并按s纬度值升序排列二维数组
 * @param origin 数据源(一维数组)
 *  [
 *    {
 *      "x": "内蒙古",
 *      "y": "375",
 *      "s": "1"
 *     },
 *     {
 *       "x": "浙江",
 *       "y": "200",
 *       "s": "1"
 *     },
 *     {
 *       "x": "辽宁",
 *       "y": "25",
 *       "s": "1"
 *     },
 *     ...
 *  ]
 * @param reverse 是否互换x、y值，默认以[x, y]排列
 * @returns dataList 转换后二维数组
 *  [
 *    ["内蒙古", "375"],
 *    ["浙江", "200"],
 *    ["辽宁", "25"],
 *    ...
 *  ]
 */
const transferDataByDimens = function (origin: any[], reverse?: boolean): Array<any[]> {
  if (!origin.length) {
    return [];
  }

  const dataList: Array<any[]> = []
  const dataMap: Map<number, any[]> = new Map([]);
  const sList: number[] = origin
    .filter((item: any) => !Number.isNaN(Number(item.s)))
    .map((item: any) => Math.floor(item.s))
    .sort((a, b) => a - b);
  const sSet: Set<number> = new Set(sList);

  // 图表series对应的二维数组数据的map结构映射
  for (let s of sSet) {
    dataMap.set(s, []);
  }
  for (let item of origin) {
    const key = Math.floor(Number(item.s));
    // 处理除x、y外的非法字段
    const { x = '', y = '' } = item;
    if (dataMap.has(key)) {
      const data = reverse ? [y, x] : [x, y];
      dataMap.get(key).push(data);
    }
  }

  // map结构转为二维数组
  for (let value of dataMap.values()) {
    dataList.push(value)
  }
  return dataList;
};

/**
 * 将含x、y、s属性的一维对象数组先通过s维度值升序排列后，
 * 再通过x维度值分组并计算每组y维度的累加值，
 * 最后返回x维度值及每组累加值二维数组
 * @param origin 数据源(一维数组)
 * @param reverse 是否互换x、y值，默认以[x, y]排列
 * @returns 计算后二维数组
 */
const getStackDataByDimens = function (origin: any[], reverse?: boolean): Array<any[]> {
  if (!origin.length) {
    return [];
  }

  const stackData: Array<any[]> = [];
  const stackValue: number[] = [];

  // 源数据格式化:过滤非x、s属性且s不可转换为number类型后,按属性s值升序排序
  const formatData = origin
    .filter((item: any) => item.x && !Number.isNaN(Number(item.s)))
    .sort(compare('s'));
  const stackGroupObj = groupByProp(formatData, 'x');
  let max: number;

  // 计算分组累加值
  for (let value of Object.values(stackGroupObj)) {
    const stackY = (<any[]>value).reduce((acc, curr) => {
      const currY = Math.floor(Number(curr.y)) || 0;
      return acc + currY;
    }, 0);
    stackValue.push(stackY);
  }

  max = Math.max.apply(null, stackValue);
  for (let key of Object.keys(stackGroupObj)) {
    const item = reverse ? [max, key] : [key, max];
    stackData.push(item)
  }
  return stackData;
}

/**
 * 按对象数组的某属性值对数组进行分组
 * @param arr 对象数组
 * @param prop 分组属性
 * @returns 按属性值分组后对象
 */
function groupByProp(arr: any[], prop: string): object {
  return arr.reduce((acc, curr) => {
    const key = curr[prop];
    if (!acc[key]) {
      acc[key] = [];
    }
    acc[key].push(curr);
    return acc;
  }, {});
}

/**
 * 按对象数组某属性值对数组进行升序排序的比较函数
 * @param prop 排序属性
 */
function compare(prop: string) {
  return function (a: any, b: any) {
    const val1 = a[prop];
    const val2 = b[prop];
    return val1 - val2;
  }
}

export {
  transferDataByDimens,
  groupDataByDimens,
  getStackDataByDimens
};

