import AbstractOption from '../../../common/abstractOption'
import updatePropsDeep from 'utils/initProps'

/**
 * 基础报表的列系列配置项
 */
export default class TableSeries extends AbstractOption {

  public config: any;

  constructor(series: Array<TableReport.Series>) {
    super();
    this.config = this.init(series)
  }

  public init(series: Array<TableReport.Series>) {
    if (!_.isArray(series) || _.isEmpty(series)) {
      return []
    }

    const res = [];
    const source: TableReport.Series = {
      index: 0,
      name: '类别一',
      key: 'series1',
      width: 15,
      color: '#333',
      fontSize: 14,
      fontWeight: 'normal',
      textAlign: 'left'
    };

    for (let item of series) {
      res.push(updatePropsDeep(source, item));
    }
    return res;
  }

  public computed(): void { }

}