import AbstractOption from '../../../common/abstractOption'
import updatePropsDeep from 'utils/initProps'

/**
 * 图表全局字体配置项
 */
export default class TextStyle extends AbstractOption {

  public config: any;

  constructor(opts?: any) {
    super()
    this.config = this.init(opts)
  }

  public init(opts?: any) {
    const source: any = {
      fontFamily: 'sans-serif'
    };

    return updatePropsDeep(source, opts);
  }

  public computed() { }

}