import RectObject from '../baseObject/rectObject';
import { ClipBoardType } from '../interface';

export default class ClipBoard implements ClipBoardType{

    // 暂存part的数组，可以是一个part，也可以是一组part
    public partGroupTemp: any[] = [];

    public rectCoord: RectObject = new RectObject();

    public type: string = 'single';

    public hasCache: boolean = false;

    // 拷贝功能是否能用
    public canCopy: boolean = false;

     // 粘贴功能是否能用
    public canPaster: boolean = false;

    /**
     * 更新粘贴板
     */
    public updateClipBoard( partGroup, rectCoord?: RectObject ): void {
        if (rectCoord) {
            this.type = 'multiply';
            this.mapClipBoardRectObj(rectCoord);
        }
        this.type = 'single';
        this.partGroupTemp = [];
        this.hasCache = true;
        partGroup.forEach( part => {
            this.partGroupTemp.push( part );
        });
        this.setCanCopy(true);
        this.setCanPaster(true);
    }

    /**
     * 清空粘贴板
     */
    public clearClipBoard(): void {
        this.hasCache = false;
        this.partGroupTemp = [];
        this.setCanCopy(false);
        this.setCanPaster(false);
    }

    /**
     * 映射更新 rectCoord
     */
    public mapClipBoardRectObj( rectCoord ) {
        this.rectCoord.mapRectObject( rectCoord );
    }

    /**
     * clipBoard是否有缓存
     */
    public getHasCache() {
        return this.hasCache;
    }

    /**
     * 设置拷贝功能是否可用
     */
    public setCanCopy( canCopy: boolean ): void {
        this.canCopy = canCopy;
    }

    /**
     * 设置粘贴功能是否可用
     */
    public setCanPaster( canPaster: boolean): void {
        this.canPaster = canPaster;
    }

    public destroyed(): void {
        this.clearClipBoard();
    }
}