























import Vue from 'vue'
import updatePropsDeep from 'utils/initProps'
const idPrefix = 'baseTable'

export default Vue.extend({
  name: 'baseTable',
  props: ['option'],
  data() {
    return {
      id: this._.uniqueId(idPrefix),

      // 基础配置项
      initOption: {
        // 通用属性
        global: {
          flexible: true,
          fontFamily: 'sans-serif',
          line: {
            width: 0,
            color: '#333',
            style: 'solid'
          },
          paddingHorizontal: 20,
          paddingVertical: 15
        },
        // 表头
        thead: {
          show: true,
          background: 'rgb(25, 81, 0)',
          color: '#fff',
          fontSize: 12,
          fontWeight: 'normal',
          textAlign: 'left'
        },
        // 行
        row: {
          evenBackground: '#0a2732',
          oddBackground: '#003b51',
          line: {
            width: 1,
            color: 'rgb(25, 81, 0)',
            style: 'solid'
          }
        },
        // 列
        column: {
          line: {
            width: 3,
            color: 'rgb(87, 210, 254)',
            style: 'solid'
          }
        },
        // 列系列
        series: [],
        // 数据
        data: []
      }
    }
  },
  computed: {
    // 表格样式
    rootStyle(): object {
      const {
        flexible,
        fontFamily,
        line: { width, style, color }
      } = this.initOption.global

      return {
        height: `${flexible ? '100%' : 0}`,
        fontFamily: `${fontFamily}`,
        border: `${width}px ${style} ${color}`
      }
    },

    // 表头样式
    theadStyle(): object {
      const { show } = this.initOption.thead
      return {
        display: `${show ? 'table-header-group' : 'none'}`
      }
    },
    thStyle(): object {
      const {
        global: { paddingHorizontal, paddingVertical },
        thead: { background, color, fontSize, fontWeight, textAlign }
      } = this.initOption
      const { line } = this.initOption.column

      return {
        backgroundColor: `${background}`,
        color: `${color}`,
        fontSize: `${fontSize}px`,
        fontWeight: `${fontWeight}`,
        textAlign: `${textAlign}`,
        borderRight: `${line.width}px ${line.color} ${line.style}`,
        padding: `${paddingVertical}px ${paddingHorizontal}px`
      }
    },

    // 表体样式
    tdStyle(): object {
      const {
        global: { paddingHorizontal, paddingVertical },
        row,
        column
      } = this.initOption

      return {
        borderRight: `${column.line.width}px ${column.line.color} ${column.line.style}`,
        borderBottom: `${row.line.width}px ${row.line.color} ${row.line.style}`,
        padding: `${paddingVertical}px ${paddingHorizontal}px`
      }
    },

    evenBgc(): string {
      const { evenBackground } = this.initOption.row
      return evenBackground
    },
    oddBgc(): string {
      const { oddBackground } = this.initOption.row
      return oddBackground
    },
    columns(): any[] {
      const _series = this._.cloneDeep(this.initOption.series)
      _series.forEach((item: any) => {
        const { width, color, fontSize, fontWeight, textAlign } = item
        item.style = {
          width: `${width}%`,
          color: `${color}`,
          fontSize: `${fontSize}px`,
          fontWeight: `${fontWeight}`,
          textAlign: `${textAlign}`
        }
      })
      return _series
    },
    data(): any[] {
      return this.initOption.data
    }
  },
  methods: {
    setOption(option: object): void {
      if (this._.isPlainObject(option)) {
        this.initOption = updatePropsDeep(this.initOption, option)
      }
    }
  },
  created() {
    this.setOption(this.option)
  }
})
