import UUID from '../../utils/uuid';
// import element from '../element/element';
import _ from 'lodash';
import PageState from './pageState';
export default class StateManage {

    /**
     * pageState功能是否可用
     */
    public static ableTag: boolean = false;

    /**
     * 当前页面状态实例
     */
    public currentPageState: any = undefined;

    /**
     * 初始化pageState
     * 新建全局和默认的状态
     * @returns {any[]} 返回pageState实例的数组 
     */
    public initState(): any[] {
        if (StateManage.ableTag) {
            return [];
        }
        const defaultStateArr: any[] = [];
        const globalState = this.addState('全局状态');
        const defaultState = this.addState('默认状态');
        globalState.setGlobalState();
        defaultState.setDefaultState();
        defaultStateArr.push(...[globalState, defaultState]);
        return defaultStateArr;
    }

    /**
     * 添加pageState
     * @returns {any} 返回pageState实例
     */
    public addState(name?): any {
        if (StateManage.ableTag) {
            return;
        }
        const stateId = this.createdStateId();
        let stateName = '状态';
        if (name) {
            stateName = name;
        }
        const pageState = new PageState(stateId, stateName);
        return pageState;
    }
    /**
     * 删除添加的state
     */
    public removeState(stateList, curState): void {
        if (stateList.length > 0) {
            _.remove(stateList, (n) => {
                return curState === n.stateId;
            });
        }
    }


    public storedPartContainer(): any {
        return PageState.partContainer;
    }

    public readPartContainer( partContainer: any[] ): void {
        if (!this.currentPageState) {
            return;
        }
        PageState.partContainer = partContainer;
    }

    public clearPartContainer(): void {
        if (!this.currentPageState) {
            return;
        }
        this.currentPageState.partContainer = [];
    }

    public setStateName( stateName ): void {
        if (!this.currentPageState) {
            return;
        }
        this.currentPageState = stateName;
    }

    public setGlobalState(): void {
        this.currentPageState.globalState = true;
    }

    public setDefaultState(): void {
        this.currentPageState.defaultState = true;
    }

    public isGlobalState(): boolean {
        return this.currentPageState.globalState;
    }

    public isDefaultState(): boolean {
        return this.currentPageState.defaultState;
    }


    public enable(): void {
        PageState.ableTag = false;
    }

    public disable(): void {
        PageState.ableTag = true;
    }

    private createdStateId(): string {
        return 'state_' + new UUID().init();
    }

}