import AbstractOption from '../../../common/abstractOption'
import updatePropsDeep from 'utils/initProps'

/**
 * series-bar系列列表配置项
 * barCategoryGap、barGap设置取series-bar最后一项设置生效
 * barWidth与barCategoryGap属性相互冲突
 */
export default class SeriesBar extends AbstractOption {

  public config: any;

  constructor(series: Array<Chart.SeriesItem>) {
    super();
    this.config = this.init(series)
  }

  public init(series: Array<Chart.SeriesItem>) {
    if (!_.isArray(series) || _.isEmpty(series)) {
      return []
    }

    const res = [];
    const source: Chart.SeriesItem = {
      $barGap: 30,
      $barCategoryGap: 20,
      type: 'bar',
      name: '',
      stack: null,
      // barGap: '30%',
      // barCategoryGap: '20%',
      yAxisIndex: 0,
      label: {
        show: false,
        position: 'inside',
        fontSize: 12,
        color: '',
        fontWeight: 'normal'
      },
      z: 3,
      tooltip: {
        show: true
      },

      // 折线柱图折线私有属性
      smooth: false,
      lineStyle: {
        color: '',
        type: 'solid',
        width: 2
      },
      symbol: 'circle',
      symbolSize: 8,

      itemStyle: {
        color: ''
      },
      data: []
    };

    for (let item of series) {
      const props: any = updatePropsDeep(source, item);

      // 柱间间距处理
      const { $barGap = 30, $barCategoryGap = 20 } = props;
      props.barGap = `${parseInt($barGap)}%`;
      props.barCategoryGap = `${parseInt($barCategoryGap)}%`;
      res.push(props);
    }
    return res;
  }

  public computed(): void { }

}