











































































































import Vue from 'vue';
import { mapState, mapActions, mapMutations } from 'vuex';
import slVueTree from 'sl-vue-tree';
export default Vue.extend({
    components: {
        slVueTree,
    },
    data() {
        return {
            pageTree: [],
            filterText: '',
            currentState: '',
            lastNode: '',
            modelName: '',
            currentNode: '',
            slVueTree: {},
            recordLastNode: {},
            recordEditButton: false,
            inputPageName: '',
            cureentPageCtm: {},
        };
    },
    computed: {
        ...mapState({
            pageManage: (state: any) => state.screen.pageManage,
        }),
        pagesContainer(): Model.Page[] { 
            return this.pageManage.pagesContainer;
        },
    },
    methods: {
        ...mapMutations([
            'add_Pages',
            'remove_page',
            'init_page',
            'update_current_page',
            'edit_page',
            'copy_page',

            'clearPartSeletion',
            'setSelectedBox',
        ]),
        ...mapActions([
            // 'initPageManage',
        ]),
        getIconVisible(node: any): any {
            let isVisible: string = 'hidden';
            if (node && node.data.isHover) {
                isVisible = 'visible';
            }
            return {
                visibility: isVisible,
            };
        },
        /**
         * 新增一个根级页面
         */
        addPage() {
            this.add_Pages();
            const mapPageModel = this.getLastPage();
            mapPageModel.data.edit = true;
            mapPageModel.isSelected = true;

            this.pageTree.forEach( (pageItem: Model.Page) => {
                pageItem.isSelected = false;
                pageItem.data.edit = false;
            });
            this.pageTree.push(mapPageModel);
            
            this.inputPageName = mapPageModel.data.pagesName;
            this.$nextTick(() => {
                const pageId = mapPageModel.data.pagesId;
                (this.$refs[pageId]as any).focus();
                this.changeCurPage(mapPageModel);
            });
        },
        /**
         * 编辑页面
         */
        editPage(pageNode: any) {
            
            if (_.isNil(pageNode)) {
                pageNode = this.cureentPageCtm;
            }

            // 标志当前状态
            const isEdit = pageNode.data.edit;

            // 如果在编辑的过程中，点击了另一个编辑的页面，前一个页面需要被关闭
            if (_.isEmpty(this.recordLastNode)) {
                this.recordLastNode = pageNode;
            } else {
                const newPageId = pageNode.data.pagesId;
                const oldPagesId = this.recordLastNode.data.pagesId;
                if (newPageId === oldPagesId) {
                    if (!this.inputPageName) {
                        this.$message.warning('页面名称不能为空');
                        return;
                    }
                }
                const cloneData = _.cloneDeep(this.recordLastNode.data);
                cloneData.edit = false;
                const partch = { data: cloneData };
                this.slVueTree.updateNode( this.recordLastNode.path, partch );
            }

            // 更新组件的属性和状态
            type changeEditFuc = (toEdit: boolean, pageName?: string) => void;
            const changeEdit: changeEditFuc = ( toEdit, pageName) => {
                const pageModelMix = pageNode.data;
                pageModelMix.edit = toEdit;
                const partch = { data: pageModelMix };
                if (pageName) {
                    pageModelMix.pagesName = pageName;
                    Object.assign(partch, { title: pageName }, { data: pageModelMix });
                }
                this.slVueTree.updateNode( pageNode.path, partch );
            };

            // 判断状态，更新页面的名称
            if (isEdit) {
                if (!this.inputPageName) {
                    this.$message.warning('页面名称不能为空');
                    return;
                }
                // 切换为不可编辑状态
                changeEdit(false, this.inputPageName);
                const pageOpt = {
                    page: pageNode.data,
                    pagesName: this.inputPageName
                };
                this.edit_page(pageOpt);
            } else {
                // 映射input的值
                this.inputPageName = pageNode.title;
                if (!this.inputPageName) {
                    return;
                }
                // 切换为可编辑状态
                changeEdit(true);
            }
            // 记录前一个页面的节点
            this.recordLastNode = pageNode;

            // 获取input聚焦
            this.$nextTick(() => {
                const pageId = pageNode.data.pagesId;
                (this.$refs[pageId]as any).focus();
            });

        },
        /**
         * 删除一个页面
         * 删除页面需要去更新选中的页面
         */
        removePage( node: any) {
            console.log("????");
            
            if (_.isNil(node)) {
                node = this.cureentPageCtm;
            }
            
            // 只有一个页面时不能被删除
            if (this.pagesContainer.length <= 1) {
                this.$message.warning('至少保留一个页面');
                return;
            }
            // 删除展示层当前选中的页面
            this.slVueTree.remove([node.path]);

            // 遍历树结构，将要删除的id推到数组中，删除model的页面模型
            const idCacheArr: string[] = [];
            this.traverse([node], idCacheArr);
            this.remove_page(idCacheArr);
            
            // 更新父节点是否要展开和更换图标
            const preNode = this.slVueTree.getPrevNode(node.path);
            
            if ( preNode && preNode.children.length === 0) {
                const patch = {isLeaf: true, isExpanded: false};
                this.slVueTree.updateNode(preNode.path, patch);
            }
            
            // 更新展示层当前选中的页面
            // 如果前面有节点就选中前面的节点，如果没有选中第一个节点
            // 会同时更新model层的数据
            if (preNode) {
                this.changeCurPage(preNode);
            } else {
                const firstNode = this.slVueTree.getFirstNode();
                this.changeCurPage(firstNode);
            }
        },
        /**
         * 添加子页面
         */
        appendPage(): void {
            
            const pageNode = this.cureentPageCtm;
            // 模型添加页面
            const { pagesId: parentId } = pageNode.data;
            this.add_Pages( parentId );

            // 更新当前节点为父级并展开节点
            const partch = {isLeaf: false, isExpanded: true};
            this.slVueTree.updateNode( pageNode.path, partch );

            // 组件插入新的节点，inside指插入为子节点
            const copy = this.getLastPage();
            const cursorPosition = {
                node: pageNode,
                placement: 'inside',
            };
            this.slVueTree.insert( cursorPosition, copy );
            
        },
        /**
         * 复制页面
         */
        copyPage() {
            const pageNode = this.cureentPageCtm;
            
            // 更新模型层
            this.copy_page(pageNode.data);

            // 组件插入新的节点，inside指插入为子节点
            const copy = this.getLastPage();
            pageNode.isSelected = true;
            pageNode.data.edit = true;
            const cursorPosition = {
                node: pageNode,
                placement: 'after',
            };
            
            this.slVueTree.insert( cursorPosition, copy );
            // 是否选中出现输入框
            // this.$nextTick(() => {
            //     const pageId = pageNode.data.pagesId;
            //     (this.$refs[pageId]as any).focus();
            // });
        },
        /**
         * 映射模型数据为组件数据
         */
        mapTreeNode( pageNode: any ): any {
            return Object.assign({}, { 
                title: pageNode.pagesName, 
                isLeaf: true,
            }, { 
                data: _.cloneDeep( _.defaultsDeep( { pageInstance: null, edit: false,  isHover: false }, pageNode ))
            });
        },
        /**
         * 更新当前选中页面节点
         */
        changeCurPage(node: any) {

            // 跟新当前选中页面状态
            if (node.path) {
                this.slVueTree.select(node.path);
            }
            // 取消编辑器的一些样式
            this.clearPartSeletion();
            this.setSelectedBox(false);
            // 这里需要做一些额外的操作，比如取消选框
            const pageModel = node.data;
            this.update_current_page(pageModel);

            // 保存当前页面node的引，用于编辑删除等操作
            this.cureentPageCtm = node;
        },
        /**
         * 初始化页面的模型
         */
        initPageModel() {
            // 初始化模型
            this.init_page();
            // 映射模型数据
            this.pagesContainer.forEach( ( page: any, index: number ) => {
                const pageModel = page.getProperties();
                const mapPageModel = this.mapTreeNode(pageModel);
                // 选中第一个页面
                if (index === 0) {
                    mapPageModel.isSelected = true;
                }
                this.pageTree.push(mapPageModel);
            });
        },
        /**
         * 初始化对子组件的引用
         */
        initRef() {
            this.slVueTree = this.$refs.slVueTree;
        },
        /**
         * 初始化第一个选中的页面
         */
        initFirstSelectPage() {
            const firstPageModel = this.slVueTree.getSelected()[0];
            this.update_current_page(firstPageModel.data);
        },
        /**
         * 递归遍历查找需要删除页面的pageId数组
         */
        traverse( nodeTree: any, idCacheArr: string[] ): void {
            let nodeLen = nodeTree.length;
            while ( nodeLen-- ) {
                const nodeModel = nodeTree[nodeLen];
                idCacheArr.push(nodeModel.data.pagesId);
                if (nodeModel.children) {
                    this.traverse( nodeModel.children, idCacheArr );
                }
            }
        },
        /**
         * 获取页面模型最后一个页面
         */
        getLastPage() {
            const newPage = this.pagesContainer[this.pagesContainer.length - 1];
            const pageProp = newPage.getProperties();
            const copyPage = this.mapTreeNode(pageProp);
            return copyPage;
        },
        /**
         * 隐藏弹出框
         */
        handleShowPoper() {
            this.$emit('handleShowPoper', false);
        },
        /**
         * 扁平的数组树改为树形树
         */
        initBuildTree(list: any[]) {
            // const list: any = [{"statesContainer":[],"partContainer":[],"pagesId":"page_B48594085000000194686F511B90D330","pagesName":"首页"},{"statesContainer":[],"partContainer":[],"pagesId":"page_B48594D56000000144609D00199018FC","pagesName":"页面","parentId":"page_B48594085000000194686F511B90D330"},{"statesContainer":[],"partContainer":[],"pagesId":"page_B4859539100000014A2E1380C5797190","pagesName":"页面","parentId":"page_B48594D56000000144609D00199018FC"},{"statesContainer":[],"partContainer":[],"pagesId":"page_B485957860000001D926B200494F13B4","pagesName":"页面","parentId":"page_B48594085000000194686F511B90D330"},{"statesContainer":[],"partContainer":[],"pagesId":"page_B48595A680000001FDE6BAF0177015E0","pagesName":"页面","parentId":"page_B485957860000001D926B200494F13B4"},{"statesContainer":[],"partContainer":[],"pagesId":"page_B4859614E00000012B705A501800D000","pagesName":"页面"},{"statesContainer":[],"partContainer":[],"pagesId":"page_B48596E710000001606ECB4765EC2080","pagesName":"页面","parentId":"page_B4859614E00000012B705A501800D000"}];
            const map: any = {};
            const tree = [];
            let node;
            let i;
            for (i = 0; i < list.length; i ++) {
                map[list[i].pagesId] = list[i]; 
                list[i].children = []; 
            }
            
            for (i = 0; i < list.length; i += 1) {
                node = list[i];
                if (node.parentId) {
                    map[node.parentId].children.push(node);
                } else {
                    tree.push(node);
                }
            }
            this.traverseMapNode(tree);
            return tree;
        },
        /**
         * 递归遍历查找需要删除页面的pageId数组
         */
        traverseMapNode( nodeTree: any ): void {
            let nodeLen = nodeTree.length;
            while ( nodeLen-- ) {
                const nodeModel = nodeTree[nodeLen];
                this.updateNode(nodeModel);
                if (nodeModel.children) {
                    this.traverseMapNode( nodeModel.children );
                }
            }
        },
        /**
         * 映射模型数据为组件数据
         */
        updateNode( pageNode: any ): void {
            pageNode.data = _.cloneDeep( 
                _.defaultsDeep({pageInstance: null, edit: false, isHover: false}, pageNode),
            );
            pageNode.title = pageNode.pagesName;
            // 如果存在孩子节点，就设置不是叶子节点
            if (pageNode.children.length > 0) {
                pageNode.isLeaf = false;
            } else {
                pageNode.isLeaf = true;
            }
        },
        handleClickOutSide( node: any ) {
            this.editPage(node);
        },
        pageMouseOut(node) {
            // 更新组件的属性和状态
            this.changeHover(node, false);
        },
        pageMouseOver(node) {
            this.changeHover(node, true);
        },
        changeHover( pageNode: any, isHover: boolean ) {
            const pageModelMix = pageNode.data;
            pageModelMix.isHover = isHover;
            const partch = { data: pageModelMix };
            this.slVueTree.updateNode( pageNode.path, partch );
        },
    },
    created() {
        // console.log("zheli shi page created");
    },
    mounted() {
        this.initRef();
        this.initPageModel();
        this.initFirstSelectPage();
        // console.log("zheli shi page mounted");
    },
});

