'use strict'
import EventObject from './eventObject';

export default class ContextMenuObject extends EventObject{
    
    /**
     * contextMenuType 右菜单类型
     * 取值 { part, canvas, template, cmpt }
     */
    public ctmType: string  = '';

    /**
     * contextMenuTarget 右菜单目标
     * 取值 { part, canvas }
     */
    public ctmTarget: string = '';

    public showMenu: boolean = false;

    constructor() {
        super();
    }

    public mapEventObject(eventTemp): void {
        super.mapEventObject( eventTemp );
        const { 
            ctmType = '',
            ctmTarget = '',
            showMenu = false,
        } = eventTemp;
        this.ctmType = ctmType;
        this.ctmTarget = ctmTarget;
        this.showMenu = showMenu;
    }

    public setCtmType( ctmType: string ): void {
        this.ctmType = ctmType;
    }

    public setCtmTarget( ctmTarget: string ): void {
        this.ctmTarget = ctmTarget;
    }

    public setShowMenu( showMenu: boolean ): void {
        this.showMenu = showMenu;
    }

}