
import Vue from 'vue'
import BaseTable from './base/table.vue'

const cptManager: Map<string, any> = new Map([['baseTable', BaseTable]])

export default Vue.extend({
  name: 'reports',
  props: {
    typeName: {
      type: String,
      default: ''
    },
    option: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data() {
    return {}
  },
  render(h): any {
    const capitTypeName = this.typeName
    let cptName: any = BaseTable

    if (capitTypeName) {
      cptName = cptManager.get(capitTypeName)
    }

    return h(cptName, {
      props: {
        option: this.option
      }
    })
  }
})
