import BaseShape from './abstractShape';
import GuiAdapter from './adapterGui/adapter/text';
import OptionAdapter from './adapterOption/adapter/baseShapeAdapter';

const baseOption: Shape.Option = {
  font: {
    fontSize: 16
  }
};

export default class TextShape extends BaseShape {

  public instance: any;
  public shapeStyle: any;
  public guiOption: any;
  public guiAdapter: any;

  constructor(part: Model.Part) {
    super(part);
    this.guiOption = [];
    this.guiAdapter = null;
    this.instance = null;
    this.shapeStyle = null;
  }

  public initShapeOption() {
    const option = new OptionAdapter().init(baseOption, 'baseText');
    const mergeObj = super.mergeOptions(option);
    this.shapeStyle = {
      width: this.partStyle.width,
      height: this.partStyle.height,
      ...mergeObj
    };
  }

  public initShapeGui() {
    this.guiAdapter = new GuiAdapter();
    this.guiOption = this.guiAdapter.initGuiOption({ content: this.shapeStyle });
  }

}
