import AbstractOption from '../../../common/abstractOption'

export default class Font extends AbstractOption {

  public config: any;

  constructor(shapeStyle?: Shape.Font) {
    super();
    this.config = this.init(shapeStyle);
  }

  public init(shapeStyle?: Shape.Font): object {
    const {
      fontSize = 14,
      color = '#333',
      fontStyle = 'bold',
      textAlign = 'center',
      alignItems = 'middle',
      fontWeight = 'normal'
    } = shapeStyle || {};

    return {
      fontSize,
      color,
      fontStyle,
      textAlign,
      alignItems,
      fontWeight
    };
  }

  public computed(): void { }

}