
import editScreenCfg from 'screenCfg/screen/edit';
import UUID from 'utils/uuid';

/**
 * project对象
 */
export default class ProjectObject {

    // 项目id
    public proId: string;

    // 项目名称
    public proName: string;

    // 网格间距
    public grid: number;

    // 是否显示网格
    public gridShow: boolean;

    // 文档高度
    public docClientH: number;

    // 文档宽度
    public docClientW: number;

    // 记录缩放的大小
    public panelScale: number;

    // 布局大小
    public layoutSize: any;

    // 用户定义屏幕高度
    public screenH: number = 0;

    // 用户定义屏幕高度
    public screenW: number = 0;

    // 画布id
    public divViewPort: string;

    // 框线选择的模型 [0: 'include', 1: 'intersect']，包含和相交
    public rubberBandMode: number;

    /**
     * 渲染方式
     * equal 等比缩放
     * HScale 宽度铺满，高度缩放
     * WScale 高度铺满，宽度缩放
     */
    public scaleMode: string;

    /**
     * 左侧tab点击活动tab,这个可以先不记录
     */
    public leftNavTabActive: string;

    /**
     * 右侧tab点击活动tab,这个可以先不记录
     */
    public rightNavTabActive: string;

    // 项目保存时间
    public saveTime: string;

    // 从配置表映射基本的属性值
    constructor(projectProp?: Model.Project) {
        this.setPureData(projectProp);
    }

    public setSaveTime(saveTime: string): void {
        this.saveTime = saveTime;
    }

    public setProName(proName: string): void {
        this.proName = proName;
    }

    public setGrid(grid: number): void {
        this.grid = grid;
    }

    public setGridShow(gridShow: boolean): void {
        this.gridShow = gridShow;
        
    }

    public setPanelScale(panelScale: number): void {
        this.panelScale = panelScale;
    }

    public setScreenH(screenH: number): void {
        this.screenH = screenH;
    }

    public setScreenW(screenW: number): void {
        this.screenW = screenW;
    }

    public setDocClientH(docClientH: number): void {
        this.docClientH = docClientH;
    }

    public setDocClientW(docClientW: number): void {
        this.docClientW = docClientW;
    }

    public setScaleMode(scaleMode: string) {
        this.scaleMode = scaleMode;
    }

    public setRubberBandMode(rubberBandMode: number) {
        this.rubberBandMode = rubberBandMode;
    }

    /**
     * 获取项目的纯数据
     * 
     * @returns {Model.Project} 
     */
    public getPureData(): Model.Project {
        return {
            proId: this.proId,
            proName: this.proName,
            panelScale: this.panelScale,
            layoutSize: this.layoutSize,
            screenH: this.screenH,
            screenW: this.screenW,
            divViewPort: this.divViewPort,
            grid: this.grid,
            gridShow: this.gridShow,
            scaleMode: this.scaleMode,
            saveTime: this.saveTime,
        };
    }

    /**
     * 设置项目的纯数据
     * 
     * @returns {Model.Project} 
     */
    public setPureData( projectProp: Model.Project ): void {
        if (!projectProp) {
            projectProp = editScreenCfg;
        }
        const {
            proId,
            divViewPort,
            proName,
            docClientH,
            docClientW,
            panelScale,
            layoutSize,
            screenH,
            screenW,
            grid,
            gridShow,
            scaleMode,
            saveTime,
            rubberBandMode,
        } = projectProp;

        this.divViewPort = divViewPort || editScreenCfg.divViewPort;
        this.layoutSize = layoutSize || editScreenCfg.layoutSize;
        this.docClientH = docClientH || editScreenCfg.docClientH;
        this.docClientW = docClientW || editScreenCfg.docClientW;
        
        this.proId = proId || this.createdProjectId();

        this.screenH = screenH || editScreenCfg.screenH;
        this.screenW = screenW || editScreenCfg.screenW;
        this.proName = proName || editScreenCfg.proName;
        this.panelScale = panelScale || editScreenCfg.panelScale;
        this.grid = grid || 10;
        this.gridShow = gridShow || true;
        this.scaleMode = scaleMode || 'equal';
        this.saveTime = saveTime || '';
        this.rubberBandMode = rubberBandMode || 1;
    }

    // private initProjectObj( projectProp?: Model.Project ) {
    //     if (!projectProp) {
    //         projectProp = editScreenCfg;
    //     }
    //     for (const [proKey, proVal] of Object.entries(projectProp)) {
    //         if (this.hasOwnProperty(proKey)) {
    //             const cfgValue = !_.isNil(editScreenCfg[proKey]) ? editScreenCfg[proKey] : '';
    //             this[proKey] = !_.isNil(proVal) ?  proVal : cfgValue;
    //         }
    //     }
    //     console.log(this);
    // }

    public destroyed() {
        this.divViewPort = null;
        this.proName = null;
        this.panelScale = null;
        this.layoutSize = null;
        this.docClientH = null;
        this.docClientW = null;
        this.screenH = null;
        this.screenW = null;
        this.proId = null;
        this.grid = null;
        this.gridShow = null;
        this.scaleMode = null;
        this.saveTime = null;
        this.rubberBandMode = null;
    }

    private createdProjectId(): string {
        return 'pro_' + new UUID().init();
    }
}
