import AbstractOption from '../../../common/abstractOption'

/**
 * 基础报表的数据配置项
 */
export default class TableData extends AbstractOption {

  public config: any;

  constructor(seriesData: Array<any>) {
    super();
    this.config = this.init(seriesData)
  }

  public init(seriesData: Array<any>) {
    if (!_.isArray(seriesData) || _.isEmpty(seriesData)) {
      return []
    }

    // add some表格数据处理逻辑
    return seriesData
  }

  public computed(): void { }

}