import Part from '../part/part';
import PartManage from '../part/partManage';

/**
 * group类
 */
export default class Groups extends Part {

    /**
     * 组件id
     */
    public groupId: string = '';

    /**
     * 组件id
     */
    public partType: string = '';

    /**
     * 依附在组件下的基本part列表，可以是模板和part，组件会被解析为part
     */
    public partGroup: Model.Part[] = [];

    constructor(uniObj) {
        super(uniObj);
        this.partType = uniObj.partType;
        this.handlePartGroup(uniObj.partGroup);
    }

    public initPart(id: string, size: any) {
        // throw new Error("Method not implemented.");
    }

    public setPublicStyle(option): void {
        if (!_.isObject(option)) {
            return;
        }
        // 修改part上的属性
        const entries = Object.entries(option);
        if (entries.length > 0) {
            entries.forEach(entry => {
                const key = entry[0];
                const value = entry[1];
                if (!_.isNil(this.partStyle[key])) {
                    this.partStyle[key] = value;
                }
            });
        }
    }

    public getPartAllProps(): Model.Group {
        const basicProps: Model.Part = super.getPartAllProps();
        const propsTem: Model.Group = {
            ...basicProps,
            groupId: this.groupId,
            partGroup: this.partGroup.map((part: Part): Model.Part => {
                return part.getPartAllProps();
            }),

        };
        return propsTem;
    }

    public setPartAllProps(partType: any): void {
        super.setPartAllProps(partType);
        const { groupId, partGroup } = partType;
        this.groupId = groupId;
        this.handlePartGroup(partGroup);
    }

    public setPartEvent(): void {
        throw new Error("Method not implemented.");
    }

    // public destroyed(): void {
        // throw new Error("Method not implemented.");
    // }

    private handlePartGroup(partGroup): void {
        this.partGroup = new PartManage().addPart(partGroup);
    }
}
