























































import Vue from 'vue';
export default Vue.extend({
    props: {
        side: {
            type: Object,
            default: () => {
                return {};
            },
        },
    },
    computed: {
        enable(): boolean {
            return this.side.disable;
        }
    },
    methods: {
        handleTool(event, child) {
            // console.log(child);
            if (child) {
                this.$emit('handleTool', child);
                return;
            }
            this.$emit('handleTool', this.side);
        }
    }
})
