










import Vue from 'vue';
export default Vue.extend({
    data() {
        return {
            value1: 0,
            max: 150,
            min: 10,
            step: 5,
        };
    },
    computed: {
        projectObj(): any {
            return this.$store.getters.projectObj;
        },
        panelScale: {
            get(): number {
                return this.projectObj.panelScale;
            },
            set( value: number ) {
                this.$store.commit('setPanelScale', value);
            },
        },
    },
    methods: {
        zoomIn() {
            if (this.panelScale - this.step < this.min) {
                return;
            }
            this.panelScale = this.panelScale - this.step;
        },
        zoomOut() {
            if (this.panelScale - this.step > this.max) {
                return;
            }
            this.panelScale = this.panelScale + this.step;
        },
        /**
         * 初始化监听事件接收器
         */
        editorEvtReceiver(isDestoyed?: boolean): void {
            const eventObjCfg = [
                {
                    name: 'zoomIn',
                    handle: this.zoomIn,
                },
                {
                    name: 'zoomOut',
                    handle: this.zoomOut,
                }
            ];
            for (const eventObj of eventObjCfg) {
                if (isDestoyed) {
                    this.$root.busRef.$off(eventObj.name);
                } else {
                    this.$root.busRef.$on(eventObj.name, (eventType: string) => {
                        eventObj.handle(eventType);
                    });
                }
            }
        },
    },
    mounted() {
        this.editorEvtReceiver();
    },
    beforeDestroy() {
        this.editorEvtReceiver(true);
    },
});
