const mutations = {
    updateClipBoard(state: Screen.state, clipBoard: any) {
        const { partGroup, rectCoord } = clipBoard;
        state.editor.clipBoard.updateClipBoard(partGroup, rectCoord);
    },
    clearClipBoard(state: Screen.state) {
        state.editor.lockPart.clearClipBoard();
    },
    cut_his(state: Screen.state) {},
    delete_his(state: Screen.state) {},
    paste_his(state: Screen.state) {}
}
const getter = {
    clipBoard: (state: Screen.state) => state.editor && state.editor.clipBoard,
}
export default {
    mutations,
    getter
}