
















































import Vue from 'vue';
import { mapMutations, mapGetters } from 'vuex';
export default Vue.extend({
    data() {
        return {
            cmptGroupName: '',
            cmptName: '默认',
            newCmptGroup: false,
            newCmptGroupName: '组件',
        };
    },
    props: {
        cmptModalShow: {
            type: Boolean,
            default: false,
        },
    },
    computed: {
        modalShow: {
            get() {
                return this.cmptModalShow;
            },
            set(value: boolean) {
                this.$emit( 'handleCmptModal', value);
            },
        },
        ...mapGetters([
            'cmptManage',
        ]),
        cmptGroupList(): Model.CmptGroup[] {
            return this.cmptManage.cmptGroupList;
        },
    },
    watch: {
        newCmptGroup( isNew: boolean ): void {
            if (!isNew) {
                return;
            }
            // 获取input聚焦
            this.$nextTick(() => {
                (this.$refs.newCmptGroupRef as any).focus();
            });
        },
        modalShow(isModelShow: boolean): void {
            if (isModelShow) {
                this.handleSelectGroup();
            }
        },
    },
    methods: {
        ...mapMutations([
            'add_cmpt',
            'add_cmpt_group',
        ]),
        handerCmptGroup() {
            this.newCmptGroup = !this.newCmptGroup;
        },
        addCmptGroup() {
            if (!this.newCmptGroupName) {
                // 输入为空代表取消输入
                // this.$message.warning('输入不能为空');
                this.newCmptGroup = !this.newCmptGroup;
                return;
            }
            
            let cmptGourpName: string;
            cmptGourpName = this.newCmptGroup ? this.newCmptGroupName : this.cmptGroupName;
            this.add_cmpt_group(cmptGourpName);

            this.handleSelectGroup();
            this.handerCmptGroup();
        },
        addCmpt() {
            if (this.newCmptGroup) {
                this.$message.error('请选择响应的组件类型');
                return;
            }
            if (!this.cmptGroupName) {
                this.$message.error('组件类型不能为空');
                return;
            }
            const cmpt = {
                cmptName: this.cmptName,
                cmptGroupId: this.cmptGroupName,
            };
            this.$emit( 'addCmpt', cmpt);
        },
        /**
         * 设置select选项为最后一项
         */
        handleSelectGroup() {
            const { length } = this.cmptGroupList;
            if (!length) {
                return;
            }
            // 设置select选项为新增项
            const lastCmptGroup: Model.CmptGroup = this.cmptGroupList[length - 1];
            this.cmptGroupName = lastCmptGroup.cmptGroupId;
        },
    },
});
