












import Vue from 'vue';
import { mapGetters } from 'vuex';
export default Vue.extend({
    name: 'Cell',
    props: {
        part: {
            type: Object,
            default: {
                partId: ''
            }
        }
    },
    computed: {
        ...mapGetters(['partEvent']),
        mouseDownEvt(): any {
            return this.partEvent.mouseDown
        },
        partId(): string {
            return this.part.partId
        },
        mountedId(): string {
            return 'mounted_' + this.partId
        },
        partStyle(): any {
            return this.part.partStyle
        },
        pStyle(): any {
            return {
                width: this.partStyle.width + 'px',
                height: this.partStyle.height + 'px',
                position: this.partStyle.position,
                left: this.partStyle.x + 'px',
                top: this.partStyle.y + 'px',
                transform: `rotate(${this.partStyle.angle}deg)`,
                backgroundColor: this.partStyle.backgroundColor || '',
                opacity: 0.7,
                zIndex: this.part.layer
            }
        },
        partGroup(): any {
            return this.part.partGroup
        },
        width(): number {
            return this.partStyle.width
        },
        height(): number {
            return this.partStyle.height
        }
    },
    watch: {
        width() {
            this.part.setPublicStyle({
                width: this.width
            })
        },
        height() {
            this.part.setPublicStyle({
                height: this.height
            })
        }
    },
    methods: {
        initPart(): void {
            const size = {
                width: this.partStyle.width,
                height: this.partStyle.height
            };
            this.part.init(`#${this.partId}`, size);
        },
        clickBaseDown(event: MouseEvent): void {
            this.$root.busRef.$emit('selectPartEvent', this.part, event);
        },
        mouseover(event: MouseEvent): void {
            this.$root.busRef.$emit('partMouseOver', this.part, event);
        },
        mouseout(event: MouseEvent): void {
            this.$root.busRef.$emit('partMouseOut', this.part, event);
        },
        partContextmenu(event: MouseEvent): void {
            this.$root.busRef.$emit('partContentMenu', this.part, event);
        }
    },
    mounted() {
        this.initPart();
    },
    beforeDestroy() {
        this.mouseout(null);
    }
})
