import access from '../../config/access/index';
import menuConfig from '../../config/menu/menuConifg';
import _ from 'lodash';
export default class MenuMapper {


    public activeMenu: any = [];
    /**
     * 根据权限完整的菜单（return final menu）
     * outAccess { 
     *  rect: false,  
     * }
     */
    public initMenu( outAccess? ) {
        const mapAccess = this.mapOuterAccess(outAccess);
        const staticMenu = this.mapStaticMenu(mapAccess);
        return staticMenu;
    }

    /**
     * 根据录入的权限，更新基础配置的权限
     * @param outAccess 登陆时获取的用户权限
     */
    private mapOuterAccess(outAccess?): any {
        if (!outAccess) {
            return access;
        }
        return access;
    }

    /**
     * 根据后台放回的数据，生成相应的菜单，目前主要是组合和模版
     */
    mapActiveMenu( access ): any {
        if( !Array.isArray(access) ){
            return;
        };
        
    }

    /**
     * 根据权限渲染静态菜单
     * @param access 配置禁止权限的文件和后台放回权限的深合并
     */
    private mapStaticMenu( access ): any {
        const origin = _.cloneDeep( access );
        const swapObj = {};
        const matchArray: any[] = [];
        this.matchNoAccess( access, swapObj, origin, matchArray );
        console.log(matchArray);
        
        for (const item of matchArray) {
            // let parentNode = item.parentNode;
            const childNode = item.childNode;
            // if ( !parentNode ) {
            //     parentNode = childNode;
            // }
            this.matchRenderMenu(menuConfig, childNode);
        }
        return menuConfig;
    }

    /**
     * 深度遍历权限的配置数据，缺陷是只能控制最底层的权限
     * @param access 源数据
     * @param swapObj 暂保存指定格式的数据（这里指无权限）
     * @param origin 源数据的备份，用于判断是否为1级node节点
     * @param matchArray 返回指定格式的数据（这里指无权限）
     */
    private matchNoAccess( access, swapObj, origin, matchArray  ): void {
        for (const [itKey, itValue] of Object.entries(access)) {
            const isOriginKey = _.isBoolean(origin[itKey]);
            if (_.isObject(itValue)) {
                if ( !isOriginKey ) {
                    swapObj = {};
                    // Object.assign(swapObj, { parentNode: itKey });
                }
                this.matchNoAccess( itValue , swapObj, origin, matchArray );
            } else {
                if ( !itValue ) {
                    // if ( isOriginKey ) {
                    //     swapObj = {};
                    //     Object.assign(swapObj, { parentNode: itKey });
                    // } else {
                        Object.assign(swapObj, { childNode: itKey });
                    // }
                    matchArray.push(_.cloneDeep(swapObj));
                }
            }
        }
    }

    private matchRenderMenu(renderConfig , childNode) {
        for (const it of renderConfig) {
            if ((it.type || it.childType) && (childNode === it.type || childNode === it.childType)) { 
                Object.assign(it, { access: false });
                return;
            } else {
                if (it.items && it.items.length) {
                    this.matchRenderMenu(it.items,  childNode);
                }
            }
            if (it.value && it.value === childNode) {
                if ( it.items.length ) {
                    Object.assign(it, { access: false });
                    return;
                }
            } else {
                if (it.items && it.items.length) {
                    this.matchRenderMenu(it.items,  childNode);
                }
            }
        }
    }

}
