import BaseChart from '../abstractChart'
import OptionAdapter from '../adapterOption/adapter/lineAndBar'
import GuiAdapter from '../adapterGui/adapter/stackBar'
import DataTransfer from '../../common/dataTransfer/echarts/index'
import { option, data } from '../config/defaultOption/noneAxisBar'

/**
 * 累加基本柱图模型
 * 其为垂直基本柱图的衍生模型
 * 即属性界面配置项有差异
 */
export default class NoneAxisBar extends BaseChart {

  public guiAdapter: any;
  public initialOption: any;
  public optionGroup: any
  public guiOption: any;
  public defaultOption: any;
  public staticData: any;

  constructor(partType: Model.Part) {
    super(partType);
    const defaults = partType.shapeStyle || option;
    this.staticData = partType.staticData || data;
    this.guiAdapter = null;
    this.initialOption = null;
    this.optionGroup = null;
    this.guiOption = [];
    this.defaultOption = null;

    super.getChartInitOption(defaults, this.staticData);
  }

  public initChartOption() {
    const adapter = new OptionAdapter();
    const initParam = super.getChartParams();
    this.initialOption = adapter.init(this.defaultOption, 'baseBar');
    this.optionGroup = adapter.getInstance();

    super.initChart(initParam);
    super.setChartOption(this.initialOption);
  }

  public initChartGui() {
    const adapter = new GuiAdapter();
    this.guiOption = adapter.init(this.initialOption, 'noneAxisBar');
    this.guiAdapter = adapter;
  }

  public updateShapeStyle(formObj: any) {
    const guiId = formObj.rootId;
    const propKey: string = formObj.mKey;

    // 数据系列
    if (guiId === 'series') {
      // 数据系列对应series数组除第一项柱样式之外的每一项
      // 因此界面配置项的seriesIndex的取值从1开始
      const seriesIndex = formObj.index;
      const seriesCopy = this.shapeStyle[propKey].map(
        (item: any, index: number) => {
          if (index === seriesIndex) {
            item = super.getUpdatedChartOption(item, this.updatedOption[propKey]);
          }
          return item;
        }
      );
      this.shapeStyle[propKey].length = 0;
      this.shapeStyle[propKey] = seriesCopy;
    }

    // 柱样式
    if (guiId === 'barStyle') {
      const path = formObj.path;
      if (path === 'series.itemStyle.color') {
        // 更新柱样式的柱背景色，对应series的第一项,
        // 属性为series.itemStyle.color
        this.shapeStyle[propKey][0] = super.getUpdatedChartOption(
          this.shapeStyle[propKey][0],
          this.updatedOption[propKey]
        );
      } else {
        // 更新除柱背景色外的其他样式，对应series的所有项，
        // 但barCategoryGap、barGap对应只取数据系列最后一项值作为生效值
        // 为统一标准，series更新时统一更新每一项所有属性
        const seriesCopy = this.shapeStyle[propKey].map(
          (item: any) => {
            item = super.getUpdatedChartOption(item, this.updatedOption[propKey]);

            // 柱间间距处理
            const { $barGap = 30, $barCategoryGap = 20 } = item;
            item.barGap = `${$barGap}%`;
            item.barCategoryGap = `${$barCategoryGap}%`;
            return item;
          }
        );
        this.shapeStyle[propKey].length = 0;
        this.shapeStyle[propKey] = seriesCopy;
      }
    }

    // 公共属性
    if (!['series', 'barStyle'].includes(guiId)) {
      this.shapeStyle[propKey] = super.getUpdatedChartOption(
        this.shapeStyle[propKey],
        this.updatedOption[propKey],
      );
    }
  }

  public updateChartOption(formObj: any) {
    const guiId = formObj.rootId;
    const propKey: string = formObj.mKey;
    const shapeStyle = _.cloneDeep(this.shapeStyle[propKey]);
    const currentOption = {
      [propKey]: shapeStyle
    };

    // 柱子样式初始化时取series第一项作为初始化项，这样才可保证
    // 柱样式设置有初始化的柱背景色值，但柱样式或系列更新时，
    // 需隐藏作为柱背景的series第一项label
    if (guiId === 'barStyle' || guiId === 'series') {
      currentOption['series'][0]['label']['show'] = false;
    }

    // series、双y轴等特殊项需更新整项属性
    const option = propKey === 'series' ? currentOption : this.updatedOption;
    super.setChartOption(option);
  }

  public isNeedComputedYAxis() {
    return false;
  }

  public setChartSeriesOption(defaults: Chart.Option, seriesData: any[]) {
    if (!seriesData.length) {
      return [];
    }

    const series = _.cloneDeep(defaults.series);
    const dataTransfer = new DataTransfer();
    const dataList = dataTransfer.getSingleSeriesData(seriesData);
    const stackData = dataTransfer.getStackSingleSeriesData(seriesData);
    const diff = dataList.length - series.length;
    let addIndex = 0;

    // 累加柱图series.length = 1 + dataList.length
    // 合法的系列数据数量大于默认系列数量时自动添加默认系列项
    while (diff + 1 > 0 && diff + 1 > addIndex) {
      series.push(
        {
          itemStyle: { color: '' },
          tooltip: { show: true },
          $barGap: -100,
          $barCategoryGap: 30
        }
      );
      addIndex++;
    }

    // 合法的系列数据数量小于默认系列数量时自动清除多余的默认系列项
    if (dataList.length && diff < 0) {
      series.length = dataList.length + 1;
    }

    series.forEach((item: any, index: number) => {
      const name = index === 0 ? '' : `系列名`;
      const itemColor = index === 0 ?
        'rgba(0,0,0,0.05)' :
        super.getChartSeriesColor(index - 1);
      item.name = item.name ? item.name : name;
      item.itemStyle.color = item.itemStyle.color ? item.itemStyle.color : itemColor;
      item.data = index === 0 ? stackData : (dataList[0] || []);

      // 第一项固定为最大值系列
      if (index === 0) {
        item.z = 2;
        item.tooltip.show = false;
      }
    });
    return series;
  }

  public setChartLegendName() {
    return false
  }

}


