

































































































import Vue from 'vue';
import { mapGetters, mapMutations } from 'vuex';
import ImageDialog from './imageDialog.vue';
export default Vue.extend({
    name: 'GlobalStyle',
    components: {
        ImageDialog,
    },
    data() {
        return {
            inputStep: 10,
            imageModalShow: false,
            predefineColors: [
                '#ff4500',
                '#ff8c00',
                '#ffd700',
                '#90ee90',
                '#00ced1',
                '#1e90ff',
                '#c71585',
                'rgba(255, 69, 0, 0.68)',
                'rgb(255, 120, 0)',
                'hsv(51, 100, 98)',
                'hsva(120, 40, 94, 0.5)',
                'hsl(181, 100%, 37%)',
                'hsla(209, 100%, 56%, 0.73)',
                '#c7158577'
            ],
        };
    },
    computed: {
        ...mapGetters([
            'projectObj',
            'currentPage',
        ]),
        rubberBandMode: {
            set(scaleType: boolean): void {
                this.setRubberBandMode(Number(scaleType));
            },
            get(): string {
                return this.projectObj.rubberBandMode.toString();
            },
        },
        scaleMode: {
            set(scaleType: boolean): void {
                this.setScaleMode(scaleType);
            },
            get(): boolean {
                return this.projectObj.scaleMode;
            },
        },
        showGrid: {
            set(show: boolean): void {
                this.setGridShow(show);
            },
            get(): boolean {
                return this.projectObj.gridShow;
            },
        },
        screenW: {
            set(screenW) {
                this.setScreenW(screenW);
            },
            get(): boolean {
                return this.projectObj.screenW;
            }
        },
        screenH: {
            set(screenH) {
                this.setScreenH(screenH);
            },
            get(): boolean {
                return this.projectObj.screenH;
            }
        },
        backgroudColor: {
            set(color) {
                const opt = { backgroundColor: color, page: this.currentPage }
                this.edit_page(opt);
            },
            get(): string {
                let bgColor;
                if (!this.currentPage) {
                    bgColor = '#eee';
                } else {
                    bgColor = this.currentPage.backgroundColor;
                }
                return bgColor;
            }
        },
        backgroudImage: {
            get(): string {
                return this.currentPage.backgroundImage;
            }
        }
    },
    methods: {
        ...mapMutations([
            'setGrid',
            'setGridShow',
            'setScreenH',
            'setScreenW',
            'edit_page',
            'setScaleMode',
            'setRubberBandMode',
        ]),
        handleChange() {
            console.log("lalal");
        },
        showImageModal() {
            this.imageModalShow = true;
        },
        handleImageModal( val ) {
            this.imageModalShow = val;
        },
        getImgSrc( srcImg ) {
            this.imageModalShow = false;
            const opt = { backgroundImage: srcImg, page: this.currentPage };
            this.edit_page(opt);
        },
        resetBackground() {
            const opt = { backgroundColor: '', backgroundImage: '', page: this.currentPage };
            this.edit_page(opt);
        }
    },
});
