/**
 * element对象
 * chart、shape、报表等基类
 */
import Part from '../part/part';
import { AbstractElement } from '../abstract/abstractElement';
import PartDataSource from './common/dataCfgModel/dataSource';

export class Element extends Part implements AbstractElement {

  public instance: any;

  public shapeStyle: any;

  // 数据配置项目
  public partData: PartDataSource;

  // 事件配置,交互配置
  public partEvent: any;

  constructor(part: Model.Element) {
    super(part);
    this.shapeStyle = part.shapeStyle;
    this.partData = new PartDataSource(part.partData);
  }

  /**
   * @override
   * 重新getPartAllProps
   * 新增数据源
   */
  public getPartAllProps(): Model.Element {
    const elementProps: Model.Element = super.getPartAllProps();
    elementProps.partData =  this.partData.getPartAllProps();
    return elementProps;
  }

  // TODO: 子类应重写具体设置方法
  public setPartStyle(option: Model.Element): void {}

  // TODO: 需对接数据的子类应重写此方法
  public getPartStaticData(): any { }

  // TODO: 需对接数据的子类应重写此方法
  public setPartStaticData(option: any): void { }

  // TODO: 需对接数据的子类应重写此方法
  public setPartActiveData(option: any): void { }

  // TODO: 需进行数据系列新增、删除操作的子类应重写此方法
  public setPartGuiSeries(setType: string, seriesOption: any): void { }

  public setPartData(option: any): void {
    const mode = option.mode;
    this.partData.updateProps(option, mode);
  }

}
