/**
 * 基础的element属性配置界面适配器
 * 如图表、基础图形、基础报表
 */
export default abstract class Adapter {

  /**
   * 当前element的shapeStyle的series.length
   */
  abstract seriesLength: number;

  /**
   * 获取当前element默认的属性界面配置
   * @param type element类型
   * @return 当前element属性界面默认配置对象
   */
  abstract getDefaultOption(type: string): object;

  /**
   * 创建当前element的属性界面配置
   * @param guiObj 当前element的属性界面配置对象，如图表
   *  {
   *    global: {
   *      level: 0
   *      name: "全局样式"
   *      component: null
   *      children: [{…}, {…}, {…}]
   *    }
   *    title:{
   *      ...
   *    }
   *    ...
   *  }
   * @return 当前element的属性界面列表
   */
  abstract createGuiGroup(guiObj: object): any[];

  /**
   * 根据当前element的shapeStyle的series.length创建属性界面series系列
   * @param defaultSeries 默认的series属性配置对象
   * @param length 某种series.type, 适用于不同series.type的组合，如图表折线柱图类型
   * @return 创建的属性界面series系列
   */
  abstract createSeriesGui(defaultSeries: any, type?: string): void;

  /**
   * 标识当前element属性界面series系列的项
   * @param item series系列的当前项
   * @param index series系列的当前index
   */
  abstract createSeriesIndex(item: any, index: number): void;

  /**
   * 根据当前element属性对象值初始化属性界面配置项对应值
   * @param guiOption 当前element属性界面配置列表，如图表
   *  [
   *    {
   *      level: 0
   *      name: "全局样式"
   *      component: null
   *      children: [{…}, {…}, {…}]
   *    }
   *    {
   *      level: 0,
   *      name: '标题',
   *      ...
   *    }
   *    ...
   *  ]
   * @param defaultStyle 默认的当前element属性对象(shapeStyle)，如图表
   *  {
   *    grid: {
   *      bottom: 60
   *      left: 60
   *      right: 66
   *      top: 80
   *      ...
   *    }
   *    series: [
   *      {
   *        ...
   *      }
   *      ...
   *    ]
   *    ...
   *  }
   * @return 当前element属性界面初始值后的配置列表
   */
  abstract initGuiOption(guiOption: any[], defaultStyle: any): any[];

  /**
  * 根据表单组件修改的值更新当前element的属性界面配置项对应值
  * @param guiOption 当前element属性界面配置列表，如图表
  *  [
  *    {
  *      level: 0
  *      name: "全局样式"
  *      component: null
  *      children: [{…}, {…}, {…}]
  *    }
  *    {
  *      level: 0,
  *      name: '标题',
  *      ...
  *    }
  *    ...
  *  ]
  * @param currentStyle 当前更新的element属性对象(shapeStyle的某个属性)，如图表
  *  {
  *    grid: {
  *      bottom: 100
  *      left: 60
  *      right: 60
  *      top: 60
  *      ...
  *    }
  *  }
  * @param formObj 当前图形的表单组件修改的值，如图表
  *  {
  *    rootId: "legend"
  *    type: "inputNumber"
  *    formOption: {
  *      max: 36
  *      min: 0
  *      step: 0.5
  *    }
  *    mKey: "legend"
  *    path: "legend.textStyle.fontSize"
  *    model: 12.5
  *    val: 12.5
  *  }
  * @return 当前element属性界面更新值后的配置列表
  */
  abstract updateGuiOption(guiOption: any[], currentStyle: any, formObj: any): any[];

  /**
   * 初始化当前element的属性界面配置项
   * @param defaultObj 当前element的属性对象(shapeStyle)
   * @param type element类型
   * @return 当前element属性界面配置列表
   */
  abstract init(defaultObj: any, type: string): any[];

  /**
   * 公共的属性界面配置项对应的默认值赋值
   * @param key 当前element属性对象(shapeStyle)的key值
   *  如图表的'gird' 'xAxis'等
   * @param value 当前element属性对象(shapeStyle)的value值
   *  如图表gird的value值：
   *  {
   *     bottom: 100
   *     left: 60
   *     right: 60
   *     top: 60
   *  }
   * @param option 当前element属性界面配置列表
   */
  public assignPublicGuiValueDeep(key: string, value: any, option: any[]): void {
    for (let item of option) {
      const hasChild = item.children.length
      if (!hasChild) {
        const mKey = item.component.mKey
        if (mKey === key) {
          const path = item.component.path
          const modelPath = path.replace(`${mKey}.`, ``)
          item.component.model = _.get(value, modelPath)
        }
      } else {
        this.assignPublicGuiValueDeep(key, value, item.children)
      }
    }
  }

  /**
   * series属性界面配置项对应的默认值赋值
   * @param seriesGui 当前element数据系列配置的每一项
   * @param seriesStyle 当前element数据系列配置的每一项对应的series属性对象
   */
  public assignSeriesGuiValueDeep(seriesGui: any, seriesStyle: any): void {
    const option = seriesGui.children
    for (let item of option) {
      const hasChild = item.children.length
      if (!hasChild) {
        const mKey = item.component.mKey
        const path = item.component.path
        const modelPath = path.replace(`${mKey}.`, ``)
        item.component.model = _.get(seriesStyle, modelPath)
      } else {
        this.assignSeriesGuiValueDeep(item, seriesStyle)
      }
    }
  }

}