



























































import Vue from 'vue';
import { mapState, mapGetters, mapMutations } from 'vuex';
import ToolItem from './toolItem.vue';
import { toolConfig, ToolConfig } from './toolConfig';
import keyCodeConfig from './newToolConfig';
import dayjs from 'dayjs';

export default Vue.extend({
    name: 'TopNav',
    components: {
        ToolItem,
    },
    data() {
        return {
            inputProName: '',
            editProName: false,
            toolConfigList: _.cloneDeep(toolConfig),
            ctmConfig: [],
        }
    },
    props: {
        layoutSize: {
            type: Object,
            default: {},
        }
    },
    computed: {
        ...mapState({
            screen: (state: any) => state.screen,
        }),
        ...mapGetters([
            'projectObj',
        ]),
        proName(): string {
            return this.projectObj.proName;
        },
        pureModelMapper(): any {
            return this.screen.pureModelMapper;
        },
        topNavSty(): {height: string} {
            return {
                height: `${this.layoutSize.topBarHeight}px` || '240px',
            };
        },
    },
    watch: {
        editProName(isEdit) {
            if (isEdit) {
                this.$nextTick( () => {
                    this.inputProName = this.proName;
                    this.$refs.proNameRef.focus();
                });
            }
        }
    },
    methods: {
        ...mapMutations([
            'setProName',
            'setSaveTime',
        ]),
        /**
         * 放回大屏
         */
        backToScreen() {
            this.$router.push('screenMg');
        },
        routePreScreen() {
            const {href} = this.$router.resolve({
                name: 'preview',
            });
            window.open(href, '_blank');
            // this.$router.push('preview');
        },
        /**
         * 保存所有的大屏数据
         */ 
        saveScreen() {
            const allPureData = this.pureModelMapper.getSavePureModel();
            console.log(JSON.stringify(allPureData));
            const nowTime = dayjs().format('YYYY-MM-DD HH:mm');
            this.setSaveTime(nowTime);
        },
        handleTool(side) {
            // 禁止使用的，就不要传递事件操作
            if (side.disable) {
                return;
            }

            const unexpectType = ['clipboard', 'layer'];
            const eventtype = side.type;
            if (unexpectType.includes(eventtype)) {
                return;
            }
            if (eventtype === 'save') {
                this.saveScreen();
            }
            
            this.$root.busRef.$emit(eventtype, eventtype);
        },
        onToolChange() {
            this.$root.busRef.$on('enableWatch', (message) => {
                if (_.isNil(message)) {
                    return;
                }
                if (!Array.isArray(message)) {
                    message = [message];
                }
                const copyMsg = _.cloneDeep(message);
                copyMsg.forEach( ( emit: any ) => {
                    const cando: boolean = emit.enable;
                    this.mapEnableCfg(emit);
                });
            });
        },
        // 映射列表配置项的使能
        mapEnableCfg( enableObj: any) {
            // 对放回格式的数据进行特殊的处理，例如locking，不需要parent,将child升级为parent
            // const toolExceptParent = ['layer', 'pasteMouse'];
            // if (toolExceptParent.includes(toolObj.child)) {
            //     toolObj.parent = toolObj.child;
            //     delete toolObj.child;
            // }
            // 需要做一些特殊的处理，因为这里不需要处理layer
            const exceptItem = ['layer', 'pasteMouse'];
            if (exceptItem.includes(enableObj.child)) {
                return;
            }
            // 这个先拎出来
            if (enableObj.child === 'delete') {
                enableObj.parent = enableObj.child
            }
            
            // 更新具体的菜单配置选项的使能，比如复制原本为false，现在更新为true
            const canDo = !enableObj.enable;
            const updateCtmCfj = (ctmCfgList: any, ctmIndex: number) => {
                // 如果值相同就没必要更新
                if (ctmCfgList[ctmIndex].disable === canDo) {
                    return;
                }
                const ctmItem: any = _.cloneDeep(ctmCfgList[ctmIndex]);
                ctmItem.disable = canDo;
                ctmCfgList.splice(ctmIndex, 1, ctmItem);
            };

            // 在配置列表中，找到对应的配置项，并执行更新使能的方法
            this.ctmConfig.some((item: any) => {
                if (enableObj.parent === item.key) {
                    const secondItemList = item.value;
                    const secondIndex = secondItemList.findIndex((chidItem: any) => {
                        return chidItem.type === enableObj.child;
                    });
                    updateCtmCfj(secondItemList, secondIndex);
                    return true;
                }
            });
        },
        /**
         * 格式化keyCode的配置列表
         */
        initFormatCtmConfig( unexpectList ) {
            const copyKeyCodeConfig = _.cloneDeep(keyCodeConfig);
            unexpectList.forEach( (unxpItem: any) => {
                const hasChild = unxpItem.child;
                // 这里可以直接修改数组
                copyKeyCodeConfig.some( (keyCodeItem: any, index: number) => {
                    if (!hasChild && unxpItem.perent === keyCodeItem.key) {
                        // 删除只有parent的大项
                        copyKeyCodeConfig.splice(index, 1);
                        return true;
                    } else if (hasChild && unxpItem.perent === keyCodeItem.key) {
                        // 删除parent下的child的项
                        const keyCodeValueList = keyCodeItem.value;
                        hasChild.forEach( ( keyCodeType: string[] ) => {
                            keyCodeValueList.some( (keyCode: any, point: number) => {
                                if (keyCode.type === keyCodeType) {
                                    keyCodeValueList.splice(point, 1);
                                }
                            });
                        });
                        return true;
                    } 
                });
            });
            this.ctmConfig = copyKeyCodeConfig;
        },
        sortCemConfig() {
            const sortArray: any[] = [];
            const sortString = ['save', 'history', 'layer', 'clipboard', 'group', 'lock', 'delete'];
            const getCtmIndex = (key) => {
                const ctm = this.ctmConfig.find( (it) => {
                    return it.key === key;
                });
                return ctm ? ctm : null;
            };
            sortString.forEach( (ctm) => {
                const cursorCtm = getCtmIndex(ctm);
                if (cursorCtm) {
                    sortArray.push(cursorCtm);
                }
            });
            this.ctmConfig = sortArray;
        },
        /**
         * 格式化keyCode的配置列表
         * 不需要 - 粘贴到鼠标
         * 不需要 - 历史
         */
        initFormatCtm() {
            const unexpectList = [
                {
                    perent: 'clipboard',
                    child: ['pasteMouse'],
                },
                {
                    perent: 'lib',
                },
                {
                    perent: 'scale',
                }
            ];
            this.initFormatCtmConfig(unexpectList);
            this.sortCemConfig();
        },
        handleClickOutSide() {
            this.setProName(this.inputProName);
            this.editProName = false;
        }
    },
    mounted() {
        this.initFormatCtm();
        this.onToolChange();
    },
    beforeDestroy() {
         this.$root.busRef.$off('enableWatch');
    },
})
