import AbstractOption from '../../../common/abstractOption'

export default class BorderRadius extends AbstractOption {

  public config: any;

  constructor(shapeStyle?: Shape.BorderRadius) {
    super();
    this.config = this.init(shapeStyle);
  }

  public init(shapeStyle?: Shape.BorderRadius): object {
    const {
      borderRadius = 0
    } = shapeStyle || {};

    return {
      borderRadius
    };
  }

  public computed(): void { }

}