import Vue from 'vue';
import Router from 'vue-router';
Vue.use(Router);

import editScreen from '@/views/screen/application/panel/edit/editScreen.vue';
const preScreen = () => import('@/views/screen/application/panel/preview/preScreen.vue');
const dataV = () => import('@/views/dataV/layout/layout.vue');
const screenMg = () => import('@/views/dataV/userScreen/screenMg.vue');
const dataMap = () => import('@/views/dataV/dataMap/dataMap.vue');


export default new Router({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes: [{
        path: '/',
        name: 'editScreen',
        component: editScreen,
    },
    {
        path: '/preview',
        name: 'preview',
        component: preScreen,
    },
    {
        path: '/dataV',
        name: 'dataV',
        component: dataV,
        children: [
            {
                path: '/screenMg',
                name: 'screenMg',
                component: screenMg,
            },
            {
                path: '/dataMap',
                name: 'dataMap',
                component: dataMap,
            }
        ]
    }],
});
