import ContextMenuObject from '../base/contextMenuObject';

export default class ContextMenuEvent {
    
    contextMenu: ContextMenuObject;

    constructor() {
        this.contextMenu = new ContextMenuObject();
    }

    public initContextMenu( eventTemp ) {
        this.contextMenu.mapEventObject( eventTemp );
    }

    public setCtmType( ctmType: string ): void {
        this.contextMenu.setCtmType(ctmType);
    }

    public setCtmTarget( ctmTarget: string ): void {
        this.contextMenu.setCtmTarget(ctmTarget);
    }

    public setShowMenu( showMenu: boolean ): void {
        this.contextMenu.setShowMenu(showMenu);
    }
}