






// https://github.com/think2011/ref-line
import Vue from 'vue';
import { mapState } from 'vuex';
import _ from 'lodash';
interface Position {
    top: number;
    height: number;
    bottom: number;
    left: number;
    width: number;
    right: number;
}
export default Vue.extend({
    data() {
        return {
            gap: 3,
            lines: [{
                    name: 'xt',
                    xStyle: true,
                    yStyle: false,
                    show: false,
                    style: {
                        top: 0
                    }
                },
                {
                    name: 'xc',
                    xStyle: true,
                    yStyle: false,
                    show: false,
                    style: {
                        top: 0
                    }
                },
                {
                    name: 'xb',
                    xStyle: true,
                    yStyle: false,
                    show: false,
                    style: {
                        top: 0
                    }
                },
                {
                    name: 'yl',
                    xStyle: false,
                    yStyle: true,
                    show: false,
                    style: {
                        left: 0
                    }
                },
                {
                    name: 'yc',
                    xStyle: false,
                    yStyle: true,
                    show: false,
                    style: {
                        left: 0
                    }
                },
                {
                    name: 'yr',
                    xStyle: false,
                    yStyle: true,
                    show: false,
                    style: {
                        left: 0
                    }
                },
            ]
        }
    },
    props: {},
    computed: {
        ...mapState({
            basePartList: (state: any) => state.screen.pageManage.partContainer,
            partSelection: (state: any) => state.screen.editor.partSelection,
            selectPartId: (state: any) => state.screen.editor.partId,
        }),
        panelScale(): number {
            return this.$store.getters.projectObj.panelScale;
        },
        calScale(): number {
            return this.panelScale / 100;
        },
        selectGroup() {
            return this.partSelection.selectGroup;
        },
        selectPart() {
            return this.partSelection.selectPart;
        },
        selectType() {
            return this.partSelection.type;
        },
    },
    methods: {
        /**
         * @param dragObj { x,y, partId, type } 拖拽transform实时更新的数据，用于多选
         */
        check(dragObj) {
            this.uncheck();
            const dragRect = this._getDragRect(dragObj);
            if (this.basePartList.length === 1) {
                return;
            }
            this.basePartList.forEach((item) => {
                if (this._checkPart(item)) {
                    return;
                };
                let {
                    top,
                    height,
                    bottom,
                    left,
                    width,
                    right
                } = item.refLine;
                let dragWidthHalf = dragRect.width / 2;
                let itemWidthHalf = width / 2;
                let dragHeightHalf = dragRect.height / 2;
                let itemHeightHalf = height / 2;
                let conditions = {
                    top: [
                        // xt-top
                        {
                            isNearly: this._isNearly(dragRect.top, top),
                            lineNode: 'xt',
                            lineValue: top,
                            dragValue: top
                        },
                        // xt-bottom
                        {
                            isNearly: this._isNearly(dragRect.bottom, top),
                            lineNode: 'xt',
                            lineValue: top,
                            dragValue: top - dragRect.height
                        },
                        // xc
                        {
                            isNearly: this._isNearly(dragRect.top + dragHeightHalf, top + itemHeightHalf),
                            lineNode: 'xc',
                            lineValue: top + itemHeightHalf,
                            dragValue: top + itemHeightHalf - dragHeightHalf
                        },
                        // xb-top
                        {
                            isNearly: this._isNearly(dragRect.bottom, bottom),
                            lineNode: 'xb',
                            lineValue: bottom,
                            dragValue: bottom - dragRect.height
                        },
                        // xb-bottom
                        {
                            isNearly: this._isNearly(dragRect.top, bottom),
                            lineNode: 'xb',
                            lineValue: bottom,
                            dragValue: bottom
                        },
                    ],

                    left: [
                        // yl-left
                        {
                            isNearly: this._isNearly(dragRect.left, left),
                            lineNode: 'yl',
                            lineValue: left,
                            dragValue: left
                        },
                        // yl-right
                        {
                            isNearly: this._isNearly(dragRect.right, left),
                            lineNode: 'yl',
                            lineValue: left,
                            dragValue: left - dragRect.width
                        },
                        // yc
                        {
                            isNearly: this._isNearly(dragRect.left + dragWidthHalf, left + itemWidthHalf),
                            lineNode: 'yc',
                            lineValue: left + itemWidthHalf,
                            dragValue: left + itemWidthHalf - dragWidthHalf
                        },
                        // yr-left
                        {
                            isNearly: this._isNearly(dragRect.right, right),
                            lineNode: 'yr',
                            lineValue: right,
                            dragValue: right - dragRect.width
                        },
                        // yr-right
                        {
                            isNearly: this._isNearly(dragRect.left, right),
                            lineNode: 'yr',
                            lineValue: right,
                            dragValue: right
                        },
                    ]
                }


                for (let key in conditions) {
                    // 遍历符合的条件并处理
                    conditions[key].forEach((condition) => {
                        if (!condition.isNearly) {
                            return;
                        }
                        // 改变拖拽图像的style
                        //  dragNode.style[key]           = `${condition.dragValue}px`
                        // 改变显示线的style值
                        this._handleLineNode(key, condition);
                    })
                }
            })
        },
        /**
         * 重置参考线
         */
        uncheck() {
            this.lines.forEach(item => {
                item.show = false;
            });
        },
        /**
         * @private
         * 检查part是否存在partGroup中
         */
        _checkPart(part): boolean {
            let isSelect: boolean = false;
            if (this.selectType === 'single') {
                if (part.partId === this.selectPartId) {
                    isSelect = true;
                }
            } else if (this.selectType === 'multiply') {
                this.selectGroup.some(item => {
                    if (item.partId === part.partId) {
                        isSelect = true;
                        return true;
                    }
                });
            }
            return isSelect;
        },
        /**
         * @param dragObj
         * 获取拖拽的方位信息
         */
        _getDragRect(dragObj): Position {
            let dragRect: Position = {
                top: 0,
                bottom: 0,
                left: 0,
                right: 0,
                height: 0,
                width: 0
            };
            if (this.selectType === 'single') {
                const dragPart: any = _.find(this.basePartList, {
                    partId: this.selectPartId
                });
                dragRect = dragPart.refLine;
            } else if (this.selectType === 'multiply') {
                const {
                    width,
                    height
                } = this.selectPart;
                const {
                    x,
                    y
                } = dragObj;
                const groupRefLine = {
                    top: y,
                    bottom: y + height,
                    left: x,
                    right: x + width,
                    height,
                    width
                }
                dragRect = groupRefLine;
            }
            return dragRect;
        },
        _handleLineNode(key, condition) {
            const lineIndex = this.lines.findIndex((item) => {
                return item.name === condition.lineNode;
            });
            if (lineIndex === -1) {
                return;
            }
            this.lines[lineIndex].style[key] = condition.lineValue * this.calScale + 'px';
            this.lines[lineIndex].show = true;
        },
        _isNearly(dragValue, targetValue) {
            return Math.abs(dragValue - targetValue) <= this.gap;
        }
    },
});
