<template>
	<div
		:style="styleComp"
		style="display: block" 
		v-show="show">
		<slot>
		</slot>
	</div>
</template>
<script>
export default {
	name: 'context-menu',
	data() {
		return {
			triggerShowFn: () => {},
			triggerHideFn: () => {},
			x: 0,
			y: 0,
			style: {},
			binded: false,
			isPartCtm: true,
		}
	},
	props: ['show', 'target', 'styleComp'],
	
	watch: {
		show(val, oldval) {
			if (this.show) {
				this.bindHideEvents()
			} else {
				this.unbindHideEvents()
			}
		},
		target(val, oldval) {
			this.bindEvents()
		}
	},
	methods: {
		// 初始化事件
		bindEvents () {
			this.$nextTick(() => {
				if (!this.target || this.binded) return; 
				this.triggerShowFn = this.contextMenuHandler.bind(this);
				this.target.addEventListener('contextmenu', this.triggerShowFn);
				this.binded = true;
			})
		},
		// 取消绑定事件
		unbindEvents () {
			if (!this.target) return
			this.target.removeEventListener('contextmenu', this.triggerShowFn)
		},
		// 绑定隐藏菜单事件
		bindHideEvents () {
			this.triggerHideFn = this.clickDocumentHandler.bind(this)
			document.addEventListener('mousedown', this.triggerHideFn)
			document.addEventListener('mousewheel', this.triggerHideFn)
		},
		// 取消绑定隐藏菜单事件
		unbindHideEvents () {
			document.removeEventListener('mousedown', this.triggerHideFn)
			document.removeEventListener('mousewheel', this.triggerHideFn)
		},
		// 鼠标按压事件处理器
		clickDocumentHandler (e) {
			this.$emit('handleShowCtm', false, e)
		},
		// 右键事件事件处理
		contextMenuHandler (e) {
			this.$root.busRef.$emit('canvasCtm', e);
			this.x = e.clientX
			this.y = e.clientY
			this.layout()
			this.$emit('handleShowCtm', true, e)
			e.preventDefault()
		},
		// 布局
		layout () {
			this.style = {
				left: this.x + 'px',
				top: this.y + 'px'
			}
		}
	},
	mounted () {
		this.bindEvents()
	},
	beforeDestroy(){
		this.unbindEvents()
	},
}
</script>
