const mutations = {
    setIsHoverPart(state: Screen.state, isHoverPart: boolean) {
        state.editor.hoverPart.setIsHoverPart(isHoverPart);
    },
    mapHoverPartStyle(state: Screen.state, partStyle: any) {
        state.editor.hoverPart.mapHoverPartStyle(partStyle);
    },
};
const getter = {
    hoverPart: (state: Screen.state) => state.editor && state.editor.hoverPart,
};
export default {
    mutations,
    getter
};
