import cmptMap from './form/index'

export default {
  name: 'smart-component',
  functional: true,
  props: {
    opts: {
      type: Object
    }
  },
  render: (h: any, ctx: any) => {
    function appropriateComponent() {
      const typeName = ctx.props.opts.type
      for (let [cmptName, vueCmpt] of Object.entries(cmptMap)) {
        if (cmptName === typeName) return vueCmpt
      }
    }

    return h(
      appropriateComponent(),
      {
        props: {
          opts: ctx.props.opts
        },
        on: {
          'on-data-change': function (opts: any, val: any) {
            ctx.listeners.onChange(opts, val)
          }
        }
      }
    )
  }
}