<script>
const echIcon = [
    'baseLine', 
    'areaLine', 
    'dblLine', 

    'noneAxisBar', 
    'verticalStackBar', 
    'horizontalStackBar',
    'verticalGroupBar',
    'horizontalGroupBar',
    'baseLineBar',

    'basePie',
    'comparePie',
    'ratioPie',
    'rosePie',

    'baseTable',
    'simpleText'];
export default {
    props: {
        name: {
            type: String,
            default: 'baseLine',
            validator:  (value) => {
                // 这个值必须匹配下列字符串中的一个
                return echIcon.indexOf(value) !== -1
            }
        }
    },
    computed: {
        currentIcon() {
            return this.name;
        }
    }
};
</script>
