






















































































import partTransform from '../../../component/dragSelect/partTransform.vue';
import RefLine from '../referenceLine/reLine.vue';
import Vue from 'vue';
import { mapState, mapGetters } from 'vuex';
declare const _: any;
export default Vue.extend({
    name: 'Transform',
    components: {
      partTransform,
      RefLine,
    },
    data() {
        return {
            // isHoverPart: false
            refline: {},
        };
    },
    mounted() {
        this.initRef();
    },
    props: {
        noZoomStyle: {
            type: Object,
            default: {
                top: "0%",
                left: "0%",
                width: "0%",
                height: "0%",
            }
        },
        noZoomScreenStyle: {
            type: Object,
            default: {
                width: "0px",
                height: "0px",
            }, 
        },
        panelScale: {
            type: Number,
            default: 50,
        },
        isPartMouseDown: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        ...mapState({
            basePartList: (state: any) => state.screen.pageManage.partContainer,
            selectTransform: (state: any) => state.screen.editor,
        }),
        ...mapGetters([
            'projectObj',
        ]),
        grid() {
            return this.projectObj.grid;
        },
        gridArray() {
            return [this.grid, this.grid];
        },
        // 取得hoverPart模块
        hoverPart() {
            return this.selectTransform.hoverPart;
        },
        // 取得partSelection模块
        partSelection(): any {
            return this.selectTransform.partSelection;
        },
        // 取得transform模块
        transform() {
            return this.selectTransform.transform;
        },
        // 取得selectPart
        selectPart() {
            return this.partSelection.selectPart;
        },
        selectGroup() {
            return this.partSelection.selectGroup;
        },
        calScale() {
            return this.panelScale/100;
        },
        x() {
            return this.selectPart.x * this.calScale;
        },
        y() {
            return this.selectPart.y * this.calScale;
        },
        w() {
            return this.selectPart.width * this.calScale;
        },
        h() {
            return this.selectPart.height * this.calScale;
        },
        isMultiply() {
            return this.partSelection.type === 'multiply' ? true: false;
        },
        lockPart() {
            return this.selectTransform.lockPart;
        },
        rotatable() {
            return !this.transform.disableRotate;
        },
        resizable() {
            return !this.transform.disableScale;
        },
        selecttable() {
            return !this.transform.disableSelect;
        },
        draggable() {
            return !this.transform.disableDrag;
        },
        partId() {
            return this.selectTransform.partId;
        },
        selected() {
            return this.transform.selectBox;
        }
    },
    watch: {
        isMultiply(value): void {
            // console.log('isMultiply' + value);
        },
        selected(value): void {
            //  console.log('selected' + value);
        },
    },
    methods: {
        initRef(){
            this.refline = this.$refs.refline;
        },
        zoomMapData( data ) {
            return Math.round(data / this.calScale);
        },
        _resize(x, y, w, h, emitName) {
            const partId = this.partId;
            const resizeObj = {
                partId: partId,
                type: 'resize',
                x: this.zoomMapData(x),
                y: this.zoomMapData(y),
                w: this.zoomMapData(w),
                h: this.zoomMapData(h),
            }
            this.$emit(emitName, resizeObj);
            // hack
            if (!_.isEmpty(this.refline)) {
                this.refline.check();
            }
        },
        _drag(x: number, y: number, emitName: string, hasMove?: boolean) {
            const partId = this.partId;
            const dragObj = {
                partId,
                type: 'drag',
                x: this.zoomMapData(x),
                y: this.zoomMapData(y),
            };
            if (_.isBoolean(hasMove)) {
                dragObj.hasMove = hasMove;
            }
            this.$emit(emitName, dragObj);
            this.refline.check(dragObj);
        },
        _rotate(r, emitName) {
            const partId = this.partId;
            const rotateObj = {
                partId: partId,
                type: 'rotate',
                r: r,
            }
            this.$emit(emitName, rotateObj);
            this.refline.check();
        },
        resizing(x, y, w, h) {
            const emitName = 'partUpdate';
            this._resize(x, y, w, h,emitName);
        },
        dragging(x, y) {
            const emitName = 'partUpdate';
            this._drag(x, y, emitName);
        },
        rotating(r) {
            const emitName = 'partUpdate';
            this._rotate(r, emitName);
        },
        resizestop(x, y, w, h) {
            const emitName = 'transformUpdata';
            this._resize(x, y, w, h, emitName);
            this.refline.uncheck();
        },
        dragstop(x, y, hasMove) {
            const emitName = 'transformUpdata';
            this._drag(x, y, emitName, hasMove);
            this.refline.uncheck();
        },
        rotatestop(r) {
            const emitName = 'transformUpdata';
            this._rotate(r, emitName);
            this.refline.uncheck();
        },
        partMouseUp() {
            this.$emit('partBodyMouseUp');
        }
    }
})
