declare const _: any;
export default class RefLine {

    /**
     * 初始化每个part的refline
     * 目前没用到，因为part初始化的时候就做了这件事
     */
    public initRefLine( partContainer ): void {
        if(partContainer.length !== 0) {
            partContainer.map( (part) => {
                const partStyle = part.partStyle;
                const refLine = this.rectMaxOrMin(partStyle);
                part.refLine.setPartAllProps(refLine);
            });
        }
    }

    /**
     * 更新part的参考线
     */
    public updateRefLine( part ) {
        if(part && !_.isEmpty(part)) {
            const partStyle = part.partStyle;
            const refLine = this.rectMaxOrMin(partStyle);
            part.refLine.setPartAllProps(refLine);
        }
    }

    /**
     * 计算矩形区域的位置和大小，主要用于旋转part的计算
     * 
     * @private
     * @param {any} part 基础的元素
     * @returns  
     */
    private rectMaxOrMin( part ) {
        const { x, y, width, height, angle: partAngle } = part;
        const xCenter = (width) / 2 + x;
        const yCenter = (height) / 2 + y;
        const xArr: any[] = [];
        const yArr: any[] = [];
        const partAllCoord = [
            [x, y],
            [x + width, y],
            [x, y + height],
            [x + width, y + height],
        ];
        for (const item of partAllCoord) {
            const payload = { 
                x0: item[0],
                y0: item[1], 
                xcenter: xCenter, 
                ycenter: yCenter,
                angle: partAngle,
            };
            const coord: any = this.calRoateRect(payload);
            xArr.push( coord.x );
            yArr.push( coord.y );
        }
        const xmax = _.max(xArr) || 0;
        const xmin = _.min(xArr) || 0;
        const ymax = _.max(yArr) || 0;
        const ymin = _.min(yArr) || 0;
        return {
            left: xmin,
            top: ymin,
            width: xmax - xmin,
            height: ymax - ymin,
            right: xmax,
            bottom: ymax,
        };
    }

    private calRoateRect( payload ): any {
        const { x0, y0, xcenter, ycenter, angle  } = payload;
        const piAngle = Math.PI / 180 * angle;
        const X = (x0 - xcenter) * Math.cos(piAngle) - (y0 - ycenter) * Math.sin(piAngle) + xcenter; 
        const Y = (x0 - xcenter) * Math.sin(piAngle) + (y0 - ycenter) * Math.cos(piAngle) + ycenter;
        return {	
            x: X, 
            y: Y,
        };
    }
}

