import AbstractOption from '../../../common/abstractOption'
import updatePropsDeep from 'utils/initProps'

/**
 * 图表提示框tooltip组件配置项
 */
export default class Tooltip extends AbstractOption {

  public config: any;

  constructor(opts?: Chart.Tooltip) {
    super()
    this.config = this.init(opts)
  }

  public init(opts?: Chart.Tooltip) {
    const source: Chart.Tooltip = {
      $formatter: 'data',
      show: true,
      trigger: 'axis',
      confine: false,
      triggerOn: 'mousemove',
      backgroundColor: 'rgba(50,50,50,0.8)',
      borderColor: '#333',
      borderWidth: 0,
      padding: 10,
      textStyle: {
        color: '#fff',
        fontFamily: 'sans-serif',
        fontWeight: 'normal',
        fontSize: 12
      },
      // position: null,
      axisPointer: {
        type: 'line',
        lineStyle: {
          color: '#999',
          width: 1,
          type: 'dotted',
          opacity: 1
        },

        // 后期扩展属性
        // label: {
        //   show: false,
        //   fontSize: 12,
        //   fontWeight: 'normal',
        //   color: '#fff',
        //   margin: 3,
        //   padding: [5, 7, 5, 7],
        //   backgroundColor: 'auto',
        //   borderColor: null,
        //   borderWidth: 0,
        //   formatter: null
        // },
        // shadowStyle: {
        //   color: 'rgba(150,150,150, 0.1)',
        //   opacity: 1
        // },
        // crossStyle: {
        //   color: '#555',
        //   width: 1,
        //   type: 'dashed',
        //   opacity: 1
        // }
      }
    };

    const props: any = updatePropsDeep(source, opts)
    const formatter = this.computed(props.$formatter)
    return { ...props, ...formatter }
  }

  public computed(value: any): any {
    const map: any = {
      data: {
        formatter: null
      },
      axis: {
        formatter: function (params: any): string {
          let str = ''
          const res = params.map((item: any) => {
            return {
              value: item.axisValue,
              label: item.marker
            }
          })
          res.forEach((each: any) => {
            str += `${each.label} ${each.value}</br>`
          })
          return str
        }
      }
    }
    return map[value]
  }

}