import AbstractOption from '../../../common/abstractOption'

export default class Shadow extends AbstractOption {

  public config: any;

  constructor(shapeStyle?: Shape.Shadow) {
    super();
    this.config = this.init(shapeStyle);
  }

  public init(shapeStyle?: Shape.Shadow): object {
    const {
      shadowColor = 'transparent',
      shadowH = 0,
      shadowV = 0,
      shadowBlur = 0,
      shadowSpread = 0
    } = shapeStyle || {};

    return {
      shadowColor,
      shadowH,
      shadowV,
      shadowBlur,
      shadowSpread
    };
  }

  public computed(): void { }

}