declare const _: any;
import EventObject from '../base/eventObject';
import EventUtil from '../base/eventUtil';
const eventContainer = ['mousedown', 'mousemove', 'mouseup', 'mouseContext', 'mouseover', 'mouseout'];
const isEvtObj = (evtObj) => { return evtObj.domId && _.isFunction(evtObj.handle) };
export default class CanvasEvent {

    /**
     * 用于存放事件对象，比如mouseup，mousedown, mousemove
     */
    public eventListeners = new Map();

    constructor() {

    }

    /**
     * 添加dom事件监听
     * @eventObj 事件对象
     * @update 强行更新事件（这里指更新默认属性，比如id，handle），默认是对之前的事件恢复事件监听
     */
    public addClickListener( eventObj: EventObject, update: boolean = false ): void {
        const { eventType } = eventObj;
        let evtObject = this.eventListeners.get(eventType);
        // 检查是否存在标准的dom事件对象
        if( !isEvtObj(eventObj) ) {
            return;
        }
        // 检查是否存在事件对象
        if( evtObject ) {
            if(!evtObject.active && !update) {
                this.addDomListener(evtObject);
                evtObject.active = true;
            }
            if(update) {
                evtObject = this.addEventObject(eventObj);
                this.addDomListener(evtObject);
            }
        } else {
            evtObject = this.addEventObject(eventObj);
            this.addDomListener(evtObject);
        }
    }

    /**
     * 移除dom事件监听
     * @eventObj 事件对象
     */
    public removeClickListener( eventObj: EventObject ): void {
        const { eventType } = eventObj;
        const evtObject = this.eventListeners.get(eventType);
        // 检查是否存在标准的dom事件对象
        if ( !isEvtObj(eventObj) ) {
            return;
        }
        // 检查是否存在事件对象
        if ( evtObject ) {
            if ( evtObject.active ) {
                const { domId, handle } = eventObj;
                EventUtil.removeListener( domId, eventType, handle );
                evtObject.active = false;
            }
        }
    }

     /**
     * 新增事件对象
     */
    private addEventObject( eventObj: EventObject ): EventObject {
        const { eventType } = eventObj;
        const evtObject = new EventObject( eventObj );
        // 拷贝方法
        evtObject.handle = eventObj.handle;
        this.eventListeners.set( eventType, evtObject );
        return evtObject;
    }

    /**
     * 新增事件对象
     */
    private addDomListener( evt: EventObject ): void {
        const { domId, eventType, handle } = evt;
        EventUtil.addListener( domId, eventType, handle );
    }

    /**
     * 增加dom的事件监听
     */
    private addAllDomListener( eventObjs: any[] ): void {
        eventObjs.forEach( event => {
            if( event.domId && _.isFunction(event.handle) ) {
                EventUtil.addListener(event.domId, event.eventType, event.handle );
            }
        });
    }

    /**
     * 移除dom的事件监听
     */
    private removeAllDomListener( eventObjs: any[] ): void {
        eventObjs.forEach( event => {
            if( event.domId && _.isFunction(event.handle) ) {
                EventUtil.removeListener(event.domId, event.eventType, event.handle );
            }
        });
    }

}