const mutations = {
    setType(state: Screen.state, type: string) {
        state.editor.partSelection.setType(type);
    },
    setReleasePart(state: Screen.state, isRelease: boolean) {
        state.editor.partSelection.setReleasePart(isRelease);
    },
    updateSelectPart(state: Screen.state, selectPart: any) {
        state.editor.partSelection.mapSelectPart(selectPart);
    },
    updateSelectGroup(state: Screen.state, part: any) {
        state.editor.partSelection.mapSelectGroup(part);
    },
    clearSelectGroup(state: Screen.state) {
        state.editor.partSelection.clearSelectGroup();
    },
    // transform 操作结束，更新selectorPart的值，对historty有影响
    transform_his(state: Screen.state) {
    },
    // 清楚partSelection状态中间层
    clearPartSeletion(state: Screen.state) {
        state.editor.partSelection.clearPartSeletion();
    },
    // 更新partSelection状态中间层
    updatePartSeletion(state: Screen.state, partSelObj: any) {
        state.editor.partSelection.updatePartSeletion(partSelObj);
    },
};
const getter = {
    partSelection: (state: Screen.state) => state.editor && state.editor.partSelection,
};
export default {
    mutations,
    getter
};