import axios from "axios";
import { MDto } from "../dto/baseDto";

export class BaseStorage {
    /**
     * 保存
     * @param url
     * @param object
     */
    public static save(url: string, object: any): Promise<MDto<any>> {
        return axios.post(url, JSON.stringify(object)).then(s => {
            if (s.status == 200) {
                return new MDto(s.data.code, s.data.data, s.data.msg);
            } else {
                return new MDto(-1, null, "网络异常!");
            }
        });
    }

    public static get(url: string, object: Object): Promise<MDto<any>> {
        if (_.isEmpty(object)) {
            return axios.get(url).then(s => {
                if (s.status == 200) {
                    return new MDto(s.data.code, s.data.data, s.data.msg);
                } else {
                    return new MDto(-1, null, "网络异常!");
                }
            });
        } else {
            return axios.get(url, { params: object }).then(s => {
                if (s.status == 200) {
                    return new MDto(s.data.code, s.data.data, s.data.msg);
                } else {
                    return new MDto(-1, null, "网络异常!");
                }
            });
        }
    }
}
