// 声明文件
declare const window: any;
declare const $: any;
/**
 * 绑定事件
 *
 * @export
 * @param {any} obj
 * @param {any} type
 * @param {any} handler
 */
export function bind(obj: any, type: any, handler: any) {
    const node = typeof obj == "string" ? $(obj) : obj;
    if (node.addEventListener) {
        node.addEventListener(type, handler, false);
    } else if (node.attachEvent) {
        node.attachEvent('on' + type, handler);
    } else {
        node['on' + type] = handler;
    }
}

/**
 * 解绑事件
 *
 * @export
 * @param {any} dom
 * @param {any} eventType
 * @param {any} callback
 */
export function off(dom: any, eventType: any, callback: any) {
    if (document.addEventListener) {
        dom.removeEventListener(eventType, callback)
    } else {
        dom.detachEvent('on' + eventType, callback)
    }
}

export function mouseWheel(obj: any, handler: any) {
    var node = typeof obj == "string" ? $(obj) : obj;
    bind(node, 'mousewheel', function(event: any) {
        var data = -getWheelData(event);
        handler(data);
        if (document.all) {
            window.event.returnValue = false;
        } else {
            event.preventDefault();
        }
    });
    //火狐
    bind(node, 'DOMMouseScroll', function(event: any) {
        var data = getWheelData(event);
        handler(data);
        event.preventDefault();
    });

    function getWheelData(event: any) {
        var e = event || window.event;
        return e.wheelDelta ? e.wheelDelta : e.detail * 40;
    }
}