





















import Vue from 'vue'
const idPrefix = 'image'
const pathImage = `M2.273.1h17.454c1.205 0 2.182.977 2.182 2.182v15.436a2.182 2.182 
    0 0 1-2.182 2.182H2.273A2.182 2.182 0 0 1 .09 17.718V2.282C.09 1.077 1.068.1 2.273.1zm0 
    3.29v13.22c0 .602.488 1.09 1.09 1.09h15.273a1.09 1.09 0 0 0 1.091-1.09V3.39a1.09 
    1.09 0 0 0-1.09-1.09H3.363a1.09 1.09 0 0 0-1.091 1.09zm1.09 12.172V14.4l2.905-2.928a1.09 
    1.09 0 0 1 1.664.137l1.108 1.565a1.09 1.09 0 0 0 1.766.02l3.023-4.064a1.09 1.09 0 0 
    1 1.82.105l2.987 5.165v1.113c0 .601-.486 1.089-1.086 1.091l-13.091.05a1.09 1.09 0 0 
    1-1.095-1.092zM7.183 7.8a1.643 1.643 0 0 1-1.637-1.65c0-.911.733-1.65 1.637-1.65s1.636.739 
    1.636 1.65c0 .911-.732 1.65-1.636 1.65z`

export default Vue.extend({
  name: 'imageShape',
  props: ['initOption'],
  data() {
    return {
      id: this._.uniqueId(idPrefix),
      styleMap: {
        // 全局
        width: 200,
        height: 100,
        left: 0,
        top: 0,
        zIndex: 1,
        opacity: 1,
        // 角度
        transform: 0,

        // 图片路径
        imageSrc: '',

        // 等比缩放
        equalScale: false,

        // 填充色
        backgroundColor: '',

        // 边线
        borderWidth: 1,
        borderColor: '#333',
        borderStyle: 'solid',
        borderRadius: 0,

        // 阴影
        shadowColor: '',
        shadowH: 0,
        shadowV: 0,
        shadowBlur: 0,
        shadowSpread: 0
      }
    }
  },
  computed: {
    shapeStyle(): any {
      return {
        width: `${this.styleMap.width}px`,
        height: `${this.styleMap.height}px`,
        left: `${this.styleMap.left}px`,
        top: `${this.styleMap.top}px`,
        zIndex: `${this.styleMap.zIndex}px`,
        opacity: this.styleMap.opacity,
        transform: `rotate(${this.styleMap.transform}deg)`,
        borderWidth: `${this.styleMap.borderWidth}px`,
        borderColor: this.styleMap.borderColor,
        borderStyle: this.styleMap.borderStyle,
        borderRadius: `${this.styleMap.borderRadius}px`,
        backgroundColor: this.styleMap.backgroundColor,
        boxShadow: `
            ${this.styleMap.shadowH}px 
            ${this.styleMap.shadowV}px
            ${this.styleMap.shadowBlur}px 
            ${this.styleMap.shadowSpread}px
            ${this.styleMap.shadowColor}`
      }
    },
    imageSvagPath(): string {
      return pathImage
    },
    spanStyle(): any {
      return {
        display: 'flex',
        backgroundColor: this.styleMap.backgroundColor
      }
    },
    equalScaleStyle() {
      let model = ''
      if (this.styleMap.equalScale) {
        model = 'auto'
      } else {
        model = '100%'
      }
      return {
        height: model
      }
    }
  },
  methods: {
    setOption(option: any): void {
      const styles: object = option.content || {}
      if (this._.isPlainObject(styles)) {
        for (let [key, value] of Object.entries(styles)) {
          if (!this._.isNil(this.styleMap[key])) {
            this.styleMap[key] = value
          }
        }
      }
    },
    handleIntOption() {
      this.setOption(this.initOption)
    }
  },
  mounted() {
    this.handleIntOption()
  }
})
