export default class HsistoryIDB {

    public static IDBDatabase: any;

    /**
     * 初始化indexDB
     */
    public static initIndexedDB(): any {
        return new Promise((resolve, reject) => {
            const request = window.indexedDB.open('history', 2);
            request.onsuccess = (event: any) => {
                this.IDBDatabase = request.result;
                resolve();
            };
            request.onerror = (event: any) => {
                reject();
                throw new Error(JSON.stringify(request.error));
            };
            request.onupgradeneeded = (event: any) => {
                const db: any = event.target.result;
                if (!db.objectStoreNames.contains('hisManage')) {
                    db.createObjectStore('hisManage', { keyPath: 'index' });
                }
            };
        });
    }

    /**
     * 添加快照
     */
    public static addUndo(snapshot: any): any {
        const type = 'hisManage';
        return this.addObjectStore(snapshot, type);
    }

    /**
     * 清楚所有的重做
     */
    public static clearRedo(popArray): any {
        const promises = popArray.map((item) => {
            return this.popObjectStore(item);
        });
        return new Promise((resolve, reject) => {
            Promise.all(promises).then((posts) => {
                resolve();
            }).catch((reason) => {
                reject();
            });
        });
    }

    /**
     * 清楚数据库的所有数据
     */
    public static clearIndexDb(): any {
        return this.clearObjectStore();
    }

    public static getIndex(index): any {
        const type = 'hisManage';
        return new Promise((resolve, reject) => {
            const transaction = this.getObjectStore(type).get(index);

            transaction.onsuccess = (event: any) => {
                resolve(event.target.result);
            };

            transaction.onerror = (event: any) => {
                reject();
            };
        });
    }

    /**
     * @删除指定行的历史记录
     */
    public static popObjectStore(index): Promise<any> {
        const type = 'hisManage';
        return new Promise((resolve, reject) => {
            const transaction = this.getObjectStore(type).delete(index);
            transaction.onsuccess = (event: any) => {
                resolve();
            };
            transaction.onerror = (event: any) => {
                reject();
            };
        });
    }

    /**
     * @清楚所有行的历史记录
     */
    public static clearObjectStore(): Promise<any> {
        const type = 'hisManage';
        return new Promise((resolve, reject) => {
            const transaction = this.getObjectStore(type).clear();
            transaction.onsuccess = () => {
                resolve();
            };
            transaction.onerror = () => {
                reject();
            };
        });
    }

    public static closeIDBDatabase(): Promise<any> {
        return new Promise( (resolve, reject) => {
            const request = indexedDB.open('history', 2);
            console.log('close indexdb');
            request.onsuccess = (e: any) => {
                const db: any = e.target.result;
                console.log(db);
                const IDBDatabase = db.close();

                IDBDatabase.onerror = (event) => {
                    console.log('数据库关闭异常');
                    console.log('Error');
                };

                IDBDatabase.onsuccess = (event) => {
                    console.log('数据库已关闭');
                    console.log('success');
                };
                
            }
        });
    }

    public static closeIDB(): Promise<any> {
        console.log("delete Index Db");
        return new Promise((resolve, reject) => {
            const DBDeleteRequest = window.indexedDB.deleteDatabase('history');
            DBDeleteRequest.onerror = (event) => {
                console.log('Error');
                reject();
            };

            DBDeleteRequest.onsuccess = (event) => {
                console.log('success');
                resolve();
            };
        });
    }

    public static destroyed() {
        this.IDBDatabase = null;
    }

    private static addObjectStore(snapshot, type): Promise<(resolve: any, reject: any) => void> {
        return new Promise((resolve, reject) => {
            const transaction = this.getObjectStore(type).add(snapshot);
            transaction.onsuccess = (event: any) => {
                resolve();
            };

            transaction.onerror = (event: any) => {
                reject();
            };
        });
    }

    private static getObjectStore(type): any {
        return this.IDBDatabase.transaction([type], 'readwrite').objectStore(type);
    }

    private static getCount(type): Promise<any> {
        return new Promise((resolve, reject) => {
            const transaction = this.getObjectStore(type).count();
            transaction.onsuccess = (event: any) => {
                resolve(event.target.result);
            };

            transaction.onerror = (event: any) => {
                reject();
            };
        });
    }

}
