
































































import Vue from 'vue'
import SmartComponent from './treeMiddleware'

export default Vue.extend({
  name: 'tree-item',
  props: {
    model: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  data() {
    return {
      isOpen: false
    }
  },
  components: {
    'smart-component': SmartComponent
  },
  computed: {
    isFold(): number {
      return this.model.children && this.model.children.length
    }
  },
  methods: {
    handleChange(opts: any, val: any): void {
      // 组件递归，需使用bus对象传递事件
      this.$root.busRef.$emit('on-gui-change', opts, val)
    },
    handleSeriesAdd(model: any) {
      this.$root.busRef.$emit('on-series-add', model)
    },
    handleSeriesDel(model: any) {
      this.$root.busRef.$emit('on-series-del', model)
    },
    handleToogle(): void {
      this.isOpen = !this.isOpen
    }
  }
})
