/**
 * cartesian坐标配置项
 */
export default abstract class CartesianAxis extends AbstractOption {

  /**
   * cartesian坐标配置项默认值
   */
  public default: any;

  public config: any;

  constructor() {
    super();
    this.default = source;
  }

  abstract init(opts?: any): void;

  abstract computed(value: any, key: string): void;

}