export default class Queue {

    private list: any = [];

    private size;

    constructor( size? ) {
        // 默认是15条数据
        this.size = size || 15;
    }

    public push( data ): void {
        if (!data) {
            return;
        }
        if (this.list.length === this.size) {
            this.list.shift();
        }
        this.list.push( data );
    }

    public pop() {
        return this.list.pop();
    }

    public clear() {
        this.list = [];
    }

    public getMap() {
        return this.list;
    }

    public isEmpty(): boolean {
        return this.list.length === 0 ? true : false;
    }

    public length() {
        return this.list.length;
    }

}
