
import CartesianAxis from './cartesianAxis'
import updatePropsDeep from 'utils/initProps'

/**
 * x轴配置项
 */
export default class XAxis extends CartesianAxis {

  public config: any;

  constructor(opts?: Chart.CartesianAxis) {
    super()
    this.config = this.init(opts)
  }

  public init(opts?: Chart.CartesianAxis) {
    const props: any = updatePropsDeep(this.default, opts)
    const {
      $textSize,
      $textColor,
      $textWeight,
      $rotate,
      $min,
      $max
    } = props
    props.nameTextStyle.fontSize = $textSize
    props.axisLabel.fontSize = $textSize
    props.nameTextStyle.color = $textColor
    props.axisLabel.color = $textColor
    props.nameTextStyle.fontWeight = $textWeight
    props.axisLabel.fontWeight = $textWeight
    props.axisLabel.rotate = $rotate
    props.nameRotate = $rotate
    props.min = this.countMaxOrMin($min)
    props.max = this.countMaxOrMin($max)

    return props
  }

  public computed(value: any, key: string): any {
    const map: any = {
      $textSize: {
        nameTextStyle: {
          fontSize: value
        },
        axisLabel: {
          fontSize: value
        }
      },
      $textColor: {
        nameTextStyle: {
          color: value
        },
        axisLabel: {
          color: value
        }
      },
      $textWeight: {
        nameTextStyle: {
          fontWeight: value
        },
        axisLabel: {
          fontWeight: value
        }
      },
      $rotate: {
        nameRotate: value,
        axisLabel: {
          rotate: value
        }
      },
      $max: {
        max: this.countMaxOrMin(value)
      },
      $min: {
        min: this.countMaxOrMin(value)
      }
    }
    return map[key]
  }

  /**
   * 根据属性界面值计算x轴实际最大、最小值
   * @param value 当前修改的属性界面值
   */
  public countMaxOrMin(value: any): number {
    let res: any
    const val = _.trim(value)

    // x、y轴对换, x轴最大值为select选择框选择
    if (val === 'max') {
      res = val === 'max' ? 'dataMax' : null
    }
    // x、y轴对换, x轴最小值为select选择框选择
    if (val === 'min') {
      res = val === 'min' ? 'dataMin' : null
    }
    // x、y轴未对换，x轴最大值、最小值为input输入框输入
    if (!['min', 'max'].includes(val)) {
      res = !val.length || Number.isNaN(Number(val)) ? null : parseInt(val, 10)
    }
    return res
  }

}