import PagesManage from '../pages/pagesManage';
import Canvas from '../editor/canvas';
import MenuMapper from '../menu/menu';
import CmptManage from '../cmpt/cmptManage';
import _ from 'lodash';

// 编辑层的东西，不应该放在这里
import Event from '../editor/event/event';
import PureModelMapper from '../pureModel/pureModelMapper';
import ProjectObject from './projectObject';
export default class Project {

    /**
     * 模型值edit || preview || publish
     */
    public mode: string;

    public projectObj: ProjectObject;

    /**
     * 页面容器
     */
    public pageManage: any;

    /**
     * 菜单容器
     */
    public menuManage: any;

    /**
     * 状态容器
     */
    public stateManage: any;

    /**
     * 组件容器
     */
    public partManage: any;

    /**
     * 项目管理容器
     */
    public projectManage: any;

    /**
     * canvas操作引起的临时储存
     */
    public canvas: any;

    /**
     * canvas操作引起的临时储存 -- 新版
     */
    public editor: Canvas;

    /**
     * 存放组件的管理器，持久化数据
     */
    public cmptManage: CmptManage;

    /**
     * 事件管理
     */
    public event: Event;

    /**
     * 存数据管理器
     */
    public pureModelMapper: PureModelMapper;

    constructor() {}

    /**
     * 初始化project的项目
     */
    public init(): any {
        // 从配置表映射基本的属性值
        this.pageManage = new PagesManage();
        this.menuManage = new MenuMapper();
        this.editor = new Canvas();
        this.event = new Event();
        this.projectObj = new ProjectObject();
        this.cmptManage = new CmptManage();
        this.initPureModelMapper();
        // this.projectManage = new ProjectManage();
    }

    /**
     * 初始化预览project的项目
     */
    public initPreview(): void {
        this.pageManage = new PagesManage();
        this.projectObj = new ProjectObject();
        // 这里对editor和cmptManage的引用为空。
        this.initPureModelMapper();
    }

    /**
     * 获取存数据各个模块的引用
     */
    public initPureModelMapper(): void {
        const model = {
            editor: this.editor,
            pageManage: this.pageManage,
            projectObj: this.projectObj,
            cmptManage: this.cmptManage,
        };
        this.pureModelMapper = new PureModelMapper(model);
    }

    public build(): void {
        
    }

    public destroyed(): void {
        this.pageManage.destroyed();
        this.menuManage = null;
        this.editor.destroyed();
        this.event.destroyed();
        this.projectObj.destroyed();
        this.cmptManage.destroyed();
        this.pureModelMapper.destrory();
    }

    public saveScreen(): void {

    }

    public readScreen(): void {

    }

    private createProId(): string {
        return 'pro_' + _.uniqueId();
    }
}