











import Vue from 'vue'
export default Vue.extend({
  props: {
    side: {
      type: Object,
      default: function() {
        return {
          hint: '',
          disable: false,
          value: 0,
          max: 100,
          min: 0
        }
      }
    },
    model: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  computed: {
    inputValue: {
      get(): any {
        const inValue = this.model[this.side.key]
        return inValue ? inValue : this.side.value
      },
      set(newVal: any): any {
        this.$emit('inputNumChange', this.side, newVal)
      }
    },
    inputHint(): string {
      return this.side.hint || ''
    },
    inputMin(): number {
      return this.side.min || 0
    },
    inputMax(): number {
      return this.side.max || 100
    },
    inputStep(): number {
      return this.side.step || 1
    },
    isDisable(): boolean {
      return this.side.disable
    }
  },
  methods: {
    handleChange(value: number) {
      this.$emit('inputNumChange', this.side, value)
    }
  }
})
