
declare const _:any;
import Vue from "vue";
import { mapMutations, mapState } from 'vuex';
export default Vue.extend({
    data(){
        return {
        }
    },
    computed: {
        ...mapState({
            selector: (state: any) => state.screen.editor,
        }),
        // 取得hoverPart模块
        hoverPart() {
            return this.selector.hoverPart;
        },
        // 取得partSelection模块
        partSelection(): any {
            return this.selector.partSelection;
        },
        // 取得transform模块
        transform() {
            return this.selector.transform;
        },
        // 取得selectPart
        selectPart() {
            return this.partSelection.selectPart;
        },
        partId() {
            return this.selector.partId;
        },
    },
    methods: {
        ...mapMutations([
            'setIsHoverPart',
            'mapHoverPartStyle'
        ]),
        handleHoverPart( part ) {
			const selectPartType = this.partSelection.type;
			const selectPartId = this.partId;
            const selectGroup = this.partSelection.selectGroup;
            const mapHoverPart = {
                partStyle: _.cloneDeep(part.partStyle),
                isHoverPart: true,
            };
			if ( selectPartType === 'single' && selectPartId ) {
				if ( part.partId !==  selectPartId ) {
                    this.mapPartSelector(mapHoverPart);
				}
			} else if ( selectPartType === 'multiply' ) {
				if ( part.partId !==  selectPartId ) {
					const indexNumber = selectGroup.findIndex( item =>{
						return part.partId === item.partId;
					});
					if ( indexNumber === -1 ) {
						this.mapPartSelector(mapHoverPart);
					}
				}
			} else {
				this.mapPartSelector(mapHoverPart);
			}
		},
		resetHoverPart() {
			const hoverPart = {
                isHoverPart: false,
                partStyle: {
                    height: 0,
                    width: 0,
                    x: 0,
                    y: 0,
                    angle: 0,
                }
            };
            this.mapPartSelector(hoverPart);
        },
        mapPartSelector( mapHoverPart ){
            const { isHoverPart, partStyle } = mapHoverPart;
            this.setIsHoverPart(isHoverPart);
            this.mapHoverPartStyle(partStyle);
        }
    },
    render(h) {
        return h();
    }
});
