import AbstractOption from '../../../common/abstractOption'
import updatePropsDeep from 'utils/initProps'

/**
 * series-line系列列表配置项
 * 初始化时需显示的指定每一项的label、lineStyle、itemStyle颜色
 */
export default class SeriesLine extends AbstractOption {

  public config: any;

  constructor(series: Array<Chart.SeriesItem>) {
    super()
    this.config = this.init(series)
  }

  public init(series: Array<Chart.SeriesItem>) {
    if (!_.isArray(series) || _.isEmpty(series)) {
      return []
    }

    const res = [];
    const source: Chart.SeriesItem = {
      name: '',
      type: 'line',
      yAxisIndex: 0,
      stack: null,
      step: false,

      label: {
        show: false,
        fontSize: 12,
        color: '#008cff',
        fontWeight: 'normal'
      },

      smooth: false,
      lineStyle: {
        color: '#008cff',
        type: 'solid',
        width: 2
      },

      symbol: 'circle',
      symbolSize: 8,
      itemStyle: {
        color: '#008cff'
      },

      areaStyle: {
        color: 'transparent'
      },
      markPoint: {
        data: []
      },
      markLine: {
        data: []
      },

      data: []
    };

    for (let item of series) {
      res.push(updatePropsDeep(source, item));
    }
    return res;
  }

  public computed(): void { }

}