













import Vue from 'vue';
export default Vue.extend({
    name: 'PoperSlot',
    props: {
        position: {
            type: Object,
            default: () => {
                return {
                    x: '300px',
                    y: '70px',
                };
            },
        },
    },
    computed: {
        wrapperStyle() {
            return {
                top: this.position.y,
                right: this.position.x,
            };
        },
    },
});
