import { Commit } from 'vuex';
import Core from '@/views/screen/core/core';
import pages from './pageModel/pages';
import state from './stateModel/state';
import part from './partModel';
import project from './projectModel/project';
import editor from './editor/index';
import cmpt from './cmptModel/cmpt';
import editDataMap from './editConfig';

// initial state
const initState: Screen.state = {
    pageManage: null,
    cmptManage: null,
    editor: null,
    event: null,
    projectObj: null,
    pureModelMapper: null,
};

// actions
const actions = {
    ...editor.action,
    initCoreModel(context: { commit: Commit; state: any }, model: string): void {
        let editData: any = {};
        if (editDataMap) {
            if (model === 'preview') {
                editData = _.cloneDeep(editDataMap);
            }
        }
        // 挂载project的引用
        const proRef = new Core();
        context.state.projectRef = proRef;
        
        const modelTemp = model ? model : 'edit';
        const project: any = proRef.init(modelTemp, editData);
        Object.assign(context.state, { ...project });
    },
    // destroyedCoreModel(context: { commit: Commit; state: any }): void {
    //     context.commit('destroyedCoreModelMutation');
    // },
};

// mutations
const mutations = {
    ...state,
    ...part,
    ...pages.mutations,
    ...project.mutations,
    ...editor.mutations,
    ...cmpt.mutations,
    destroyedCoreModel(state: Screen.state) {
        state.projectRef.destroyed();
        state.pageManage = null;
        state.cmptManage = null;
        state.editor = null;
        state.event = null;
        state.projectObj = null;
        state.pureModelMapper = null;
        state.projectRef = null;
        // console.log('触发销毁模型');
    },
};
const getters = {
    ...pages.getter,
    ...editor.getter,
    ...cmpt.getter,
    ...project.getter,
    partContainer: (state: Screen.state) => state.pageManage && state.pageManage.partContainer,
};

export default {
    state: initState,
    getters,
    actions,
    mutations,
};
