import { Commit } from 'vuex';
import Queue from './queue';
import _ from 'lodash';

// initial state
const state: History.manager = {
    // 
	redoManager: undefined,
	undoManager: undefined,
	present: undefined,
	firstState: undefined,

};

// getters
const getters = {
};

// actions
const actions = {
};

// mutations
const mutations = {
	initManager( state: History.manager ) {
		state.redoManager = new Queue();
		state.undoManager = new Queue();
	},
	addFirstSate( state: History.manager, stateCanvas ) {
		state.firstState = _.cloneDeep(stateCanvas);
	},
	updateFirstState( state: History.manager, stateCanvas ){
		// state.firstState = _.cloneDeep(stateCanvas);
	},
	clearRedo( state: History.manager ) {
        state.redoManager.clear();
    },
    clearUndo( state: History.manager ) {
        state.undoManager.clear();
	},
	addUndo( state: History.manager , payload) {
		state.undoManager.push(payload);
	},
	redo( state: History.manager ) {
		const mutationEvent = state.redoManager.pop();
		state.present = mutationEvent;
        state.undoManager.push( mutationEvent );
    },
    undo( state: History.manager ) {
		const mutationEvent = state.undoManager.pop();
		state.present = mutationEvent;
        state.redoManager.push( mutationEvent );
    },
};

export default {
  state,
  getters,
  actions,
  mutations,
};
