







import Vue from 'vue'
export default Vue.extend({
  name: 'colorPicker-el',
  props: {
    opts: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  data() {
    return {
      predefineColors: [
        '#ff4500',
        '#ff8c00',
        '#ffd700',
        '#90ee90',
        '#00ced1',
        '#1e90ff',
        '#c71585',
        'rgba(255, 69, 0, 0.68)',
        'rgb(255, 120, 0)',
        'hsv(51, 100, 98)',
        'hsva(120, 40, 94, 0.5)',
        'hsl(181, 100%, 37%)',
        'hsla(209, 100%, 56%, 0.73)',
        '#c7158577'
      ]
    }
  },
  computed: {
    color: {
      get(): string {
        return this.opts.model && this.opts.model !== 'transparent'
          ? this.opts.model
          : ''
      },
      set(val: string): string {
        return val
      }
    }
  },
  methods: {
    handleChange(val: string) {
      val = val || '#333'
      this.$emit('on-data-change', this.opts, val)
    }
  }
})
